import React, { Component } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import { ScaleLoader } from "react-spinners";

class ShipmentView extends Component {
  constructor(props) {
    super(props);

    this.state = {
      title: "",
      vendor: 1,
      totalAmount: 0,
      cargoCharges: "",
      date: "",
      shipments: [],
      loading: false,
      notification: false,
      notificationText: "",
      danger: false,
    };


  }
  componentDidMount() {
    const id = this.props.match.params.shipmentId;
    (async () => {
      this.setState({ loading: true })
      try {
        const response = await axios.get(`/shipment/get/${id}`)
        console.log(response)
        this.setState({
          title: response.data.title,
          vendor: response.data.vendorId,
          date: response.data.date,
          cargoCharges: response.data.cargoCharges,
          shipments: response.data.shipmentItems,
          totalAmount: response.data.totalAmount
        });
        this.setState({ loading: false })

      }
      catch (error) {
        console.error("Error fetching product details:");
      };
    })();
  }

  render() {
    return (
      <div className="admin-content mx-auto">
        {this.state.loading && (
          <div className="text-center">
            <ScaleLoader
              size={150}
              color={"#123abc"}
              loading={this.state.loading}
            // rounded={true} // Set the spinner to be rounded
            />
          </div>
        )}
        <div className="w-100 mb-5">
          <h4>Shipment Details</h4>
          <button
            className="btn  btn-secondary float-right"
            style={{ marginBottom: 20 }}
            onClick={() => {
              window.print();
            }}
          >
            Print
          </button>
        </div>
        <div className="w-75">
          <form onSubmit={this.handleSubmit}>
            <div className="container-fluid">
              <div className="row">
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Title</label>
                    <input
                      type="text"
                      className="form-control"
                      name="title"
                      value={this.state.title}
                      readOnly
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Vendor</label>
                    <input
                      type="text"
                      className="form-control"
                      name="vendor"
                      value={this.state.vendor}
                      readOnly
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Amount</label>
                    <input
                      type="text"
                      className="form-control"
                      name="amount"
                      row="10"
                      value={this.state.totalAmount}
                      readOnly
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Date</label>
                    <input
                      type="text"
                      className="form-control"
                      name="date"
                      value={this.state.date}
                      readOnly
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Cargo Charges</label>
                    <input
                      className="form-control"
                      name="cargoCharges"
                      value={this.state.cargoCharges}
                      onChange={this.handleInputChange}
                      readOnly
                    />
                  </div>
                </div>
              </div>
              <div className="items-list">
                <h4 style={{ textAlign: "center" }}>
                  Items of Shipment {this.state.title}
                </h4>
                {this.state.shipments.length > 0 ? (
                  <div>
                    <table
                      style={{
                        width: "100%",
                        borderCollapse: "collapse",
                        border: "1px solid #ddd",
                        marginLeft: 0,
                        backgroundColor: "#f5f5f5",
                      }}
                    >
                      <thead style={{ backgroundColor: "#c2c5c0" }}>
                        <tr>
                          <th style={tableHeaderStyle}>Item Id</th>
                          <th style={tableHeaderStyle}>Quantity</th>
                          <th style={tableHeaderStyle}>Cost/Unit</th>

                        </tr>
                      </thead>
                      <tbody>
                        {this.state.shipments.map((item) => (
                          <tr
                            key={item.ItemId}
                            style={tableRowStyle}
                            onClick={() => {
                              // Navigate to "View Item" page
                              this.props.history.push(`/app/store/store/product-category/view/view/${item.itemId}`);
                            }}
                          >
                            <td style={tableCellStyle}>{item.itemId}</td>
                            <td style={tableCellStyle}>{item.quantity}</td>
                            <td style={tableCellStyle}>{item.cost}</td>

                          </tr>
                        ))}
                      </tbody>
                    </table></div>) : (<div>
                      <table
                        style={{
                          width: "100%",
                          borderCollapse: "collapse",
                          border: "1px solid #ddd",
                          marginLeft: 0,
                          backgroundColor: "#f5f5f5",
                        }}
                      >
                        <thead style={{ backgroundColor: "#c2c5c0" }}>
                          <tr>
                            <th style={tableHeaderStyle}>Item Id</th>
                            <th style={tableHeaderStyle}>Quantity</th>
                            <th style={tableHeaderStyle}>Cost/Unit</th>

                          </tr>
                        </thead>
                        <tbody>
                          {this.state.shipments.map((item) => (
                            <tr
                              key={item.ItemId}
                              style={tableRowStyle}
                              onClick={() => {
                                // Navigate to "View Item" page
                                this.props.history.push(`/app/store/store/product-category/view/view/${item.itemId}`);
                              }}
                            >
                              <td style={tableCellStyle}>{item.itemId}</td>
                              <td style={tableCellStyle}>{item.quantity}</td>
                              <td style={tableCellStyle}>{item.cost}</td>

                            </tr>
                          ))}
                        </tbody>
                      </table>
                      <p style={{ marginLeft: 500, marginTop: 30 }}>
                        <h4>No Item Found </h4>
                      </p>

                    </div>
                )}


              </div>

              <div className="col-12 mt-3">
                <div className="form-group">
                  <Link to="/app/store/store/shipment/list" className="btn btn-warning back-link">
                    Back
                  </Link>
                </div>
              </div>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

export default ShipmentView;
// Inline CSS styles
const tableHeaderStyle = {
  backgroundColor: "#c2c5c0",
  padding: "10px",
  border: "1px solid #ddd",
  fontWeight: "bold",
};

const tableRowStyle = {
  cursor: "pointer",
  transition: "background-color 0.3s",
};

const tableCellStyle = {
  padding: "10px",
  border: "1px solid #ddd",
};


