import React, { Component } from "react";
import AnchorTag from "../../components/Anchortag";
import InputFormGroup from "../input/InputFormGroup";
import SelectFormGroup from "../input/SelectFormGroup";
import axios from "axios";
import { ScaleLoader } from "react-spinners";


class EmployeeForm extends Component {
  constructor(props) {
    super(props);
    this.Gender = [
      { id: 1, name: "Male" },
      { id: 2, name: "Female" },
    ];
    this.Desgnations = [
      {
        id: 1,
        name: "Jn. Front End dev",
      },
      {
        id: 2,
        name: "Android Developer",
      },
      {
        id: 3,
        name: "QA",
      },
      {
        id: 4,
        name: "Wordpress Developer",
      },
      {
        id: 5,
        name: "Intrn",
      },
      {
        id: 6,
        name: "Content Writer",
      },
    ];
    this.state = {
      firstName: "",
      lastName: "",
      email: "",
      joiningDate: "",
      gender: "",
      designation: "",
      customDesignation: "",
      notification: false,
      danger: false,
      notificationText: "",
      loading: false,
    };
  }
  resetFormFields = () => {
    this.setState({
      firstName: "",
      lastName: "",
      email: "",
      joiningDate: "",
      gender: "",
      designation: "",
      customDesignation: "",
      // Add other fields and their initial values if needed
    });
  };
  handleInputChange = (event) => {
    const { name, value } = event.target;
    this.setState({
      [name]: value,
    });
  };

  handleSubmit = (event) => {
    event.preventDefault();
    const { firstName, lastName, email, joiningDate, gender, designation } =
      this.state;
    const formData = {
      firstName,
      lastName,
      email,
      joiningDate,
      gender,
      designation: designation === "other" ? this.state.customDesignation : designation,
    };
    this.setState({ loading: true });
    console.log(JSON.stringify(formData));
    // Make a POST request to your backend API with Axios
    axios.post("/employee/create", formData)
      .then((response) => {
        if (response.data.code === 201) {
          // Handle a successful response from the server here
          console.log("Form data sent successfully:", response.data);
          this.setState({
            notification: true,
            notificationText: response.data.message

          })
          this.resetFormFields()
        } else if (response.data.code === 404 || response.data.code === 500 ) {
          console.log("", response.data);
          this.setState({
            danger: true,
            notificationText: response.data.message,
          })
          //  console.log(response.data.message)

        }
        setTimeout(() => {
          this.setState({ notification: false , danger: false});
        }, 3000);
      })
      .catch((error) => {
        // Handle any errors that occur during the request
        this.setState({ notificationText: "An error occured while creating Employee", danger: true, })
        setTimeout(() => {
          this.setState({ danger: false });
        }, 3000);
        console.error("Error sending form data:", error);
      }).finally(() => {
        // Set loading to false after the request is complete
        this.setState({ loading: false });
      });
  };

  render() {
    const roleOptions = [
      { value: "Front End Developer", label: "Front End Developer" },
      { value: "Backend Developer", label: "Backend Developer" },
      { value: "Content writer", label: "Content writer" },
      { value: "Android Developer", label: "Android Developer" },
      { value: "QA", label: "QA" },
      { value: "Intrn", label: "Intrn" },
      { value: "Designer", label: "Designer" },
      { value: " Social Media Management", label: " Social Media Management" },
      { value: "Business development Executive", label: "Business development Executive" },
      { value: "Junior Android Developer", label: "Junior Android Developer" },
      { value: "Lead Expert", label: "Lead Expert" },
      { value: "SQA", label: "SQA" },
      { value: "Engineering Manager", label: "Engineering Manager" },
      { value: "Team Lead", label: "Team Lead" },
      { value: "Program Manager", label: "Program Manager" },
      { value: "Sr. Prod. Manager", label: "Sr. Prod. Manager" },
      { value: "WordPress", label: "WordPress" },
      { value: "SEO", label: "SEO" },
      { value: "Digital Maketing", label: "Digital Maketing" },
      { value: "Admin", label: "Admin" },
      { value: "Accounts", label: "Accounts" },
      { value: "Prod. Manager", label: "Prod. Manager" },
      { value: "Office Coordiantor", label: "Office Coordiantor" },
      { value: "Office boy", label: "Office boy" },
      { value: "other", label: "Other" },

      // Add more options as needed
    ];
    return (
      <div className="admin-content mx-auto">
        {this.state.loading && (
          <div className="text-center">
            <ScaleLoader
              size={150}
              color={"#123abc"}
              loading={this.state.loading}
              rounded={true} // Set the spinner to be rounded
            />
          </div>
        )}
        {this.state.notification && (
          <div className="alert alert-success" role="alert">
            {this.state.notificationText}
          </div>
        )}
        {this.state.danger && (
          <div className="alert alert-danger" role="alert">
            {this.state.notificationText}
          </div>
        )}

        <div className="w-100 mb-5">
          <button
            className="btn btn-primary float-right"
            onClick={() => {
              window.history.back();
            }}
          >
            Back to Employees List
          </button>
          <h4>Create Employee</h4>
        </div>
        <div className="w-75">
          <form onSubmit={this.handleSubmit}>
            <div className="container-fluid">
              <div className="row">
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">First Name</label>
                    <input
                      type="text"
                      className="form-control"
                      name="firstName"
                      required
                      value={this.state.firstName}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Last Name</label>
                    <input
                      type="text"
                      className="form-control"
                      name="lastName"
                      required
                      value={this.state.lastName}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Email</label>
                    <input
                      type="email"
                      className="form-control"
                      name="email"
                      row="10"
                      value={this.state.email}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Joining Date</label>
                    <input
                      type="date"
                      className="form-control"
                      name="joiningDate"
                      required
                      value={this.state.joiningDate}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Gender</label>
                    <select
                      className="form-control"
                      name="gender"
                      required
                      value={this.state.gender}
                      onChange={this.handleInputChange}
                    >
                      <option value="">Select Gender</option>
                      <option value="male">Male</option>
                      <option value="female">Female</option>
                      <option value="other">Other</option>
                    </select>
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Designation</label>
                    <select
                      className="form-control"
                      name="designation"
                      required
                      value={this.state.designation}
                      onChange={this.handleInputChange}
                    >
                      <option value="">Select Designation</option>
                      {roleOptions.map((option) => (
                        <option key={option.value} value={option.value}>
                          {option.label}
                        </option>
                      ))}
                    </select>
                  </div>
                  {this.state.designation === "other" && (
                    <div className="form-group">
                      <label className="mb-2">Input Designation</label>
                      <input
                      placeholder="Please write other designation"
                        type="text"
                        className="form-control"
                        name="customDesignation"
                        value={this.state.customDesignation}
                        onChange={this.handleInputChange}
                      />
                    </div>
                  )}
                </div>

                <div className="col-12 mt-3">
                  <div className="form-group">
                    <button
                      className="btn btn-warning"
                      onClick={() => {
                        window.history.back();
                      }}
                    >
                      Back
                    </button>
                    <button type="submit" className="btn btn-success ml-3" value="Submit">Submit</button>
                  </div>
                </div>
              </div>
            </div>
          </form>
        </div>
      </div >
    );
  }
}

export default EmployeeForm;



