import React, { Component } from "react";
import { Link } from "react-router-dom";
import { ScaleLoader } from "react-spinners";

import axios from "axios";

class PasswordResetVerifyForm extends Component {

  constructor(props) {
    super(props);
    this.state = {
      email: "",
      notification: false,
      notificationText: "",
      loading: false,
      danger: false,
    };

  }

  handleInputChange = (event) => {
    const { name, value } = event.target;
    this.setState({
      [name]: value,
    });
  };

  handleSubmit = (event) => {
    event.preventDefault();
    const {
      email

    } = this.state;
    const formData = {
      email
    };
    console.log(JSON.stringify(formData));
    this.setState({ loading: true })
    // Make a POST request to your backend API with Axios
    axios.post(`/user/forgetpassword/`, formData)
      .then((response) => {
        // Handle a successful response from the server here
        console.log("Form data sent successfully:", response.data);
        if (response.data.code === 200) {
          this.setState({
            email: "",
            notification: true,
            danger: false,
            notificationText: response.data.message
          })
        } else if (response.data.code === 400 || response.data.code === 500) {
          this.setState({
            danger: true,
            notification: false,
            notificationText: response.data.message,
          });
        }
        setTimeout(() => {
          this.setState({ danger: false, notification: false });
        }, 10000);
        // Handle any other status codes if needed
      })
      .catch((error) => {
        // Handle any errors that occur during the request
        this.setState({
          notificationText: "An error occurred while sending email",
          danger: true,
        });
        console.error("Error sending form data:", error);
      }).finally(() => {
        // Set loading state to false after the request is complete
        this.setState({ loading: false });
      });
  }
  render() {
    return (
      <div>
          <header >
                <nav className="navbar navbar-expand-lg navbar-dark bg-dark" >
                    <a className="navbar-brand" href="#">Inventory Managment System</a>
                    <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarNav">
                        <ul className="navbar-nav ml-auto">
                            <li className="nav-item ">
                                <a className="nav-link" href="/">Home</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" href="/pricing">Pricing</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link active" href="/login">Login</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" href="/contact">Contact</a>
                            </li>
                        </ul>
                    </div>
                </nav>
            </header>

      <div className="auth mx-auto">
        {this.state.loading && (
          <div className="loader">
            <ScaleLoader
              size={1500}
              color={"#123abc"}
              loading={this.state.loading}
              rounded={true} // Set the spinner to be rounded
            />
          </div>
        )}
        {this.state.danger && (
          <div className="mt-5 alert alert-danger" role="alert">
            {this.state.notificationText}
          </div>
        )}
        {this.state.notification && (
          <div className="mt-5 alert alert-success" role="alert">
            {this.state.notificationText}
          </div>
        )}
        <form className="">
          <div className="form-group">
            <h3 className="text-center pb-3">Account Verify</h3>
          </div>
          <div className="form-group">
            <label>Email<span style={{color: "red"}}>*</span></label>
            <input type= "email" className="form-control" id="username" name="email" required value={this.state.email}
              onChange={this.handleInputChange}
            />
          </div>
          <div className="form-group">
            <button className="btn btn-warning w-100 mt-2" onClick={this.handleSubmit}>Send Email</button>
          </div>
          <div>
            <p>Back to Login. <Link to="/login">Login</Link></p>
          </div>
        </form>
      </div>
      </div>
    )
  }
}

export default PasswordResetVerifyForm;


