import React, { Component } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import { useLocation } from 'react-router-dom';
import { ScaleLoader } from "react-spinners";


class PasswordResetForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      newPassword: "",
      confirmPassword: "",
      loading: false,
      notification: false,
      notificationText: "",
      danger: false,
    };

  }

  handleInputChange = (event) => {
    const { name, value } = event.target;
    this.setState({
      [name]: value,
    });
  };

  handleSubmit = (event) => {
    event.preventDefault();
    const {
      newPassword,
      confirmPassword
    } = this.state
    const urlParams = new URLSearchParams(window.location.search);
    const resetToken = urlParams.get("token");
    if(newPassword != confirmPassword){
      this.setState({
        danger: true,
        notificationText:"Password do not match",
        
      })
      setTimeout(() => {
        this.setState({ notification: false, danger:false,});
      }, 5000);
      return
    }

    const formData = {
      newPassword,
      resetToken
    };
    console.log(JSON.stringify(formData));
    this.setState({ loading: true })

    // Make a POST request to your backend API with Axios
    axios.post(`/user/resetpassword`, formData)
      .then((response) => {
        // Handle a successful response from the server here
        console.log("Form data sent successfully:", response)
        if (response.data.code === 200) {
          // Handle a successful response from the server here
          this.setState({
            notification: true,
            danger: false,
            notificationText: response.data.message,
            newPassword: "",
            confirmPassword: "",
          })
        }else if(response.data.code === 400 || response.data.code === 500){
          this.setState({
            notification: false,
            danger:true,
            notificationText: response.data.message,
          })

        }
        setTimeout(() => {
          this.setState({ notification: false, danger:false,});
        }, 5000);

      }).catch((error) => {
        // Handle any errors that occur during the request
        console.error("Error fetching tableData:", error);
      });
    this.setState({ loading: false }); // Set loading to false after deleting
  }
  render() {
    return (
      <div>
          <header >
                <nav className="navbar navbar-expand-lg navbar-dark bg-dark" >
                    <a className="navbar-brand" href="#">Inventory Managment System</a>
                    <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarNav">
                        <ul className="navbar-nav ml-auto">
                            <li className="nav-item ">
                                <a className="nav-link" href="/">Home</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" href="/pricing">Pricing</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link active" href="/login">Login</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" href="/contact">Contact</a>
                            </li>
                        </ul>
                    </div>
                </nav>
            </header>
      <div className="auth mx-auto">
        {this.state.loading && (
          <div className="loader">
            <ScaleLoader
              size={1500}
              color={"#123abc"}
              loading={this.state.loading}
              rounded={true} // Set the spinner to be rounded
            />
          </div>
        )}
        {this.state.notification && (
          <div className="mt-5 alert alert-success" role="alert">
            {this.state.notificationText}
          </div>
        )}
        {this.state.danger && (
          <div className="mt-5 alert alert-danger" role="alert">
            {this.state.notificationText}
          </div>
        )}
        <form className="">
          <div className="form-group">
            <h3 className="text-center pb-3">Password Reset</h3>
          </div>
          <div className="form-group">
            <label>New Password</label>
            <input type="password" className="form-control" name="newPassword" value={this.state.newPassword}
              onChange={this.handleInputChange}
            />
          </div>
          <div className="form-group">
            <label>Confirm Password</label>
            <input type="password" className="form-control" id="username" name="confirmPassword" value={this.state.confirmPassword}
              onChange={this.handleInputChange}
            />                    </div>
          <div className="form-group">
            <button className="btn btn-warning w-100 mt-2" onClick={this.handleSubmit}>Reset Password</button>
          </div>
          <div>
            <p>Back to Login. <Link to="/login">Login</Link></p>
          </div>
        </form>
      </div>
      </div>
    )
  }
}

export default PasswordResetForm;


