import React, { Component } from "react";
import axios from "axios";
import { getUser } from '../Auth';
import { ScaleLoader } from "react-spinners";


class PasswordResetForm extends Component {

  constructor(props) {
    super(props);
    this.state = {
      currentPassword: "",
      newPassword: "",
      confirmPassword: "",
      danger: false,
      notification: false,
      notificationText: "",
      loading: false,
    };

  }
  // resetFormFields = () => {
  //   this.setState({
  //     currentPassword: "",
  //     newPassword: "",
  //     confirmPassword: "",
  //     // Add other fields and their initial values if needed
  //   });
  // };

  handleInputChange = (event) => {
    const { name, value } = event.target;
    this.setState({
      [name]: value,
    });
  };

  handleSubmit = (event) => {
    event.preventDefault();
    this.setState({ loading: true }); // Set loading state to true
    const {
      currentPassword,
      newPassword,
      confirmPassword

    } = this.state;
    const formData = {
      currentPassword,
      newPassword,
      confirmPassword
    };
    console.log(JSON.stringify(formData));
    const user = getUser()
    const id = user.userId
    console.log(id)
    // Make a POST request to your backend API with Axios
    axios.put(`/user/changePassword/${id}`, formData)
      .then((response) => {
        // Handle different status codes
        // console.log("response", response)

        if (response.data.code === 401 || response.data.code === 402 || response.data.code === 404) {
          console.log("response", response)
          this.setState({
            danger: true,
            notificationText: response.data.message,
          });
        } else if (response.data.code === 200) {
          console.log("Form data sent successfully:", response.data);
          this.setState({
            notification: true,
            notificationText: response.data.message,
          });
          // this.resetFormFields();
        }
        setTimeout(() => {
          this.setState({ notification: false, danger: false });
        }, 3000);

        // Handle any other status codes if needed
      })
      .catch((error) => {
        // Handle any errors that occur during the request
        this.setState({
          notificationText: "An error occurred while Changing Password",
          danger: true,
        });
        console.error("Error sending form data:", error);
        setTimeout(() => {
          this.setState({ danger: false });
        }, 3000);
      }).finally(() => {
        // Set loading state to false after the request is complete
        this.setState({ loading: false });
      });
  }

  render() {
    return (
      <div className="admin-content mx-auto">
        {this.state.loading && (
          <div className="loader">
            <ScaleLoader
              size={1500}
              color={"#123abc"}
              loading={this.state.loading}
              rounded={true} // Set the spinner to be rounded
            />
          </div>
        )}
        {this.state.danger && (
          <div className="alert alert-danger" role="alert">
            {this.state.notificationText}
          </div>
        )}
        {this.state.notification && (
          <div className="alert alert-success" role="alert">
            {this.state.notificationText}
          </div>
        )}
        <div className="w-100 mb-5">

          <h4>Change Password</h4>
        </div>
        <div className="w-75">
          <form onSubmit={this.handleSubmit}>
            <div className="container-fluid">
              <div className="row">
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Current Password</label>
                    <input
                      type="password"
                      className="form-control"
                      name="currentPassword"
                      required
                      value={this.state.currentPassword}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">New Password</label>
                    <input
                      type="password"
                      className="form-control"
                      name="newPassword"
                      required
                      row="10"
                      value={this.state.newPassword}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Confirm Password</label>
                    <input
                      type="password"
                      className="form-control"
                      name="confirmPassword"
                      required
                      row="10"
                      value={this.state.confirmPassword}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>
              </div>

              <div className="col-12 mt-3">
                <div className="form-group">
                  <button type="submit" className="btn btn-success back-link" value="Submit" >Submit</button>
                </div>
              </div>

            </div>
          </form>
        </div>
      </div>
    );
  }
}

export default PasswordResetForm;


