import React, { Component } from "react";
import axios from "axios";
import { ScaleLoader } from "react-spinners";
import { Link } from "react-router-dom";

class ProductForm extends Component {

  constructor(props) {
    super(props);
    this.state = {
      name: "",
      description: "",
      color: "",
      price: 0,
      seller: "",
      itemRefId: "",
      officeCategoryId: 0,
      employeId: null,
      employeeOptions: [],
      loading: false,
    };

  }

  handleInputChange = (event) => {
    const { name, value } = event.target;
    this.setState({
      [name]: value,
    });
  };


  async componentDidMount() {
    this.setState({ loading: true });
    const id = this.props.match.params.itemId;

    try {
      const response = await axios.get(`/officeItem/get/${id}`)
      console.log(response)
      this.setState({
        name: response.data.name,
        description: response.data.description,
        color: response.data.color,
        price: response.data.price,
        seller: response.data.seller,
        itemRefId: response.data.itemRefId,
        employeId: response.data.employeId,
        officeCategoryId: response.data.officeCategoryId,
      });
      this.setState({ loading: false });

    }
    catch (error) {
      console.error("Error fetching product details:");
    };
  }

  render() {
    const unitOptions = ["pcs", "kg", "lb"];
    return (
      <div className="admin-content mx-auto">
        {this.state.loading && (
          <div className="text-center">
            <ScaleLoader size={150} color={"#123abc"} loading={this.state.loading} rounded={true} />
          </div>
        )}
        <div className="w-100 mb-5">
          <button
            className="btn btn-primary float-right"
            onClick={() => {
              window.history.back();
            }}
          >
            Back to Items List
          </button>

          <h4>Item Details</h4>
        </div>
        <div className="w-75">
          <form>
            <div className="container-fluid">
              <div className="row">
                <div className="col-12">
                  <div className="form-group">
                    <label className="mb-2">Title</label>
                    <input
                      type="text"
                      className="form-control"
                      name="name"
                      value={this.state.name}
                      onChange={this.handleInputChange}
                      readOnly
                    />
                  </div>
                </div>
                <div className="col-12">
                  <div className="form-group">
                    <label className="mb-2">Desciption</label>
                    <textarea
                      type="text"
                      className="form-control"
                      name="description"
                      row="10"
                      value={this.state.description}
                      onChange={this.handleInputChange}
                      readOnly
                    />
                  </div>
                </div>

                <div className="col-8">
                  <div className="form-group">
                    <label className="mb-2">Color</label>
                    <input
                      type="text"
                      className="form-control"
                      name="seller"
                      value={this.state.color}
                      onChange={this.handleInputChange}
                      readOnly
                    />
                  </div>
                </div>
                <div className="col-8">
                  <div className="form-group">
                    <label className="mb-2">Seller</label>
                    <input
                      type="text"
                      className="form-control"
                      name="seller"
                      value={this.state.seller}
                      onChange={this.handleInputChange}
                      readOnly
                    />
                  </div>
                </div>
                <div className="col-4">
                  <div className="form-group">
                    <label className="mb-2">Price</label>
                    <input
                      type="number"
                      className="form-control"
                      name="price"
                      value={this.state.price}
                      onChange={this.handleInputChange}
                      readOnly
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Item Reference Id</label>
                    <input
                      type="text"
                      className="form-control"
                      name="itemRefId"
                      value={this.state.itemRefId}
                      onChange={this.handleInputChange}
                      readOnly
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Assigned to Employee</label>
                    <input
                      type="text"
                      className="form-control"
                      name="itemRefId"
                      value={this.state.employeId}
                      onChange={this.handleInputChange}
                      readOnly
                    />
                  </div>
                </div>

              </div>

              <div className="col-12 mt-3">
                <div className="form-group">
                <Link to={`/app/office/office/product-category/view/${this.state.officeCategoryId}`} className="btn btn-warning back-link">
                    
                    Back
                  </Link>
                </div>
              </div>

            </div>
          </form>
        </div>
      </div>
    );
  }
}

export default ProductForm;



