import React, { Component } from "react";
import PageHeader from "../../components/PageHeader";
import axios from "axios";
import { getUser } from '../Auth';
import { ScaleLoader } from "react-spinners";
import { Card, CardBody, CardTitle } from 'reactstrap'; // Assuming you're using Reactstrap for Bootstrap components

class DashboardPage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            deliveryTotalAmount: 0,
            AmountReceived: 0,
            AmountReceivable: 0,
            shipmentTotalAmount: 0,
            AmountPaid: 0,
            AmountPayable: 0,
            totalExpense: 0,
            totalRevenue: 0,
            profit: 0,
            loss: 0,
            loading: true,
        };
        this.userRole = "owner";
    }
    componentDidMount() {
        // Make your API request here
        console.log("userrrrrr", this.props.user)
        axios.get("/storedashboard/get").then((response) => {
            console.log(response.data)
            this.setState({
                deliveryTotalAmount: response.data.deliveryTotalAmount,
                AmountReceived: response.data.AmountReceived,
                AmountReceivable: response.data.AmountReceivable,
                shipmentTotalAmount: response.data.shipmentTotalAmount,
                AmountPaid: response.data.AmountPaid,
                AmountPayable: response.data.AmountPayable,
                totalExpense: response.data.totalExpense,
                totalRevenue: response.data.totalRevenue,
                profit: response.data.profit,
                loss: response.data.loss,
                loading: false,
            });
        });
        console.log(this.state.AmountPaid);
    }


    render() {
        return (
            <div className="admin-content mx-auto">
                {this.state.loading && (
                    <div className="text-center">
                        <ScaleLoader
                            size={150}
                            color={"#123abc"}
                            loading={this.state.loading}
                            rounded={true} // Set the spinner to be rounded
                        />
                    </div>
                )}
                <Card className="w-75 heading" >
                    <CardBody>
                        <CardTitle tag="h4">Welcome to Dashboard</CardTitle>
                    </CardBody>
                </Card>
                <div className="container-fluid mt-3">
                    <div className="row">
                        <div className="col-6 ">
                            <Card className="dash-summary-card cards">
                                <CardBody>
                                    <p><b>Delivery Total Amount</b></p>
                                    <p>{this.state.deliveryTotalAmount}</p>
                                </CardBody>
                            </Card>
                        </div>
                        <div className="col-6"> 
                        <Card className="dash-summary-card cardss">
                                <CardBody>
                                    <p><b>AmountReceived</b></p>
                                    <p>{this.state.AmountReceived}</p>
                                </CardBody>
                            </Card>
                          
                        </div>
                        <div className="col-6"> 
                        <Card className="dash-summary-card cards">
                                <CardBody>
                                <p><b>AmountRecievable</b></p>
                                <p>{this.state.AmountReceivable}</p>
                                </CardBody>
                            </Card>
                          
                        </div>                       

                        <div className="col-6">
                            <Card className="dash-summary-card cards">
                                <CardBody>
                                    <p><b>Amount Paid</b></p>
                                    <p>{this.state.AmountPaid}</p>
                                </CardBody>
                            </Card>
                        </div>
                        <div className="col-6"> 
                        <Card className="dash-summary-card cardss">
                                <CardBody>
                                <p><b>shipmentTotalAmount</b></p>
                                <p>{this.state.shipmentTotalAmount}</p>
                                </CardBody>
                            </Card>
                          
                        </div>
                     
                        <div className="col-6">
                            <Card className="dash-summary-card cardss">
                                <CardBody>
                                    <p><b>Amount Payable</b></p>
                                    <p>{this.state.AmountPayable}</p>
                                </CardBody>
                            </Card>
                        </div>
                        <div className="col-6">
                            <Card className="dash-summary-card cards">
                                <CardBody>
                                    <p><b>Total Expense</b></p>
                                    <p>{this.state.totalExpense}</p>
                                </CardBody>
                            </Card>
                        </div>
                        <div className="col-6">
                            <Card className="dash-summary-card cardss">
                                <CardBody>
                                    <p><b>Total Revenue</b></p>
                                    <p>{this.state.totalRevenue}</p>
                                </CardBody>
                            </Card>
                        </div>
                        <div className="col-6">
                            <Card className="dash-summary-card cards">
                                <CardBody>
                                    <p><b>Profit</b></p>
                                    <p>{this.state.profit}</p>
                                </CardBody>
                            </Card>
                        </div>
                        <div className="col-6">
                            <Card className="dash-summary-card cardss">
                                <CardBody>
                                    <p><b>Loss</b></p>
                                    <p>{this.state.loss}</p>
                                </CardBody>
                            </Card>
                        </div>
                        <div className="col-6">
                            <Card className="dash-summary-card cards">
                                <CardBody>
                                    <p><b>Total Inventory</b></p>
                                    <p>123</p>
                                </CardBody>
                            </Card>
                        </div>
                        
                    </div>
                </div>
            </div>
        );
    }
}

export default DashboardPage;


