import React, { Component } from "react";
// import "../../../style/admin.css";
import AnchorTag from "../Anchortag";
import axios from "axios";
import { ScaleLoader } from "react-spinners";
import { Link } from "react-router-dom";
import { getUser } from '../Auth'

class UserView extends Component {
    constructor(props) {
        super(props);
        this.state = {
            email: "",
            fullName: "",
            roleId: "",
            role: "",
            businessId: "",
            status: true,
            loading: false, // Add loading state
            notification: false,
            notificationText: "",
            selfRole: 0,
        }
    }
    async componentDidMount() {
        // Make an API request to fetch the item options
        const user = getUser()
        this.setState({ selfRole: user.role })
        const id = this.props.match.params.userId;
        this.setState({ loading: true })
        try {
            const response = await axios.get(`/user/get/${id}`);
            console.log("response", response.data)
            const role = ""
            if (response.data.roleId == 1) {
                this.role = "Admin"
            } else if (response.data.roleId == 2) {
                this.role = "Basic Office"
            } else if (response.data.roleId == 3) {
                this.role = "Basic Store"
            }
            else {
                this.role = "Pro"
            }
            this.setState(
                {
                    email: response.data.email,
                    fullName: response.data.fullName,
                    roleId: response.data.roleId,
                    status: response.data.status,
                    role: this.role,
                    businessId: response.data.businessId,
                });
            console.log("role", this.state.role)
            this.setState({ loading: false })

        } catch (error) {
            console.error("Error fetching item options:", error);
        }
    }

    render() {
        return (
            <div className="admin-content mx-auto">
                {this.state.loading && (
                    <div className="text-center">
                        <ScaleLoader
                            size={150}
                            color={"#123abc"}
                            loading={this.state.loading}
                            rounded={true} // Set the spinner to be rounded
                        />
                    </div>
                )}
                <div className="w-100 mb-5">
                    {this.state.selfRole === 1 ? (
                        <Link to={`/app/store/profile/business-list/view/${this.state.businessId}`} className="btn btn-primary float-right ">
                            Back to User List
                        </Link>
                    ) : this.state.selfRole === 2 ? (
                        <Link to={`/app/office/profile/user`} className="btn btn-primary float-right ">
                            Back to User List
                        </Link>
                    ) : (
                        <Link to={`/app/store/profile/user`} className="btn btn-primary float-right ">
                            Back to User List
                        </Link>
                    )
                    }
                    <h4>User Details</h4>
                </div>
                <div className="w-75">
                    <form>
                        <div className="container-fluid">
                            <div className="row">
                                <div className="col-6">
                                    <div className="form-group">
                                        <label className="mb-2">Full Name</label>
                                        <input type="text" className="form-control" value={this.state.fullName} readOnly />
                                    </div>
                                </div>
                                <div className="col-6">
                                    <div className="form-group">
                                        <label className="mb-2">Email</label>
                                        <input type="text" className="form-control" value={this.state.email} readOnly />
                                    </div>
                                </div>
                                <div className="col-6">
                                    <div className="form-group">
                                        <label className="mb-2">Plan Purchased</label>
                                        <input type="text" className="form-control" value={this.state.role} readOnly />
                                    </div>
                                </div>
                                <div className="col-6">
                                    <div className="form-group">
                                        <label className="mb-2">Status </label>
                                        <input type="text" className="form-control" value={this.state.status ? "Active" : "In Active"} readOnly />
                                    </div>
                                </div>
                                <div className="col-12 mt-3">
                                    <div className="form-group">
                                        {this.state.selfRole === 1 ? (
                                            <Link to={`/app/store/profile/business-list/view/${this.state.businessId}`} className="btn btn-warning ">
                                                Back
                                            </Link>
                                        ) : this.state.selfRole === 2 ? (
                                            <Link to={`/app/office/profile/user`} className="btn btn-warning ">
                                                Back
                                            </Link>
                                        ) : (
                                            <AnchorTag className="btn btn-warning" itemValue="Back" link="/app/store/profile/user" />
                                        )
                                        }
                                    </div>
                                </div>
                            </div>
                        </div>

                    </form>
                </div>

            </div>
        )
    }
}

export default UserView;


