import React, {Component} from "react";
import { Route, Switch } from "react-router-dom";
import ProductCategoryList from "../../../components/customer/PeoductCategoryList";
import ProductCategoryForm from "../../../components/customer/ProductCategoryForm";
import ProductCategoryEdit from "../../../components/customer/ProductCategoryEdit";
import ProductList from "../../../components/customer/ProductList";
import ProductForm from "../../../components/customer/ProductForm";
import ProductView from "../../../components/customer/ProductView";
import ProductEdit from "../../../components/customer/ProductEdit";
import EmployeeList from "../../../components/customer/EmployeeList";
import EmployeeForm from "../../../components/customer/EmployeeForm";
import EmployeeView from "../../../components/customer/EmployeeView";
import EmployeeEdit from "../../../components/customer/EmployeeEdit";
import ExpenseCategoryList from "../../../components/customer/ExpenseCategoryList";
import ExpenseForm from "../../../components/customer/ExpenseForm";
import ExpenseCategoryForm from "../../../components/customer/ExpenseCategoryForm";
import ExpenseList from "../../../components/customer/ExpenseList";
import ExpenseCategoryEdit from "../../../components/customer/ExpenseCategoryEdit";
import ExpenseView from "../../../components/customer/ExpenseView";
import ExpenseEdit from "../../../components/customer/ExpenseEdit";
import Dashboard from "../../../components/customer/Dashboard";


class CustomerShopPage extends Component{
    constructor(props){
        super(props);
    }


    render(){
        return (
            <div className="w-100">
            <Switch>
                <Route exact path={`${this.props.match.path}/product-category/list`} component={ProductCategoryList}/>
                <Route exact path={`${this.props.match.path}/product-category/create`} component={ProductCategoryForm}/>
                <Route exact path={`${this.props.match.path}/product-category/view/:categoryId`} component={ProductList}/>
                <Route exact path={`${this.props.match.path}/product-category/edit/:categoryId`} component={ProductCategoryEdit}/>
                <Route exact path={`${this.props.match.path}/product/create/:categoryId`} component={ProductForm}/>
                <Route exact path={`${this.props.match.path}/product-category/view/view/:itemId`} component={ProductView}/>
                <Route exact path={`${this.props.match.path}/product-category/view/edit/:itemId`} component={ProductEdit}/>


                <Route exact path={`${this.props.match.path}/employee/list`} component={EmployeeList}/>
                <Route exact path={`${this.props.match.path}/employee/create`} component={EmployeeForm}/>
                <Route exact path={`${this.props.match.path}/employee/view/:employeeId`} component={EmployeeView}/>
                <Route exact path={`${this.props.match.path}/employee/edit/:employeeId`} component={EmployeeEdit}/>


                <Route exact path={`${this.props.match.path}/expense/list`} component={ExpenseList}/>
                <Route exact path={`${this.props.match.path}/expense/create`} component={ExpenseForm}/>
                <Route exact path={`${this.props.match.path}/expense/view/:expenseId`} component={ExpenseView}/>
                <Route exact path={`${this.props.match.path}/expense/edit/:expenseId`} component={ExpenseEdit}/>
                <Route exact path={`${this.props.match.path}/dashboard`} component={Dashboard}/>

            </Switch>
            
        </div>
        ) 
    }
}

export default CustomerShopPage;


