import React, { Component } from "react";
import AnchorTag from "../../Anchortag";
import axios from "axios";
import { ScaleLoader } from "react-spinners";
import Table from "../../table/Table";


class DeliveryList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columnList: ["ID", "Title", "Date Added", "Amount", "Action"],
      tableData: [],
      tableDataa: [],

      allowAction: false,
      loading: false, // Add loading state
      notification: false,
      notificationText: "",
      startDate: "",
      endDate: "",
      name: "",
      email: "",
      filter: "",
    };


  }
  handleInputChange = (event) => {
    const { name, value } = event.target;
    this.setState({
      [name]: value,
    });
  };

  handleSubmit = (event) => {
    event.preventDefault();
    const { startDate, endDate } = this.state;
    const formData = {
      startDate,
      endDate,
    };
    console.log(JSON.stringify(formData));
   this.setState({loading:true})
  axios.post("/delivery/getbydate",formData).then((response) => {
    // Update the state with the fetched tableData
    console.log(response)
    const formattedData = response.data.map((item) => ({
      "id": item.deliveryId, // Assuming CategoryId is the ID you want
        "name": item.title,
        "date": item.date,
        "amount": item.totalItemsAmount,
        "status": item.status,
    }));
    this.setState({ loading: false });

    // Update the state with the formatted data
    if (formattedData !== null && formattedData.length > 0) {
      this.setState({ tableData: formattedData, });
      if(this.state.filter === "Pending"){
        this.pendingHandle();
      }else if(this.state.filter === "Dispatched"){
        this.dispatchedHandle();
      }
      else if(this.state.filter === "All"){
        this.Allhandle()
      }
    } else {
      this.setState({ tableData: [] });
    } 
    console.log(this.state.tableData)
  }).catch((error) => {
    this.setState({ loading: false });
    // Handle any errors that occur during the request
    console.error("Error fetching deliveries:", error);
  });
}
  componentDidMount() {
    this.setState({ columnList: ["ID", "Title", "Date Added", "Amount", "status", "Action"] })
    this.setState({ loading: true })
    // Make a GET request to your API endpoint to fetch tableData
    axios.get(`/delivery/get/`).then((response) => {
      // Update the state with the fetched tableData
      console.log(response.data)
      const formattedData = response.data.map((item) => ({
        "id": item.deliveryId, // Assuming CategoryId is the ID you want
        "name": item.title,
        "date": item.date,
        "amount": item.totalItemsAmount,
        "status": item.status,
      }));
      this.setState({ loading: false })
      // Update the state with the formatted data
      if(formattedData!=null){
     
      }else{
        this.setState({tableDataa: "No Data Found"});
        this.setState({tableData: "No Data Found"})
      }
      if (formattedData !== null && formattedData.length > 0) {
        this.setState({ tableDataa: formattedData });
        this.setState({ tableData: formattedData });
       } else {
        this.setState({ tableData: [] });
        this.setState({ tableDataa: [] });
      } 
      
      //  console.log(this.state.tableData)
    }).catch((error) => {
      this.setState({ loading: false })
      // Handle any errors that occur during the request
      console.error("Error fetching deliveries:", error);
    });
  }
  handleDelete = async (id) => {
    console.log('delete called', id)
    this.setState({ loading: true }); // Set loading to true when deleting
    const confirmDelete = window.confirm("Are you sure you want to delete?");
    if (confirmDelete) {
      await axios.delete(`/delivery/delete/${id}`).then((response) => {
        if (response.status === 200) {
          // Handle a successful response from the server here
          console.log("Form data sent successfully:", response.data);
          this.setState({
            notification: true,
            notificationText: response.data.message

          })
        }
        setTimeout(() => {
          this.setState({ notification: false });
        }, 3000);
        console.log(`${id}deleted`)
        window.location.reload();

      }).catch((error) => {
        // Handle any errors that occur during the request
        console.error("Error deleting deliveries:", error);
      });
      this.setState({ loading: false }); // Set loading to false after deleting
    }
    this.setState({ loading: false }); // Set loading to false after deleting
  }
  Allhandle=()=>{
     const { startDate, endDate}= this.state;
     if(startDate && endDate){
      const tableDataaa = this.state.tableDataa.filter((item) => item.date>= startDate && item.date <= endDate)
    this.setState({ tableData: tableDataaa , filter: "All", startDate: this.state.startDate, endDate:this.state.endDate});

  }else{
    const tableDataaa = this.state.tableDataa;
    this.setState({ tableData: tableDataaa, filter: "All", startDate:"", endDate:""})

  }}
  pendingHandle=()=>{
    const {startDate, endDate} = this.state;
    if(startDate && endDate){
    const pendingData = this.state.tableDataa .filter((item) => item.status === "Pending" && item.date >= startDate && item.date <=endDate) ;
    this.setState({ tableData: pendingData , filter: "Pending", startDate: this.state.startDate, endDate: this.state.endDate});

  }else{
     const pendingData = this.state.tableDataa.filter((item) => item.status === "Pending");
     this.setState({
      tableData: pendingData, filter: "Pending", startDate: "", endDate: "",
     })
  }}
  dispatchedHandle=()=>{
    const { startDate, endDate } = this.state;
    if(startDate && endDate){
    const DispatchedData = this.state.tableDataa .filter((item) => item.status === "Dispatched" && item.date >= startDate && 
    item.date <= endDate );
    this.setState({ tableData: DispatchedData, filter: "Dispatched", startDate: this.state.startDate, endDate:this.state.endDate });
  }else{
    // Display all dispatched deliveries without date filter
    const DispatchedData = this.state.tableDataa.filter(
      (item) => item.status === "Dispatched"
    );

    this.setState({
      tableData: DispatchedData,
      filter: "Dispatched",
      startDate: "",
      endDate: "",
    });
  }
}
  render() {
    const table = (
      <React.Fragment>
      <Table
        className="table table-striped"
        columnList={this.state.columnList}
        tableData={this.state.tableData}
        actionLinkPrefix=""
        handledelete={this.handleDelete}
      ></Table>
    </React.Fragment>
    );
  
    const noDataMessage = (
      <p style={{ textAlign:"center", marginTop: 50 }}>
        <h4>No Delivery Found </h4>
      </p>
    );
    return (
      <div className="admin-content mx-auto">
        {this.state.loading && (
          <div className="text-center">
            <ScaleLoader
              size={150}
              color={"#123abc"}
              loading={this.state.loading}
              rounded={true} // Set the spinner to be rounded
            />
          </div>
        )}
        {this.state.notification && (
          <div className="alert alert-success" role="alert">
            {this.state.notificationText}
          </div>
        )}
        <div className="w-100 mb-5">
          <AnchorTag link="/app/store/store/delivery/create" className="btn btn-sm btn-warning float-right" itemValue="Create Delivery"></AnchorTag>
          <h4>Delivery List</h4>
        </div>
        <form onSubmit={this.handleSubmit}>
        <div className="row mb-5">
                    <div className="col-12">
                        <p><b>Search Box</b></p>
                    </div>
                    <div className="col-4">
                        <div className="form-group">
                        <label>Start Date<span style={{color:"red"}}>*</span></label>
                            <input type="date" className="form-control" name="startDate" required placeholder="" value={this.state.startDate} onChange={this.handleInputChange}/>
                        </div>
                    </div>
                    <div className="col-4">
                        <div className="form-group">
                        <label>End Date<span style={{color:"red"}}>*</span></label>
                            <input type="date" className="form-control" name="endDate" required placeholder="" value={this.state.endDate} onChange={this.handleInputChange}/>
                        </div>
                    </div>
                    <div className="col-2">
                        <div className="form-group">
                        <label> </label>
                            <input type="submit" style={{marginTop: 31}} className="w-100 btn  btn-success" value="Search" />
                        </div>
                    </div>
                </div>
                </form>
                <button className="btn btn-secondary mr-1" onClick={this.Allhandle}>All</button>
                <button className="btn btn-secondary mr-1" onClick={this.pendingHandle}>Pending</button>
                <button className="btn btn-secondary" onClick={this.dispatchedHandle}>Dispatched</button>

        <button className="btn  btn-secondary float-right" style={{ marginBottom: 20 }} onClick={() => { window.print() }}>Print</button>
        {this.state.tableData.length > 0 ? (
          table
        ) : (
          <React.Fragment>
            <Table
              className="table table-striped"
              columnList={this.state.columnList}
              tableData={[]}
              actionLinkPrefix=""
              handledelete={this.handleDelete}
            />
            {noDataMessage}
          </React.Fragment>
        )}           </div>
    )
  }
}

export default DeliveryList;


