import React, { Component } from "react";
import axios from "axios";
import { ScaleLoader } from "react-spinners";
import { Link } from "react-router-dom";




class ProductForm extends Component {

  constructor(props) {
    super(props);
    this.state = {
      title: "",
      unit: "", 
      minQuantity: "",
      description: "",
      categoryId: 0,
      notification: false,
      danger: false,
      notificationText: "",
      loading: false,
    };

  }
  
  handleInputChange = (event) => {
    const { name, value } = event.target;
    this.setState({
      [name]: value,
    });
  };
  componentDidMount() {
    const id = this.props.match.params.itemId;
    (async () => {
      this.setState({loading:true})
try {
    const response=await axios.get(`/item/get/${id}`)
    console.log(response)
    this.setState({
      title: response.data.title,
      unit: response.data.unit,
      description: response.data.description,
      lowStockQuantity: response.data.lowStockQuantity,
     categoryId:response.data.categoryId
   });
   this.setState({loading:false})
  }
  catch(error){
    this.setState({loading:false})
      console.error("Error fetching product details:");
    };
  })();
  }
  handleSubmit = (event) => {
    const id = this.props.match.params.itemId;

    event.preventDefault();
    const {
      title,
      unit,
      lowStockQuantity,
      description,
      categoryId

    } = this.state;
    const formData = {
      title,
      unit,
      lowStockQuantity,
      description,
      categoryId
    };
    console.log(JSON.stringify(formData));
    this.setState({loading:true})
           
  // Make a POST request to your backend API with Axios
  axios.put(`/item/update/${id}`, formData)
  .then((response) => {
    if (response.status === 200) {
      // Handle a successful response from the server here
      console.log("Form data sent successfully:", response.data);
      this.setState({
        notification: true,
        notificationText: response.data.message

      })
    }
    setTimeout(() => {
      this.setState({ notification: false, });
    }, 3000);
  })
  .catch((error) => {
    // Handle any errors that occur during the request
    this.setState({ notificationText: "An error occured while updating officeItem", danger: true, })
    setTimeout(() => {
      this.setState({ danger: false });
    }, 3000);
    console.error("Error sending form data:", error);
  }).finally(() => {
    // Set loading to false after the request is complete
    this.setState({ loading: false });
  });
};


  render() {
    
    const unitOptions = ["pcs", "kg", "lb"]; // Array of unit options

    const employees = [
      { id: 1, name: "John Doe" },
      { id: 2, name: "Jane Smith" },
      { id: 3, name: "Michael Johnson" },
      // Add more employees as needed
    ];
    return (
      <div className="admin-content mx-auto">
          {this.state.loading && (
          <div className="text-center">
            <ScaleLoader
              size={150}
              color={"#123abc"}
              loading={this.state.loading}
              rounded={true} // Set the spinner to be rounded
            />
          </div>
        )}
        {this.state.notification && (
          <div className="alert alert-success" role="alert">
            {this.state.notificationText}
          </div>
        )}
        {this.state.danger && (
          <div className="alert alert-danger" role="alert">
            {this.state.notificationText}
          </div>
        )}
        <div className="w-100 mb-5">
          <button
            className="btn btn-primary float-right"
            onClick={() => {
              window.history.back();
            }}
          >
            Back to Items List
          </button>

          <h4>Update Item</h4>
        </div>
        <div className="w-75">
          <form onSubmit={this.handleSubmit}>
            <div className="container-fluid">
              <div className="row">
                <div className="col-12">
                  <div className="form-group">
                    <label className="mb-2">Title</label>
                    <input
                      type="text"
                      className="form-control"
                      name="title"
                      required
                      value={this.state.title}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>
                <div className="col-12">
                  <div className="form-group">
                    <label className="mb-2">Desciption</label>
                    <textarea
                      type="text"
                      className="form-control"
                      name="description"
                      row="10"
                      value={this.state.description}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>
 
                <div className="col-4">
                  <div className="form-group">
                    <label className="mb-2">Unit</label>
                    <select
                      className="form-control"
                      name="unit"
                      value={this.state.unit}
                      onChange={this.handleInputChange}
                    >
                      {unitOptions.map((unit) => (
                        <option key={unit} value={unit}>
                          {unit}
                        </option>
                      ))}
                    </select>
                  </div>
                </div>
                <div className="col-4">
                  <div className="form-group">
                    <label className="mb-2">Low Stock Quantity</label>
                    <input
                      type="number"
                      className="form-control"
                      name="lowStockQuantity"
                      required
                      value={this.state.lowStockQuantity}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>
                </div>

                <div className="col-12 mt-3">
                  <div className="form-group">
                  <Link to={`/app/store/store/product-category/view/${this.state.categoryId}`} className="btn btn-warning back-link">
                    Back
                  </Link>
                  <button type="submit" className="btn btn-success ml-3" value="Submit">Update</button>
                  </div>
                </div>
             
            </div>
          </form>
        </div>
      </div>
    );
  }
}

export default ProductForm;



