import React, { Component } from "react";
import axios from "axios";
import { ScaleLoader } from "react-spinners";
import { Link } from "react-router-dom";

class DeliveryForm extends Component {
  constructor(props) {
    super(props);

    this.state = {
      title: "",
      amount: "",
      date: "",
      cargoCharges: 0,
      existingItems: [
        {
          itemId: "",
          customerId: "",
          quantity: "",
          pricePerUnit: "", // Update with the actual item price
          customerOptions: [],
        },
      ],
      // existingCustomers: [],
      itemOptions: [],
      customerOptions: [],
      quantity: 600,
      itemPrice: 0,
      notification: false,
      notificationText: "",
      danger: false,
      loading: false,
    };
  }

  resetFormFields = () => {
    this.setState({
      title: "",
      amount: "",
      date: "",
      cargoCharges: "",
      existingItems: [
        {
          itemId: "",
          customerId: "",
          quantity: "",
          pricePerUnit: "", // Update with the actual item price
          customerOptions: [],
        },
      ],
      // existingCustomers: [],
      itemOptions: [],
      customerOptions: [],
      quantity: 600,
      itemPrice: 0,
      // Add other fields and their initial values if needed
    });
  };

  handleInputChange = (event) => {
    const { name, value } = event.target;
    this.setState({
      [name]: value,
    });
  };
  handleItemInputChange = async (event, index) => {
    const { name, value } = event.target;
    const updatedItems = [...this.state.existingItems];
    updatedItems[index][name] = value;
    this.setState({ existingItems: updatedItems });
    console.log('value', value)
    try {
      const response = await axios.get("/customer/get");
      this.setState({ customerOptions: response.data });
    } catch (error) {
      console.error("Error fetching item options:", error);
    }

    if (name == 'itemId') {
      const existingCustomerIds = this.state.existingItems
        .filter((item) => item.itemId === value)
        .map((item) => parseInt(item.customerId));
      console.log('fexistingCustomerIds', existingCustomerIds)

      // Filter response.data to exclude customer options that are already in existingItems
      const filteredCustomerOptions = this.state.customerOptions.filter((customer) => !existingCustomerIds.includes(customer.customerId));
      console.log('filteredData', filteredCustomerOptions)
      const updatedItems = [...this.state.existingItems];
      updatedItems[index].customerOptions = filteredCustomerOptions;
      this.setState({ existingItems: updatedItems });
    }
  };
  handleAddExistingItem = () => {
    const newItem = {
      itemId: 0,
      customerId: 0,
      quantity: 0,
      itemPrice: 0, // Update with the actual item price
    };
    this.setState((prevState) => ({
      existingItems: [...prevState.existingItems, newItem],
    }));
  };

  handleDeleteExistingItem = (index) => {
    const updatedItems = [...this.state.existingItems];
    updatedItems.splice(index, 1);
    this.setState({ existingItems: updatedItems });
  };
  handleSubmit = (event) => {
    event.preventDefault();
    const { title, date, vendor, charges, existingItems, cargoCharges } = this.state;
    const formData = {
      title,
      date,
      cargoCharges,
      deliveryItems: existingItems,
      status: "Pending"
    };
    console.log(formData);
    this.setState({ loading: true });
    // Make a POST request to your backend API with Axios
    axios.post("/delivery/create", formData)
      .then((response) => {
        if (response.data.code === 400 || response.data.code === 401 || response.data.code === 402 || response.data.code === 403 || response.data.code === 404 || response.data.code === 500) {
          console.log("response")
          this.setState({
            danger: true,
            notificationText: response.data.message,
          });
          setTimeout(() => {
            this.setState({ notification: false, danger: false });
          }, 12000);
        } else if (response.data.code === 200) {
          console.log("Form data sent successfully:", response.data);
          this.setState({
            notification: true,
            danger:false,
            notificationText: response.data.message,
          });
          this.resetFormFields();
          setTimeout(() => {
            this.setState({ notification: false, danger: false });
          }, 5000);
        }


        // Handle any other status codes if needed
      })
      .catch((error) => {
        // Handle any errors that occur during the request
        this.setState({
          notificationText: "An error occurred while creating Delivery",
          danger: true,
        });
        console.error("Error sending form data:", error);
        setTimeout(() => {
          this.setState({ danger: false });
        }, 3000);
      }).finally(() => {
        // Set loading state to false after the request is complete
        this.setState({ loading: false });
      });
  }
  async componentDidMount() {
    // Make an API request to fetch the item options
    try {
      const response = await axios.get("/item/get");
      this.setState({ itemOptions: response.data });
    } catch (error) {
      console.error("Error fetching item options:", error);
    }

  }
  render() {
    const { existingItems, itemOptions, customerOptions } = this.state;

    return (
      <div className="admin-content mx-auto">
        {this.state.loading && (
          <div className="text-center">
            <ScaleLoader
              size={150}
              color={"#123abc"}
              loading={this.state.loading}
              rounded={true} // Set the spinner to be rounded
            />
          </div>
        )}
        {this.state.notification && (
          <div className="alert alert-success" role="alert">
            {this.state.notificationText}
          </div>
        )}
        {this.state.danger && (
          <div className="alert alert-danger" role="alert">
            {this.state.notificationText}
          </div>
        )}
        <div className="w-100 mb-5">
          <button
            className="btn btn-primary float-right"
            onClick={() => {
              window.history.back();
            }}
          >
            Back to Delivery List
          </button>

          <h4>Add Delivery</h4>
        </div>
        <div className="w-75">
          <form onSubmit={this.handleSubmit}>
            <div className="container-fluid">
              <div className="row">
                <div className="col-12">
                  <div className="form-group">
                    <label className="mb-2">Title</label>
                    <input
                      type="text"
                      className="form-control"
                      name="title"
                      required
                      value={this.state.title}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>

                <div className="col-4">
                  <div className="form-group">
                    <label className="mb-2">Date</label>
                    <input
                      type="date"
                      required
                      className="form-control"
                      name="date"
                      value={this.state.date}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>
                <div className="col-4">
                  <div className="form-group">
                    <label className="mb-2">Delivery Charges</label>
                    <input
                      type="number"
                      className="form-control"
                      name="cargoCharges"
                      required
                      value={this.state.cargoCharges}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>
                <div className="col-4">
                  <div className="form-group">
                    <label className="mb-2">Total Items Amount</label>
                    <input
                      type="text"
                      className="form-control"
                      name="amount"
                      readOnly
                      value={this.state.amount}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>
                <div>

                </div>

                <div className="form-group">
                  {existingItems.map((item, index) => (
                    <div key={index} className="existing-item" style={{marginLeft: 15, backgroundColor: "#aab4b5", borderRadius: 10, marginBottom: 20, padding: 20 }}>
                      <div className="row">
                        <div className="col-6">
                          <div className="form-group">
                            <label className="mb-2">Select Item</label>
                            <select
                              className="form-control"
                              name="itemId"
                              required
                              value={item.itemId}
                              onChange={(event) => this.handleItemInputChange(event, index)}
                            >
                              <option value="">Select Item</option>
                              {itemOptions.map((option) => (
                                <option key={option.value} value={option.itemId}>
                                  {option.itemId}-{option.title}
                                </option>
                              ))}
                            </select>
                          </div>
                        </div>
                        <div className="col-6">
                          <div className="form-group">
                            <label className="mb-2">Customer</label>
                            <select
                              className="form-control"
                              name="customerId"
                              required
                              value={item.customerId}
                              onChange={(event) => this.handleItemInputChange(event, index)}
                            >
                              <option value="">Select Customers</option>
                              {item.customerOptions && item.customerOptions.map((option) => (
                                <option key={option.value} value={option.customerId}>
                                  {option.customerId}-{option.firstName} {option.lastName}
                                </option>
                              ))}
                              {/* Add more options as needed */}
                            </select>
                          </div>
                        </div>
                        <div className="col-4">
                          <div className="form-group">
                            <label className="mb-2">Quantity</label>
                            <input
                              required
                              type="number"
                              className="form-control"
                              name="quantity"
                              value={item.quantity}
                              onChange={(event) => this.handleItemInputChange(event, index)}
                            />
                          </div>
                        </div>
                        <div className="col-4">
                          <div className="form-group">
                            <label className="mb-2">Price per Unit</label>
                            <input
                              type="number"
                              className="form-control"
                              name="pricePerUnit"
                              required
                              value={item.pricePerUnit}
                              onChange={(event) => this.handleItemInputChange(event, index)}
                            />

                          </div>
                        </div>
                      </div>
                      <button
                        className="btn btn-danger float-right"
                        onClick={() => this.handleDeleteExistingItem(index)}
                      >
                        Delete
                      </button>
                    </div>
                  ))}
                  <button
                    className="btn btn-primary"
                    type="button"
                    onClick={this.handleAddExistingItem}
                    style={{marginLeft: 15}}
                  >
                    + Add Item
                  </button>
                </div>


              </div>

              <div className="col-12 mt-3">
                <div className="form-group">
                  <Link to="/app/store/store/delivery/list" className="btn btn-warning back-link">
                    Back
                  </Link>
                  <button type="submit" className="btn btn-success ml-3" value="Dispatch">Submit</button>
                </div>
              </div>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

export default DeliveryForm;



