import React, { Component } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import { ScaleLoader } from "react-spinners";


class EmployeeView extends Component {
  constructor(props) {
    super(props);
    this.productDetails =
      "PERFECT GIFT IDEA: Works on wet, tion and is not a product defect.";
    this.state = {
      firstName: "Arslan",
      lastName: "Tiwana",
      email: "A@gamail.com",
      joiningDate: "23-08-2023",
      gender: "male",
      designation: "junior",
      items: [
        { id: 1, name: "Keyboard", color: "black", refId: "L56985" },
        { id: 2, name: "Laptop", color: "white", refId: "L2030" },
        { id: 3, name: "Laptop", color: "white", refId: "L2030" },
        { id: 4, name: "Laptop", color: "white", refId: "L2030" },

        // Add more items as needed
      ],
      loading: false,
    };
  }
  async componentDidMount() {
    this.setState({ loading: true });
    const id = this.props.match.params.employeeId;

    try {
      const response = await axios.get(`/employee/get/${id}`)
      console.log(response)
      const date = new Date(response.data.joiningDate);
        const options = { year: 'numeric', month: 'short', day: 'numeric' };
        const formattedDate = date.toLocaleDateString('en-US', options);       
      this.setState({
        firstName: response.data.firstName,
        lastName: response.data.lastName,
        email: response.data.email,
        joiningDate: formattedDate,
        gender: response.data.gender,
        designation: response.data.designation,
        items: response.data.officeItems
      });
      this.setState({ loading: false });


    }
    catch (error) {
      console.error("Error fetching product details:");
    };
  }
  render() {
    return (
      <div className="admin-content mx-auto">
        {this.state.loading && (
          <div className="text-center">
            <ScaleLoader size={150} color={"#123abc"} loading={this.state.loading} rounded={true} />
          </div>
        )}
        <div className="w-100 mb-5">
          <h4>Employee Details</h4>
          <button
            className="btn  btn-secondary float-right"
            style={{ marginBottom: 20 }}
            onClick={() => {
              window.print();
            }}
          >
            Print
          </button>
        </div>
        <div className="w-75">
          <form onSubmit={this.handleSubmit}>
            <div className="container-fluid">
              <div className="row">
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">First Name</label>
                    <input
                      type="text"
                      className="form-control"
                      name="firstName"
                      value={this.state.firstName}
                      readOnly
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Last Name</label>
                    <input
                      type="text"
                      className="form-control"
                      name="lastName"
                      value={this.state.lastName}
                      readOnly
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Email</label>
                    <input
                      type="text"
                      className="form-control"
                      name="email"
                      row="10"
                      value={this.state.email}
                      readOnly
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Joining Date</label>
                    <input
                      type="text"
                      className="form-control"
                      name="joiningDate"
                      value={this.state.joiningDate}
                      readOnly
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Gender</label>
                    <input
                      className="form-control"
                      name="gender"
                      value={this.state.gender}
                      onChange={this.handleInputChange}
                      readOnly
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Designation</label>
                    <input
                      className="form-control"
                      name="designation"
                      value={this.state.designation}
                      readOnly
                    />
                  </div>
                </div>
                <div className="" style={{ width: "100%", marginLeft: 0, }}>
                  <h4 style={{ textAlign: "center" }}>
                    Items Assigned to {this.state.firstName} {this.state.lastName}
                  </h4>
                  {this.state.items.length > 0 ? (
                    <div>
                      <table
                        style={{
                          width: "100%",
                          borderCollapse: "collapse",
                          border: "1px solid #ddd",
                          backgroundColor: "#f5f5f5"
                        }}
                      >
                        <thead>
                          <tr>
                            <th style={tableHeaderStyle}>Reference ID</th>
                            <th style={tableHeaderStyle}>Name</th>
                            <th style={tableHeaderStyle}>Color</th>
                          </tr>
                        </thead>
                        <tbody>
                          {this.state.items && this.state.items.map((item) => (
                            <tr
                              key={item.id}
                              style={tableRowStyle}
                              onClick={() => {
                                // Navigate to "View Item" page
                                this.props.history.push(`/app/office/office/product-category/view/view/${item.officeItemId}`);
                              }}
                            >
                              <td style={tableCellStyle}>{item.itemRefId}</td>
                              <td style={tableCellStyle}>{item.name}</td>
                              <td style={tableCellStyle}>{item.color}</td>
                            </tr>
                          ))}
                        </tbody>
                      </table>
                    </div>) : (
                    <div>
                      <table
                        style={{
                          width: "100%",
                          borderCollapse: "collapse",
                          border: "1px solid #ddd",
                          backgroundColor: "#f5f5f5"
                        }}
                      >
                        <thead>
                          <tr>
                            <th style={tableHeaderStyle}>Reference ID</th>
                            <th style={tableHeaderStyle}>Name</th>
                            <th style={tableHeaderStyle}>Color</th>
                          </tr>
                        </thead>
                        <tbody>
                          {this.state.items && this.state.items.map((item) => (
                            <tr
                              key={item.id}
                              style={tableRowStyle}
                              onClick={() => {
                                // Navigate to "View Item" page
                                this.props.history.push(`/app/office/office/product-category/view/view/${item.officeItemId}`);
                              }}
                            >
                              <td style={tableCellStyle}>{item.itemRefId}</td>
                              <td style={tableCellStyle}>{item.name}</td>
                              <td style={tableCellStyle}>{item.color}</td>
                            </tr>
                          ))}
                        </tbody>
                      </table>
                      <p style={{ marginLeft: 400, marginTop: 30 }}>
                        <h4>No Item Assigned </h4>
                      </p></div>
                  )}

                </div>

                <div className="col-12 mt-3">
                  <div className="form-group">
                    <Link to="/app/office/office/employee/list" className="btn btn-warning">
                      Back
                    </Link>
                  </div>
                </div>
              </div>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

export default EmployeeView;
// Inline CSS styles
const tableHeaderStyle = {
  backgroundColor: "#c2c5c0",
  padding: "10px",
  border: "1px solid #ddd",
  fontWeight: "bold",
};

const tableRowStyle = {
  cursor: "pointer",
  transition: "background-color 0.3s",
};

const tableCellStyle = {
  padding: "10px",
  border: "1px solid #ddd",
};


