import React, { Component } from "react";
import AnchorTag from "../../components/Anchortag";
import InputFormGroup from "../input/InputFormGroup";
import SelectFormGroup from "../input/SelectFormGroup";
import axios from "axios";
import { ScaleLoader } from "react-spinners";

class EmployeeForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      title: "",
      amount: "",
      date: "",
      type: "",
      description: "",
      notification: false,
      danger: false,
      notificationText: "",
      loading: false,
    };
  }
  resetFormFields = () => {
    this.setState({
      title: "",
      amount: "",
      date: "",
      type: "",
      description: "",
      // Add other fields and their initial values if needed
    });
  }
  handleInputChange = (event) => {
    const { name, value } = event.target;
    this.setState({
      [name]: value,
    });
  };

  handleSubmit = (event) => {
    event.preventDefault();
    const { title,
      amount,
      date,
      type,
      description, } =
      this.state;
    const formData = {
      title,
      amount,
      date,
      type,
      description,
    };
    this.setState({ loading: true });
    console.log(formData);
    // Make a POST request to your backend API with Axios
    axios.post("/expense/create", formData)
      .then((response) => {
        if (response.status === 201) {
          // Handle a successful response from the server here
          console.log("Form data sent successfully:", response.data);
          this.setState({
            notification: true,
            notificationText: response.data.message

          })
          this.resetFormFields()
        }
        setTimeout(() => {
          this.setState({ notification: false });
        }, 3000);
      })
      .catch((error) => {
        // Handle any errors that occur during the request
        this.setState({ notificationText: "An error occured while creating Expense", danger: true, })
        setTimeout(() => {
          this.setState({ danger: false });
        }, 3000);
        console.error("Error sending form data:", error);
      }).finally(() => {
        // Set loading to false after the request is complete
        this.setState({ loading: false });
      });
  };
  render() {
    return (
      <div className="admin-content mx-auto">
        {this.state.loading && (
          <div className="text-center">
            <ScaleLoader
              size={150}
              color={"#123abc"}
              loading={this.state.loading}
              rounded={true} // Set the spinner to be rounded
            />
          </div>
        )}
        {this.state.notification && (
          <div className="alert alert-success" role="alert">
            {this.state.notificationText}
          </div>
        )}
        {this.state.danger && (
          <div className="alert alert-danger" role="alert">
            {this.state.notificationText}
          </div>
        )}
        <div className="w-100 mb-5">
          <button
            className="btn btn-primary float-right"
            onClick={() => {
              window.history.back();
            }}
          >
            Back to Expense List
          </button>
          <h4>Create Expense</h4>
        </div>
        <div className="w-75">
          <form onSubmit={this.handleSubmit}>
            <div className="container-fluid">
              <div className="row">
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Title</label>
                    <input
                      type="text"
                      className="form-control"
                      name="title"
                      required
                      value={this.state.title}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Amount</label>
                    <input
                      type="number"
                      className="form-control"
                      name="amount"
                      required
                      value={this.state.amount}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Date</label>
                    <input
                      type="date"
                      required
                      className="form-control"
                      name="date"
                      row="10"
                      value={this.state.date}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Type</label>
                    <input
                      type="text"
                      className="form-control"
                      name="type"
                      value={this.state.type}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>
                <div className="col-12">
                  <div className="form-group">
                    <label className="mb-2">Description</label>
                    <textarea
                      className="form-control"
                      name="description"
                      value={this.state.description}
                      onChange={this.handleInputChange}
                    />

                  </div>
                </div>
                <div className="col-12 mt-3">
                  <div className="form-group">
                    <button
                      className="btn btn-warning"
                      onClick={() => {
                        window.history.back();
                      }}
                    >
                      Back
                    </button>
                    <button type="submit" className="btn btn-success ml-3" value="Submit">Submit</button>
                  </div>
                </div>
              </div>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

export default EmployeeForm;



