import React, { Component } from "react";
import { Link } from "react-router-dom";
import axios from '../axiosConfig';
import { useAuth } from '../AuthContext';
import { ScaleLoader } from "react-spinners";



class LoginForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      email: "",
      password: "",
      userRole: 0,
      user: {},
      danger: false,
      notificationText: "",
      loading: false,
    };


  }
  resetFormFields = () => {
    this.setState({
      email: "",
      password: "",
      // Add other fields and their initial values if needed
    });
  };
  handleInputChange = (event) => {
    const { name, value } = event.target;
    this.setState({
      [name]: value,
    });
  };

  handleSubmit = async (event) => {

    this.setState({ danger: false });

    event.preventDefault();
    const {
      email,
      password,
      userRole
    } = this.state


    const formData = {
      email,
      password
    };
    this.setState({ loading: true });
    console.log(JSON.stringify(formData));

    // Make a POST request to your backend API with Axios
    await axios.post(`/user/login`, formData)
      .then((response) => {
        // console.log("response",response)
        console.log("Form data sent successfully:", response);
        // console.log("userRole", userRole)
        // Handle a successful response from the server here
        if (response.data.code === 400 || response.data.code === 401 || response.data.code === 402 || response.data.code === 403 || response.data.code === 500) {
          // console.log("", response.data);
          this.setState({
            danger: true,
            notificationText: response.data.message,
          })
          //  console.log(response.data.message)

        } else if (response.data.code === 200) {

          const userRole = response.data.data.user.role
          const user = response.data.data.user
          const isPasswordChanged = response.data.data.user.isPasswordChanged
          const authToken = response.data.authtoken;

          // Store the token securely, e.g., in local storage
          localStorage.setItem('authorization', authToken);
          this.props.login(this.state.user);
          console.log("sASsasas", this.props.user)
          if (isPasswordChanged === false) {
            window.location.href = '/passwordUpdate'
          } else if (userRole === 2) {
            window.location.href = "/app/office/office/dashboard";
          } else if (userRole === 1) {
            window.location.href = "/app/store/profile/business-list";
          } else {
            window.location.href = "/app/store/store/dashboard";
          }
        }

      })

      .catch((error) => {
        // Handle any errors that occur during the request
        this.setState({ notificationText: "An error occured while Login", danger: true, })

        console.error("Error sending form data:", error);
      }).finally(() => {
        // Set loading to false after the request is complete
        this.setState({ loading: false });
      });
  };
  render() {

    return (
      <div>
          <header >
                <nav className="navbar navbar-expand-lg navbar-dark bg-dark" >
                    <a className="navbar-brand" href="#">Inventory Managment System</a>
                    <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarNav">
                        <ul className="navbar-nav ml-auto">
                            <li className="nav-item ">
                                <a className="nav-link" href="/">Home</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" href="/pricing">Pricing</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link active" href="/login">Login</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link " href="/contact">Contact</a>
                            </li>
                        </ul>
                    </div>
                </nav>
            </header>
      <div className="auth mx-auto">
        
        {this.state.loading && (
          <div className="text-center">
            <ScaleLoader
              size={150}
              color={"#123abc"}
              loading={this.state.loading}
              rounded={true} // Set the spinner to be rounded
            />
          </div>
        )}
        {this.state.danger && (
          <div className= "mt-5 alert alert-danger" role="alert">
            {this.state.notificationText}
          </div>
        )}
        <form className="">
          <div className="form-group">
            <h4 className="text-center pb-3">User Login</h4>
          </div>
          <div className="form-group">
            <label>Email<span style={{color: "red"}}>* </span></label>
            <input required type="email" className="form-control" name="email"  value={this.state.email}
              onChange={this.handleInputChange}
            />                      </div>
          <div className="form-group">
            <label>Password<span style={{color: "red"}}>* </span></label>
            <input type="password" className="form-control" name="password" required value={this.state.password}
              onChange={this.handleInputChange}
            />                      </div>

          <div className="form-group">
            <button className="btn btn-warning w-100 mt-2" onClick={this.handleSubmit}>Login</button>

          </div>
          <div>
            <p>Forgot password? <Link to="/password-reset-account-verify">Click to Reset</Link></p>
          </div>
        </form>
      </div>
      </div>
    )
  }
}

export default LoginForm;


