import React, {Component} from "react";
import TableHead from "./TableHead";
import TdTag from "./TdTag"


class Table extends Component{
    constructor(props){
        super(props);
        // this.props.delete()
        // console.log("asdfjkahskd")
        // console.log(this.props.handledelete)
    }

    renderAction(data){
        if(this.props.allowAction){
            if(data.status=="Dispatched"){

            return <TdTag value={data["id"]} isLinked="true" linkPrefix={this.props.actionLinkPrefix} handledelete={this.props.handledelete} allowEdit={false}></TdTag>
            }
            else{
                return <TdTag value={data["id"]} isLinked="true" linkPrefix={this.props.actionLinkPrefix} handledelete={this.props.handledelete} allowEdit={true}></TdTag>

            }
        }
    }


    render(){
        return (
            <table className={this.props.className+"table-bordered table-hover table-sm"}>
                <TableHead columnList={this.props.columnList}></TableHead>
                <tbody>
                    {this.props.tableData&&
                       
                        this.props.tableData.map((data, index) => {
                            console.log(data)
                            return (
                            <tr key={index}>
                                {
                                    Object.keys(data).map((key, index) => {
                                        console.log("data",data.status)
                                        if(data.status=="Dispatched"){
                                        return <TdTag key={index} value={data[key]} isLinked="false"  handledelete={this.props.handledelete} allowEdit={false}></TdTag>
                                    }else{
                                        return <TdTag key={index} value={data[key]} isLinked="false"  handledelete={this.props.handledelete} allowEdit={true}></TdTag>

                                    }
                                    })
                                }
                                
                                {this.renderAction(data)}
                                
                                
                            </tr>
                            )
                        })
                    }
                </tbody>
            </table>
        //      <table className={this.props.className}>
        //      <TableHead columnList={this.props.columnList}></TableHead>
        //      <tbody>
        //          {this.props.tableData && this.props.tableData.length >= 1 ? (
        //              this.props.tableData.map((data, index) => (
        //                  <tr key={index}>
        //                      {Object.keys(data).map((key, subIndex) => (
        //                          <TdTag key={subIndex} value={data[key]} isLinked={false} />
        //                      ))}
        //                      {this.renderAction(data)}
        //                  </tr>
        //              ))
        //          ) : (
                     
        //                  <p style={{marginLeft:300}}>No Data Found</p>
                 
        //          )}
        //      </tbody>

        //  </table>
        ) 
    }
}

Table.defaultProps = {
    allowAction: true
}

export default Table;


