import React, { Component } from "react";
import AnchorTag from "../../Anchortag";
import Table from "../../table/Table";
import axios from "axios";
import { ScaleLoader } from "react-spinners";




class ProductList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columnList: ["ID", "Item Title", "Stock", "Delivered", "Action"],
      tableData: [],
      tableDataa: [],
      name: "",
      ItemReferenceId: "",
      categoryId: this.props.match.params.id,
      loading: false, // Add loading state
      notification: false,
      notificationText: "",
    };

    this.handlePrint = () => {
      window.print();
    };
   


  }
  handleInputChange = (event) => {
    const { name, value } = event.target;
    this.setState({
      [name]: value,
    });
  };

  handleSubmit = (event) => {
    event.preventDefault();
    const { name, ItemReferenceId } = this.state;
    const formData = {
      name,
      ItemReferenceId,
    };
    console.log(JSON.stringify(formData));
  };
  componentDidMount() {
    this.setState({ columnList: ["ID", "Item Title", "Stock", "Delivered", "low Stock Quantity", "Action"] })
    this.setState({ loading: true })

    const id = this.props.match.params.id
    console.log("hetyyyyyyyyyyyyyyyy" + id)
    // Make a GET request to your API endpoint to fetch tableData
    axios.get(`/itemcategory/get/${id}/item`).then((response) => {
      // Update the state with the fetched tableData
      console.log(response)
      const formattedData = response.data.map((item) => ({
        "id": item.itemId, // Assuming CategoryId is the ID you want
        "name": item.title,
        "stock": item.stock,
        "delivered": item.delivered,
        "lowStockQuantity": item.lowStockQuantity
      }));
      this.setState({ loading: false })

      // Update the state with the formatted data
      if (formattedData !== null && formattedData.length > 0) {
        this.setState({ tableData: formattedData });
        this.setState({ tableDataa: formattedData });
      } else {
        this.setState({ tableData: [] });
        this.setState({ tableDataa: [] });
      }

      this.setState({ columnList: ["ID", "Item Title", "Stock", "Delivered", "low Stock Quantity", "Action"] })
      // console.log(this.state.tableData)
      // console.log(this.state.columnList)
    }).catch((error) => {
      this.setState({ loading: false })
      // Handle any errors that occur during the request
      console.error("Error fetching tableData:", error);
    });
  }
  handleDelete = async (id) => {
    console.log('delete called', id)
    this.setState({ loading: true })
    const confirmDelete = window.confirm("Are you sure you want to delete?");
    if (confirmDelete) {
      await axios.delete(`/item/delete/${id}`).then((response) => {
        if (response.status === 200) {
          // Handle a successful response from the server here
          console.log("Form data sent successfully:", response.data);
          this.setState({
            notification: true,
            loading: false,
            notificationText: response.data.message

          })
        }

        console.log(`${id}deleted`)
        window.location.reload();
      }).catch((error) => {
        // Handle any errors that occur during the request
        console.error("Error fetching tableData:", error);
      });
      this.setState({ loading: false }); // Set loading to false after deleting
    }
    this.setState({ loading: false }); // Set loading to false after deleting
  }
  Allhandle = () => {

    this.setState({ tableData: this.state.tableDataa });
  }
  pendingHandle = () => {
    const pendingData = this.state.tableDataa.filter((item) => item.stock <= item.lowStockQuantity);
    this.setState({ tableData: pendingData });

  }
  render() {
    const id = this.props.match.params.id;
    const table = (
      <React.Fragment>
        <Table
          className="table table-striped"
          columnList={this.state.columnList}
          tableData={this.state.tableData}
          actionLinkPrefix=""
          handledelete={this.handleDelete}
        ></Table>
      </React.Fragment>
    );

    const noDataMessage = (
      <p style={{ textAlign: "center", marginTop: 50 }}>
        <h4> No Item Found </h4>
      </p>
    );

    return (

      <div className="admin-content mx-auto">
        {this.state.loading && (
          <div className="text-center">
            <ScaleLoader
              size={150}
              color={"#123abc"}
              loading={this.state.loading}
              rounded={true} // Set the spinner to be rounded
            />
          </div>
        )}
        {this.state.notification && (
          <div className="alert alert-success" role="alert">
            {this.state.notificationText}
          </div>
        )}
        <div className="w-100 mb-5">
          <AnchorTag link={`/app/store/store/product/create/${id}`} className="btn btn-sm btn-warning float-right" itemValue="Add New Item"></AnchorTag>

          <h4>{id} Item List</h4>
        </div>
        {/* <form onSubmit={this.handleSubmit}>
                <div className="row mb-5">
                    <div className="col-12">
                        <p><b>Search Box</b></p>
                    </div>
                    <div className="col-4">
                        <div className="form-group">
                            <input type="text" className="form-control" name="name" placeholder="Name" value={this.state.name} onChange={this.handleInputChange}/>
                        </div>
                    </div>
                    <div className="col-4">
                        <div className="form-group">
                            <input type="text" className="form-control" name="ItemReferenceId" placeholder="Item Reference Id" value={this.state.ItemReferenceId} onChange={this.handleInputChange}/>
                        </div>
                    </div>
                    <div className="col-2">
                        <div className="form-group">
                            <input type="submit" className="w-100 btn  btn-success" value="Search" />
                        </div>
                    </div>
                </div>
                </form> */}
        <button className="btn btn-secondary mr-1" onClick={this.Allhandle}>All</button>
        <button className="btn btn-secondary mr-1" onClick={this.pendingHandle}>Low Stock</button>
        <button className="btn  btn-secondary float-right" style={{ marginBottom: 20 }} onClick={this.handlePrint}>Print</button>
        {this.state.tableData.length > 0 ? (
          table
        ) : (
          <React.Fragment>
            <Table
              className="table table-striped"
              columnList={this.state.columnList}
              tableData={[]}
              actionLinkPrefix=""
              handledelete={this.handleDelete}
            />
            {noDataMessage}
          </React.Fragment>
        )}      </div>
    )
  }
}

export default ProductList;


