import React, { Component } from "react";
// import "../../../style/admin.css";
import AnchorTag from "../Anchortag";
import Table from "../table/Table";
import axios from '../axiosConfig';
import { ScaleLoader } from "react-spinners";
import { getUser } from "../Auth";

class UserList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columnList: ["ID", "Full Name", "Email", "Role", "Status", "Action"],
      tableData: [],
      allowAction: false,
      loading: false,
      notification: false,
      notificationText: "",
      danger: false,
    };
  }
  componentDidMount() {
    let id;
    const user = getUser()
    if (this.props.match.params.businessId) {
      id = this.props.match.params.businessId;
    } else {
      id = user.businessId
    }
    const formData = {
      businessId: id,
    };
    console.log("bisiness", formData);
    this.setState({ loading: true })
    this.state.columnList = ["ID", "Full Name", "Email", "Role", "Status", "Action"],

      // Make a GET request to your API endpoint to fetch tableData
      axios.post("/user/getAllUserbybusiness", formData).then((response) => {
        // Update the state with the fetched tableData
        console.log(response.data)
        let filteredData
        if (user.role == 1) {
          filteredData = response.data.filter((item) => item.isManager === true);
        } else {
          filteredData = response.data.filter((item) => item.userId != user.userId);
        }
        const formattedData = filteredData.map((item) => ({
          "id": item.userId, // Assuming CategoryId is the ID you want
          "name": item.fullName,
          "date": item.email, // Format as needed
          "role": item.roleId, // Format as needed
          "status": item.status ? "Active" : "Inactive",

        }));
        this.setState({ loading: false })
        // Update the state with the formatted data
        if (formattedData !== null && formattedData.length > 0) {
          this.setState({ tableData: formattedData });
        } else {
          this.setState({ tableData: [] });
        }
      }).catch((error) => {
        this.setState({ loading: false })
        // Handle any errors that occur during the request
        console.error("Error fetching tableData:", error);
      });
  }
  handleDelete = async (id) => {
    console.log('delete called', id)
    this.setState({ loading: true })
    const confirmDelete = window.confirm("Are you sure you want to delete?");
    if (confirmDelete) {
      await axios.delete(`/user/delete/${id}`).then((response) => {
        console.log(`${id}deleted`)
        if (response.data.code === 400 || response.data.code === 401 || response.data.code === 402 || response.data.code === 500) {
          console.log("response")
          this.setState({
            danger: true,
            notificationText: response.data.message,
          });
          setTimeout(() => {
            this.setState({ notification: false, danger: false });
          }, 5000);
        } else if (response.data.code === 200) {
          console.log("Form data sent successfully:", response.data);
          this.setState({
            notification: true,
            danger: false,
            notificationText: response.data.message,
          });
          setTimeout(() => {
            this.setState({ notification: false, danger: false });
          }, 5000);
          window.location.reload();

        }
        // Handle any other status codes if needed
      })
        .catch((error) => {
          // Handle any errors that occur during the request
          this.setState({
            notificationText: "An error occurred while deleting User",
            danger: true,
          });
          console.error("Error sending form data:", error);
          setTimeout(() => {
            this.setState({ danger: false });
          }, 3000);
        }).finally(() => {
          // Set loading state to false after the request is complete
          this.setState({ loading: false });
        });
      this.setState({ loading: false }); // Set loading to true when deleting
    }
    this.setState({ loading: false }); // Set loading to true when deleting

  }
  render() {
    const user = getUser()
    let id = this.props.match.params.businessId;
    if (!id) {
      id = user.businessId
    }
    const table = (
      <React.Fragment>
        <Table
          className="table table-striped"
          columnList={this.state.columnList}
          tableData={this.state.tableData}
          actionLinkPrefix="user/"
          handledelete={this.handleDelete}
        ></Table>
      </React.Fragment>
    );

    const noDataMessage = (
      <p style={{ textAlign: "center", marginTop: 50 }}>
        <h4>No User Found </h4>
      </p>
    );
    return (
      <div className="admin-content mx-auto">
        {this.state.loading && (
          <div className="text-center">
            <ScaleLoader
              size={150}
              color={"#123abc"}
              loading={this.state.loading}
              rounded={true} // Set the spinner to be rounded
            />
          </div>
        )}
        {this.state.notification && (
          <div className="alert alert-success" role="alert">
            {this.state.notificationText}
          </div>
        )}
        {this.state.danger && (
          <div className="alert alert-danger" role="alert">
            {this.state.notificationText}
          </div>
        )}
        <div className="w-100 mb-5">
          {user.role == 2 && <AnchorTag link={`/app/office/profile/create-user/${id}`} className="btn btn-warning float-right" itemValue="Create User"></AnchorTag>
          }
          {(user.role == 3 || user.role == 1 || user.role == 5) && <AnchorTag link={`/app/store/profile/create-user/${id}`} className="btn btn-warning float-right" itemValue="Create User"></AnchorTag>}
          <h4>{user.role != 1 && user.businessName} Users</h4>
        </div>
        {this.state.tableData.length > 0 ? (
          table
        ) : (
          <React.Fragment>
            <Table
              className="table table-striped"
              columnList={this.state.columnList}
              tableData={[]}
              actionLinkPrefix="user/"
              handledelete={this.handleDelete}
            />
            {noDataMessage}
          </React.Fragment>
        )}
      </div>
    )
  }
}

export default UserList;


