import React, { Component } from "react";
import AnchorTag from "../../components/Anchortag";
import InputFormGroup from "../../components/input/InputFormGroup";
import SelectFormGroup from "../../components/input/SelectFormGroup";
import Table from "../../components/table/Table";
import axios from "axios";
import { ScaleLoader } from "react-spinners";


class EmployeeList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columnList: [],
      tableData: [],
      startDate: "",
      endDate: "",
      name: "",
      email: "",
      loading: false, // Add loading state
      notification: false,
      notificationText: "",
    };


  }
  handleInputChange = (event) => {
    const { name, value } = event.target;
    this.setState({
      [name]: value,
    });
  };

  handleSubmit = (event) => {
    event.preventDefault();
    const { startDate, endDate } = this.state;
    const formData = {
      startDate,
      endDate,
    };
    console.log(formData);
    this.setState({ loading: true })
    axios.post("/expense/getbydate", formData).then((response) => {
      // Update the state with the fetched tableData
      console.log(response)
      const formattedData = response.data.map((item) => ({
        "id": item.expenseId, // Assuming CategoryId is the ID you want
        "name": item.title,
        "email": item.type,
        "designation": item.amount,
      }));
      this.setState({ loading: false });

      // Update the state with the formatted data
      if (formattedData !== null && formattedData.length > 0) {
        this.setState({ tableData: formattedData });
      } else {
        this.setState({ tableData: [] });
      }
    }).catch((error) => {
      this.setState({ loading: false });
      // Handle any errors that occur during the request
      console.error("Error fetching tableData:", error);
    });
  }

  componentDidMount() {
    this.setState({ loading: true });
    this.setState({ columnList: ["ID", "Name", "Type", "Date", "Amount", "Action"] })

    // Make a GET request to your API endpoint to fetch tableData
    axios.get("/expense/get").then((response) => {
      // Update the state with the fetched tableData
      console.log(response)
      const formattedData = response.data.map((item) => ({
        "id": item.expenseId, // Assuming CategoryId is the ID you want
        "name": item.title,
        "type": item.type,
        "Date": item.date,
        "Amount": item.amount,
      }));
      this.setState({ loading: false });

      // Update the state with the formatted data
      if (formattedData !== null && formattedData.length > 0) {
        this.setState({ tableData: formattedData });
      } else {
        this.setState({ tableData: [] });
      }
    }).catch((error) => {
      this.setState({ loading: false });
      // Handle any errors that occur during the request
      console.error("Error fetching tableData:", error);
    });
  }
  handleDelete = async (id) => {
    console.log('delete called', id);
    this.setState({ loading: true }); // Set loading to true when deleting
    const confirmDelete = window.confirm("Are you sure you want to delete?");
    if (confirmDelete) {
      try {
        await axios.delete(`/expense/delete/${id}`)
          .then((response) => {
            if (response.status === 200) {
              // Handle a successful response from the server here
              console.log("Form data sent successfully:", response.data);
              this.setState({
                notification: true,
                notificationText: response.data.message

              })
            }
            setTimeout(() => {
              this.setState({ notification: false });
            }, 3000);
            console.log(`${id} deleted`);
            window.location.reload();
          })
          .catch((error) => {
            // Handle any errors that occur during the request
            console.error("Error deleting:", error);
          });
      } catch (error) {
        // Handle any errors that occur during the request
        console.error("Error deleting:", error);
      }

      this.setState({ loading: false }); // Set loading to false after deleting
    }
    this.setState({ loading: false }); // Set loading to true when deleting
  }

  render() {
    const table = (
      <React.Fragment>
        <Table
          className="table table-striped "
          style
          columnList={this.state.columnList}
          tableData={this.state.tableData}
          actionLinkPrefix=""
          handledelete={this.handleDelete}
        ></Table>
      </React.Fragment>
    );

    const noDataMessage = (
      <p style={{ textAlign: "center", marginTop: 50 }}>
        <h4>No Expense Found </h4>
      </p>
    );
    return (
      <div className="admin-content mx-auto">
        {this.state.loading && (
          <div className="text-center">
            <ScaleLoader
              size={150}
              color={"#123abc"}
              loading={this.state.loading}
              rounded={true} // Set the spinner to be rounded
            />
          </div>
        )}
        {this.state.notification && (
          <div className="alert alert-success" role="alert">
            {this.state.notificationText}
          </div>
        )}
        <div className="w-100 mb-5">
          <AnchorTag link="/app/office/office/expense/create" className="btn btn-sm btn-warning float-right" itemValue="Create Expense"></AnchorTag>
          <h4>Expense List</h4>
        </div>
        <form onSubmit={this.handleSubmit}>
          <div className="row mb-5">
            <div className="col-12">
              <p><b>Search Box</b></p>
            </div>
            <div className="col-4">
              <div className="form-group">
                <label>Start Date</label>
                <input type="date" className="form-control" name="startDate" placeholder="" value={this.state.startDate} onChange={this.handleInputChange} />
              </div>
            </div>
            <div className="col-4">
              <div className="form-group">
                <label>End Date</label>
                <input type="date" className="form-control" name="endDate" placeholder="" value={this.state.endDate} onChange={this.handleInputChange} />
              </div>
            </div>
            <div className="col-2">
              <div className="form-group">
                <label> </label>
                <input type="submit" style={{ marginTop: 31 }} className="w-100 btn  btn-success" value="Search" />
              </div>
            </div>
          </div>
        </form>

        <button className="btn  btn-secondary float-right" style={{ marginBottom: 20 }} onClick={() => { window.print() }}>Print</button>
        {this.state.tableData.length > 0 ? (
          table
        ) : (
          <React.Fragment>
            <Table
              className="table table-striped"
              columnList={this.state.columnList}
              tableData={[]}
              actionLinkPrefix=""
              handledelete={this.handleDelete}
            />
            {noDataMessage}
          </React.Fragment>
        )}
      </div>
    )
  }
}

export default EmployeeList;


