import React, { Component } from "react";
import AnchorTag from "../../components/Anchortag";
import InputFormGroup from "../../components/input/InputFormGroup";
import SelectFormGroup from "../../components/input/SelectFormGroup";
import Table from "../../components/table/Table";
import axios from "axios";
import { ScaleLoader } from "react-spinners";



class EmployeeList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columnList: [],
      tableData: [],
      name: "",
      email: "",
      loading: false, // Add loading state
      notification: false,
      notificationText: "",
    };


  }
  handleInputChange = (event) => {
    const { name, value } = event.target;
    this.setState({
      [name]: value,
    });
  };

  handleSubmit = (event) => {
    event.preventDefault();
    const { name, email } = this.state;
    const formData = {
      name,
      email,
    };
    console.log(JSON.stringify(formData));
  };
  componentDidMount() {
    this.setState({ loading: true }); // Set loading to true when submitting
    this.setState({ columnList: ["ID", "Name", "Email", "Designation", "Joining Date", "Action"] })

    // Make a GET request to your API endpoint to fetch tableData
    axios.get("/employee/get").then((response) => {
      // Update the state with the fetched tableData
      console.log(response)
      const formattedData = response.data.map((item) => {
        const date = new Date(item.joiningDate);
        const options = { year: 'numeric', month: 'short', day: 'numeric' };
        const formattedDate = date.toLocaleDateString('en-US', options);
        return {
          "id": item.employeId, // Assuming CategoryId is the ID you want
          "name": item.firstName + " " + item.lastName,
          "email": item.email,
          "designation": item.designation,
          "date": formattedDate // Format as "22 Nov, 2023"
        };
      });
      this.setState({ loading: false });


      // Update the state with the formatted data
      if (formattedData !== null && formattedData.length > 0) {
        this.setState({ tableData: formattedData });
      } else {
        this.setState({ tableData: [] });
      }
    }).catch((error) => {
      this.setState({ loading: false });
      // Handle any errors that occur during the request
      console.error("Error fetching tableData:", error);
    });
  }
  handleDelete = async (id) => {
    console.log('delete called', id)
    this.setState({ loading: true }); // Set loading to true when deleting
    const confirmDelete = window.confirm("Are you sure you want to delete?");
    if (confirmDelete) {
      await axios.delete(`/employee/delete/${id}`)
        .then((response) => {
          if (response.status === 200) {
            // Handle a successful response from the server here
            console.log("Form data sent successfully:", response.data);
            this.setState({
              notification: true,
              notificationText: response.data.message

            })
          }
          setTimeout(() => {
            this.setState({ notification: false });
          }, 3000);
          window.location.reload();
        }).catch((error) => {
          // Handle any errors that occur during the request
          console.error("Error fetching tableData:", error);
        })
      this.setState({ loading: false }); // Set loading to true when deleting
    }
    this.setState({ loading: false }); // Set loading to true when deleting
  }
  render() {
    const table = (
      <React.Fragment>
        <Table
          className="table table-striped"
          columnList={this.state.columnList}
          tableData={this.state.tableData}
          actionLinkPrefix=""
          handledelete={this.handleDelete}
        ></Table>
      </React.Fragment>
    );

    const noDataMessage = (
      <p style={{ textAlign: "center", marginTop: 50 }}>
        <h4>No Employee Found </h4>
      </p>
    );
    return (
      <div className="admin-content mx-auto">
        {this.state.loading && (
          <div className="text-center">
            <ScaleLoader
              size={150}
              color={"#123abc"}
              loading={this.state.loading}
              rounded={true} // Set the spinner to be rounded
            />
          </div>
        )}
        {this.state.notification && (
          <div className="alert alert-success" role="alert">
            {this.state.notificationText}
          </div>
        )}
        <div className="w-100 mb-5">
          <AnchorTag link="/app/office/office/employee/create" className="btn btn-sm btn-warning float-right" itemValue="Create Employee"></AnchorTag>
          <h4>Employee List</h4>
        </div>
        {/* <form onSubmit={this.handleSubmit}>
                <div className="row mb-5">
                    <div className="col-12">
                        <p><b>Search Box</b></p>
                    </div>
                    <div className="col-4">
                        <div className="form-group">
                            <input type="text" className="form-control" name="name" placeholder="Name" value={this.state.name} onChange={this.handleInputChange}/>
                        </div>
                    </div>
                    <div className="col-4">
                        <div className="form-group">
                            <input type="text" className="form-control" name="email" placeholder="Email" value={this.state.email} onChange={this.handleInputChange}/>
                        </div>
                    </div>
                    <div className="col-2">
                        <div className="form-group">
                            <input type="submit" className="w-100 btn  btn-success" value="Search" />
                        </div>
                    </div>
                </div>
                </form> */}
        <button className="btn  btn-secondary float-right" style={{ marginBottom: 20 }} onClick={() => { window.print() }}>Print</button>
        {this.state.tableData.length > 0 ? (
          table
        ) : (
          <React.Fragment>
            <Table
              className="table table-striped"
              columnList={this.state.columnList}
              tableData={[]}
              actionLinkPrefix=""
              handledelete={this.handleDelete}
            />
            {noDataMessage}
          </React.Fragment>
        )}
      </div>
    )
  }
}

export default EmployeeList;


