import React, { Component } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import { useLocation } from 'react-router-dom';
import { getUser } from '../Auth'
import { ScaleLoader } from "react-spinners";

class PasswordResetForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentPassword: "",
      newPassword: "",
      confirmPassword: "",
      danger: false,
      notificationText: "",
      loading: false,
    };

  }
  // resetFormFields = () => {
  //   this.setState({
  //     currentPassword: "",
  //     newPassword: "",
  //     confirmPassword: "",
  //     // Add other fields and their initial values if needed
  //   });
  // };

  handleInputChange = (event) => {
    const { name, value } = event.target;
    this.setState({
      [name]: value,
    });
  };

  handleSubmit = (event) => {
    this.setState({ danger: false })
    event.preventDefault();
    const {
      newPassword,
      currentPassword,
      confirmPassword
    } = this.state


    const formData = {
      newPassword,
      currentPassword,
      confirmPassword
    };
    this.setState({ loading: true })
    console.log(JSON.stringify(formData));
    const user = getUser()
    const id = user.userId
    const userRole = user.role
    console.log(id)
    // Make a POST request to your backend API with Axios
    axios.put(`/user/changepassword/${id}`, formData)
      .then((response) => {
        // Handle a successful response from the server here
        if (response.data.code === 200) {
          console.log("Form data sent successfully:", response.data);
          // this.resetFormFields()
          if (userRole === 2) {
            window.location.href = "/app/office/office/dashboard";
          } else {
            window.location.href = "/app/store/store/dashboard";
          }
        } else if (response.data.code === 401 || response.data.code === 402 || response.data.code === 404) {
          console.log("respone", response.data.message)
          this.setState({
            danger: true,
            notificationText: response.data.message,
          });
        }
        // Handle any other status codes if needed
      })
      .catch((error) => {
        // Handle any errors that occur during the request
        this.setState({
          notificationText: "An error occurred while Changing Password",
          danger: true,
        });
        console.error("Error sending form data:", error);
      }).finally(() => {
        // Set loading state to false after the request is complete
        this.setState({ loading: false });
      });
  }
  render() {
    return (
      <div>
        <header >
          <nav className="navbar navbar-expand-lg navbar-dark bg-dark" >
            <a className="navbar-brand" href="#">Inventory Managment System</a>
            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
              <span className="navbar-toggler-icon"></span>
            </button>
            <div className="collapse navbar-collapse" id="navbarNav">
              <ul className="navbar-nav ml-auto">
                <li className="nav-item ">
                  <a className="nav-link" href="/">Home</a>
                </li>
                <li className="nav-item">
                  <a className="nav-link" href="/pricing">Pricing</a>
                </li>
                <li className="nav-item">
                  <a className="nav-link active" href="/login">Login</a>
                </li>
                <li className="nav-item">
                  <a className="nav-link" href="/contact">Contact</a>
                </li>
              </ul>
            </div>
          </nav>
        </header>

        <div className="auth mx-auto">
          {this.state.loading && (
            <div className="loader">
              <ScaleLoader
                size={1500}
                color={"#123abc"}
                loading={this.state.loading}
                rounded={true} // Set the spinner to be rounded
              />
            </div>
          )}
          {this.state.danger && (
            <div className="mt-5 alert alert-danger" role="alert">
              {this.state.notificationText}
            </div>
          )}
          <form className="">
            <div>
              <h2 className="text-center pb-3" style={{ color: "green" }}>New User</h2>
              <h4 className="text-center pb-3">Welcome to Leed Inventory Management System</h4>
              <h5 className="text-center pb-3">Reset Your Password To Continue</h5>

            </div>
            <div className="form-group">
              <label>Current Password<span style={{ color: "red" }}>* </span></label>
              <input type="password" className="form-control" name="currentPassword" required value={this.state.currentPassword}
                onChange={this.handleInputChange}
              />
            </div>
            <div className="form-group">
              <label>New Password<span style={{ color: "red" }}>* </span></label>
              <input type="password" className="form-control" name="newPassword" required value={this.state.newPassword}
                onChange={this.handleInputChange}
              />
            </div>
            <div className="form-group">
              <label>Confirm Password<span style={{ color: "red" }}>* </span></label>
              <input type="password" className="form-control" id="username" name="confirmPassword" required value={this.state.confirmPassword}
                onChange={this.handleInputChange}
              />                    </div>
            <div className="form-group">
              <button className="btn btn-warning w-100 mt-2" onClick={this.handleSubmit}>Reset Password</button>
            </div>
          </form>
        </div>
      </div>
    )
  }
}

export default PasswordResetForm;


