import React, { Component } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import { ScaleLoader } from "react-spinners";

class ShipmentView extends Component {
  constructor(props) {
    super(props);
    this.productDetails =
      "PERFECT GIFT IDEA: Works on wet, tion and is not a product defect.";
    this.state = {
      title: "",
      amount: 0,
      cargoCharges: 0,
      date: "",
      status: "",
      items: [],
      loading: false,
      notification: false,
      notificationText: "",
      danger: false,
    };

  }
  componentDidMount() {
    const id = this.props.match.params.deliveryId;
    (async () => {
      this.setState({ loading: true })
      try {
        const response = await axios.get(`/delivery/get/${id}`)
        this.setState({
          title: response.data.delivery.title,
          amount: response.data.delivery.totalItemsAmount,
          date: response.data.delivery.date,
          cargoCharges: response.data.delivery.cargoCharges,
          status: response.data.delivery.status,
          items: response.data.transformedDeliveryItems
        });
        this.setState({ loading: false })
      }
      catch (error) {
        console.error("Error fetching product details:");
      };
    })();
  }
  handleDisptchButton = (event) => {
    console.log("heyyy")
    const id = this.props.match.params.deliveryId;
    (async () => {
      this.setState({ loading: true })
      try {
        const response = await axios.get(`/delivery/dispatch/${id}`)
        if (response.status == 200) {
          console.log(response)
          this.setState({ loading: false, notification: true, notificationText: response.data.message })
        }
        else {
          console.log("error", response)
        }
        setTimeout(() => {
          this.setState({ notification: false, danger: false });
        }, 3000);
      }
      catch (error) {
        console.error("Error fetching product details:");
      };
    })();
  };
  render() {
    return (
      <div className="admin-content mx-auto">
        {this.state.loading && (
          <div className="text-center">
            <ScaleLoader
              size={150}
              color={"#123abc"}
              loading={this.state.loading}
            // rounded={true} // Set the spinner to be rounded
            />
          </div>
        )}
        {this.state.notification && (
          <div className="alert alert-success" role="alert">
            {this.state.notificationText}
          </div>
        )}
        <div className="w-100 mb-5">
          <h4>Delivery Details</h4>
          <button
            className="btn  btn-secondary float-right"
            style={{ marginBottom: 20 }}
            onClick={() => {
              window.print();
            }}
          >
            Print
          </button>
        </div>
        <div className="w-75">
          <form onSubmit={this.handleSubmit}>
            <div className="container-fluid">
              <div className="row">
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Title</label>
                    <input
                      type="text"
                      className="form-control"
                      name="title"
                      value={this.state.title}
                      readOnly
                    />
                  </div>
                </div>

                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Amount</label>
                    <input
                      type="text"
                      className="form-control"
                      name="amount"
                      row="10"
                      value={this.state.amount}
                      readOnly
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Date</label>
                    <input
                      type="text"
                      className="form-control"
                      name="date"
                      value={this.state.date}
                      readOnly
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Cargo Charges</label>
                    <input
                      className="form-control"
                      name="cargoCharges"
                      value={this.state.cargoCharges}
                      onChange={this.handleInputChange}
                      readOnly
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Status</label>
                    <input
                      type="text"
                      className="form-control"
                      name="status"
                      value={this.state.status}
                      readOnly
                    />
                  </div>
                </div>
              </div>

              {/* <div className="items-list">
                <h5 style={{ marginLeft: 130 }}>
                  Items of Delivery {this.state.title}
                </h5>
                <table
                  style={{
                    width: "100%",
                    borderCollapse: "collapse",
                    border: "1px solid #ddd",
                    marginLeft: 0,
                  }}
                >
                  <thead>
                    <tr>
                      <th style={tableHeaderStyle}>Customer</th>
                      <th style={tableHeaderStyle}>Name</th>
                      <th style={tableHeaderStyle}>Quantity</th>
                      <th style={tableHeaderStyle}>TotalPrice</th>
                    </tr>
                  </thead>
                  <tbody>
                    {this.state.items.map((item) => (
                      <tr
                        key={item.id}
                        style={tableRowStyle}
                        onClick={() => {
                          // Navigate to "View Item" page
                          this.props.history.push(
                            `/app/store/product-category/view/view/${item.id}`
                          );
                        }}
                      >
                        <td style={tableCellStyle}>{item.customer}</td>
                        <td style={tableCellStyle}>{item.name}</td>
                        <td style={tableCellStyle}>{item.quantity}</td>
                        <td style={tableCellStyle}>{item.totalPrice}</td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div> */}
              {/* <div className="items-list">
                {Array.from(
                  new Set(this.state.items.map((item) => item.customer))
                ).map((customerName) => (
                  <div key={customerName} style={{ marginBottom: 20 }}>
                    <h5 style={{ marginLeft: 130 }}>
                      Items of Delivery for {customerName}
                    </h5>
                    <table
                      style={{
                        width: "100%",
                        borderCollapse: "collapse",
                        border: "1px solid #ddd",
                        marginLeft: 0,
                      }}
                    >
                      <thead>
                        <tr>
                          <th style={tableHeaderStyle}>Name</th>
                          <th style={tableHeaderStyle}>Quantity</th>
                          <th style={tableHeaderStyle}>TotalPrice</th>
                        </tr>
                      </thead>
                      <tbody>
                        {this.state.items
                          .filter((item) => item.customer === customerName)
                          .map((item) => (
                            <tr
                              key={item.id}
                              style={tableRowStyle}
                              onClick={() => {
                                // Navigate to "View Item" page
                                this.props.history.push(
                                  `/app/store/product-category/view/view/${item.id}`
                                );
                              }}
                            >
                              <td style={tableCellStyle}>{item.name}</td>
                              <td style={tableCellStyle}>{item.quantity}</td>
                              <td style={tableCellStyle}>{item.totalPrice}</td>
                            </tr>
                          ))}
                      </tbody>
                    </table>
                  </div>
                ))}
              </div> */}
              <div className="items-list">
                {Array.from(new Set(this.state.items.map(item => item.customerId))).map(customerId => (

                  <div key={customerId} style={{ marginBottom: 20 }}>
                    <h5 style={{ marginLeft: 80 }}>
                      Items of Delivery for {this.state.items.find(item => item.customerId === customerId).customerName}
                    </h5>
                    <table
                      style={{
                        width: "100%",
                        borderCollapse: "collapse",
                        border: "1px solid #ddd",
                        marginLeft: 0,
                        backgroundColor: "#f5f5f5"
                      }}
                    >
                      <thead>
                        <tr>
                          <th style={tableHeaderStyle}>ItemId</th>
                          <th style={tableHeaderStyle}>Quantity</th>
                          <th style={tableHeaderStyle}>Price Per Unit</th>
                          <th style={tableHeaderStyle}>Total Amount</th>

                        </tr>
                      </thead>
                      <tbody>
                        {this.state.items
                          .filter(item => item.customerId === customerId)
                          .map(item => (
                            <tr
                              key={item.id}
                              style={tableRowStyle}
                              onClick={() => {
                                // Navigate to "View Item" page
                                this.props.history.push(
                                  `/app/store/store/product-category/view/view/${item.itemId}`
                                );
                              }}
                            >
                              <td style={tableCellStyle}>{item.itemId}</td>
                              <td style={tableCellStyle}>{item.quantity}</td>
                              <td style={tableCellStyle}>{item.pricePerUnit}</td>
                              <td style={tableCellStyle}>{item.pricePerUnit * item.quantity}</td>

                            </tr>
                          ))}
                        <tr>
                          <td colSpan="3" style={{ textAlign: "right" }}>
                            <strong>Amount Due:</strong>
                          </td>
                          <td style={{ fontWeight: "bold", }}>
                            {this.state.items
                              .filter(item => item.customerId === customerId)
                              .reduce((total, item) => parseInt(total) + parseInt(item.pricePerUnit * item.quantity), 0)}
                          </td>
                        </tr>
                      </tbody>
                    </table>
                  </div>
                ))}
              </div>


              <div className="col-12 mt-3">
                <div className="form-group">
                <Link to="/app/store/store/delivery/list" className="btn btn-warning back-link">
                    Back
                  </Link>
                  {this.state.status == "Pending" && (
                    <button
                      className="btn btn-success ml-3"
                      onClick={this.handleDisptchButton}
                    >
                      Dispatch
                    </button>
                  )}
                </div>
              </div>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

export default ShipmentView;
// Inline CSS styles
const tableHeaderStyle = {
  backgroundColor: "#c2c5c0",
  padding: "10px",
  border: "1px solid #ddd",
  fontWeight: "bold",
};

const tableRowStyle = {
  cursor: "pointer",
  transition: "background-color 0.3s",
};

const tableCellStyle = {
  padding: "10px",
  border: "1px solid #ddd",
};



