import React, { Component } from "react";
import PageHeader from "../../components/PageHeader";
import axios from "axios";
import { ScaleLoader } from "react-spinners";
import { Card, CardBody, CardTitle } from 'reactstrap'; // Assuming you're using Reactstrap for Bootstrap components

class DashboardPage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            totalItems: "",
            totalEmployees: "",
            totalExpense: "",
            loading: true,

        };
        this.userRole = "owner";

    }
    componentDidMount() {
        // Make your API request here
        axios.get("/officedashboard/get").then((response) => {
            console.log(response.data)
            this.setState({
                totalItems: response.data.totalItems,
                totalEmployees: response.data.totalEmployees,
                totalExpense: response.data.totalExpense,
                loading: false,
            });
        });
    }


    render() {
        return (
            <div className="admin-content mx-auto">
                {this.state.loading && (
                    <div className="text-center">
                        <ScaleLoader
                            size={150}
                            color={"#123abc"}
                            loading={this.state.loading}
                            rounded={true} // Set the spinner to be rounded
                        />
                    </div>
                )}
                <PageHeader headerText="Welcome to Dashboard" />
                <div className="w-75">
                    <div className="container-fluid">
                        <div className="row">

                            <div className="col-6">
                                <Card className="dash-summary-card cards">
                                    <CardBody>
                                    <p><b>Total Items In Inventory</b></p>
                                    <p>{this.state.totalItems}</p>
                                    </CardBody>
                                </Card>
                               
                            </div>
                            <div className="col-6">
                                <Card className="dash-summary-card cardss">
                                    <CardBody>
                                        <p><b>Total Employees</b></p>
                                        <p>{this.state.totalEmployees}</p>
                                    </CardBody>
                                </Card>
                            </div>
                            <div className="col-6">
                                <Card className="dash-summary-card cards">
                                    <CardBody>
                                        <p><b>Total Expenses</b></p>
                                        <p>{this.state.totalExpense}</p>
                                    </CardBody>
                                </Card>
                            </div>
                        </div>
                    </div>
                </div>

            </div>
        )
    }
}

export default DashboardPage;


