import React, {Component} from "react";
import AnchorTag from "../../Anchortag";

import Table from "../../table/Table";


class ReportList extends Component{
    constructor(props){
        super(props);
        this.columnList = ["ID", "Title", "No of Items", "Amount", "Date","Status", "Action"];
        this.tableData = [
            {"id": 1,"Title":"Delievred to Karachi" , "Items": "10", "Amount": "1000", "Date": "20th August, 2021","Type":"Dispached"},
            {"id": 2,"Title":"Delievred to Karachi" , "Items": "10", "Amount": "1000", "Date": "20th August, 2021","Type":"Pending"},
            {"id": 3,"Title":"Delievred to Karachi" , "Items": "10", "Amount": "2000", "Date": "20th August, 2021","Type":"Delivered"},
            {"id": 4,"Title":"Delievred to Karachi" , "Items": "10", "Amount": "30000", "Date": "20th August, 2021","Type":"Pending"},
            {"id": 5,"Title":"Delievred to Karachi" , "Items": "10", "Amount": "40000", "Date": "20th August, 2021","Type":"Pending"},



        ]
       
          this.state = {
            name: "",
            email: "",
            showList:false
          };

       
    }
    handleInputChange = (event) => {
        const { name, value } = event.target;
        this.setState({
          [name]: value,
        });
      };
    
      handleSubmit = (event) => {
        event.preventDefault();
        const { name, email } = this.state;
        const formData = {
          name,
          email,
        };
        this.setState({
            showList: true,
          });
        console.log(JSON.stringify(formData));
      };


    render(){
        return (
            <div className="admin-content mx-auto">
                <div className="w-100 mb-5">
                    <h4>Report</h4>
                </div>
                <form onSubmit={this.handleSubmit}>
                <div className="row mb-5">
                    <div className="col-12">
                        <p><b>Search Box</b></p>
                    </div>
                    <div className="col-6">
                        <div className="form-group">
                            <input type="text" className="form-control" name="name" placeholder="Report Type" value={this.state.name} onChange={this.handleInputChange}/>
                        </div>
                    </div>
                    <div className="col-6">
                        <div className="form-group">
                            <input type="text" className="form-control" name="name" placeholder="Period" value={this.state.name} onChange={this.handleInputChange}/>
                        </div>
                    </div>
                    <div className="col-6">
                        <div className="form-group">
                            <input type="text" className="form-control" name="name" placeholder="Status" value={this.state.name} onChange={this.handleInputChange}/>
                        </div>
                    </div> <div className="col-6">
                        <div className="form-group">
                            <input type="text" className="form-control" name="name" placeholder="Customer" value={this.state.name} onChange={this.handleInputChange}/>
                        </div>
                    </div>
                    <div className="col-6">
                        <div className="form-group">
                            <input type="text" className="form-control" name="email" placeholder="Vandor" value={this.state.email} onChange={this.handleInputChange}/>
                        </div>
                    </div>
                    <div className="col-6">
                        <div className="form-group">
                            <input type="text" className="form-control" name="email" placeholder="Items" value={this.state.email} onChange={this.handleInputChange}/>
                        </div>
                    </div>
                    <div className="col-3">
                        <div className="form-group">
                            <input type="submit" className="w-100 btn  btn-success" value="Generate Report" />
                        </div>
                    </div>
                </div>
                </form>
                <button className="btn  btn-secondary float-right" style={{marginBottom:20}} onClick={() => {window.print()}}>Print</button>
                {this.state.showList&&
                <Table className="table table-striped" columnList={this.columnList} tableData={this.tableData} actionLinkPrefix=""></Table>
           }
            </div>
        ) 
    }
}

export default ReportList;


