import React, {Component} from "react";
import { Route, Switch } from "react-router-dom";
import "../../../style/admin.css";
import HeaderNavBar from "../../../components/navigation/HeaderNavbar";
import AnchorTag from "../../../components/Anchortag";
import StorePages from "./StorePages";
import ProfileBasePage from "../profile/ProfileBasePage";
import {getUser} from '../Auth';

class CustomerRootPage extends Component{
    constructor(props){
        super(props);
        const user = getUser();

        this.userRole = user.role;
    }
    render(){
        return (
                <div>
                    <HeaderNavBar></HeaderNavBar>
                    <div className="mt-4">
                        <div className="">
                            <div className="row">
                                <div className="col-2">
                                    {this.userRole!==1 &&<div className="list-group side-navigation">
                                        <AnchorTag link={`${this.props.match.path}/store/dashboard`} liClassName="" className="list-group-item list-group-item-action links" itemValue=" Dashboard "></AnchorTag>
                                        <AnchorTag link={`${this.props.match.path}/store/product-category/list`} liClassName="" className="list-group-item list-group-item-action links" itemValue="Inventory"></AnchorTag>
                                        <AnchorTag link={`${this.props.match.path}/store/shipment/list`} liClassName="" className="list-group-item list-group-item-action links" itemValue="Shipment"></AnchorTag>
                                        <AnchorTag link={`${this.props.match.path}/store/delivery/list`} liClassName="" className="list-group-item list-group-item-action links" itemValue="Delivery"></AnchorTag>
                                        <AnchorTag link={`${this.props.match.path}/store/deliveryreturn/list`} liClassName="" className="list-group-item list-group-item-action links" itemValue="Delivery Return"></AnchorTag>
                                        <AnchorTag link={`${this.props.match.path}/store/vendor/list`} liClassName="" className="list-group-item list-group-item-action links" itemValue="Vendor"></AnchorTag>
                                        <AnchorTag link={`${this.props.match.path}/store/customer/list`} liClassName="" className="list-group-item list-group-item-action links" itemValue="Customer"></AnchorTag>
                                        {/* <AnchorTag link={`${this.props.match.path}/store/report/list`} liClassName="" className="list-group-item list-group-item-action links" itemValue="Report"></AnchorTag> */}
                                    </div>
    }
                                </div>
                                <div className="col-10">
                                    <Switch>
                                        <Route path={`${this.props.match.path}/store`} component={StorePages}/>
                                        <Route path={`${this.props.match.path}/profile`} component={ProfileBasePage}/>
                                    </Switch>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
        ) 
    }
}

export default CustomerRootPage;


