import React, {Component} from "react";
import AnchorTag from "../../components/Anchortag";


class ExpenseCategoryForm extends Component{
    constructor(props){
        super(props);
        this.state = {
            categoryName: "",
            date: "",
          };
    }
    handleInputChange = (event) => {
        const { name, value } = event.target;
        this.setState({
          [name]: value,
        });
      };
    
      handleSubmit = (event) => {
        event.preventDefault();
        const { categoryName, date } = this.state;
        const formData = {
          categoryName,
          date,
        };
        console.log(JSON.stringify(formData));
      };
    render(){
        return (
            <div className="admin-content mx-auto">
                <div className="w-100 mb-5">
                    <AnchorTag link="/app/office/office/expense-category/list" className="btn btn-primary float-right" itemValue="Back to Category List"></AnchorTag>
                    <h4>Create Expense Category</h4>
                </div>
                <div className="w-75">
                    <form onSubmit={this.handleSubmit}>
                        <div className="container-fluid">
                            <div className="row">
                                <div className="col-6">
                                    <div className="form-group">
                                        <label className="mb-2">Category Name</label>
                                        <input type="text" className="form-control" name="categoryName" value={this.state.categoryName} onChange={this.handleInputChange}/>
                                    </div>
                                </div>
                                <div className="col-6">
                                    <div className="form-group">
                                        <label className="mb-2">Date</label>
                                        <input type="date" className="form-control" name="date" value={this.state.date} onChange={this.handleInputChange}/>
                                    </div>
                                </div>

                                <div className="col-12 mt-3">
                                    <div className="form-group">
                                        <AnchorTag className="btn btn-warning" itemValue="Back" link="/app/office/expense-category/list"/>
                                        <input type="submit" className="btn btn-success ml-3" value="Submit"/>
                                    </div>
                                </div>
                            </div>
                        </div>
                        
                    </form>
                </div>
                
            </div>
        ) 
    }
}

export default ExpenseCategoryForm;


