import React, { Component } from "react";
import axios from "axios";
import { ScaleLoader } from "react-spinners";
import { Link } from "react-router-dom";

class ProductForm extends Component {

  constructor(props) {
    super(props);
    this.state = {
      name: "",
      description: "",
      color: "",
      price: 0,
      seller: "",
      itemRefId: "",
      officeCategoryId: this.props.match.params.categoryId,
      employeId: null,
      employeeOptions: [],
      notification: false,
      danger: false,
      notificationText: "",
      loading: false,
    };

  }
  resetFormFields =async () => {

    // Make an API request to fetch the item options
    this.setState({
      name: "",
      description: "",
      color: "",
      price: 0,
      seller: "",
      itemRefId: "",
      employeId: null,
      employeeOptions:[],
      // Add other fields and their initial values if needed
    });
    try {
      const response = await axios.get("/employee/get");
      console.log("employe",response.data)
      this.setState({ employeeOptions: response.data });
    } catch (error) {
      console.error("Error fetching item options:", error);
    }
  }

  handleInputChange = (event) => {
    const { name, value } = event.target;
    this.setState({
      [name]: value,
    });
  };

  handleSubmit = (event) => {
    event.preventDefault();
    const {
      name,
      description,
      color,
      price,
      seller,
      itemRefId,
      officeCategoryId,
      employeId,

    } = this.state;
    const formData = {
      name,
      description,
      color,
      price,
      seller,
      itemRefId,
      officeCategoryId:this.props.match.params.categoryId,
      employeId,
    };
    console.log("officeCategoryId", officeCategoryId)
    this.setState({ loading: true });
    console.log(formData);

    // Make a POST request to your backend API with Axios
    axios.post("/officeItem/create", formData)
      .then((response) => {
        if (response.status === 201) {
          // Handle a successful response from the server here
          console.log("Form data sent successfully:", response.data);
          this.setState({
            notification: true,
            notificationText: response.data.message

          })
          this.resetFormFields()
        }
        setTimeout(() => {
          this.setState({ notification: false, });
        }, 3000);
      })
      .catch((error) => {
        // Handle any errors that occur during the request
        this.setState({ notificationText: "An error occured while creating officeItem", danger: true, })
        setTimeout(() => {
          this.setState({ danger: false });
        }, 3000);
        console.error("Error sending form data:", error);
      }).finally(() => {
        // Set loading to false after the request is complete
        this.setState({ loading: false });
      });
  };
  async componentDidMount() {
    // Make an API request to fetch the item options
    try {
      const response = await axios.get("/employee/get");
      console.log("employe",response.data)
      this.setState({ employeeOptions: response.data });
    } catch (error) {
      console.error("Error fetching item options:", error);
    }
  }
  render() {
    return (
      <div className="admin-content mx-auto">
        {this.state.loading && (
          <div className="text-center">
            <ScaleLoader
              size={150}
              color={"#123abc"}
              loading={this.state.loading}
              // rounded={true} // Set the spinner to be rounded
            />
          </div>
        )}
        {this.state.notification && (
          <div className="alert alert-success" role="alert">
            {this.state.notificationText}
          </div>
        )}
        {this.state.danger && (
          <div className="alert alert-danger" role="alert">
            {this.state.notificationText}
          </div>
        )}
        <div className="w-100 mb-5">
          <button
            className="btn btn-primary float-right"
            onClick={() => {
              window.history.back();
            }}
          >
            Back to Items List
          </button>

          <h4>Create Item</h4>
        </div>
        <div className="w-75">
          <form onSubmit={this.handleSubmit}>
            <div className="container-fluid">
              <div className="row">
                <div className="col-12">
                  <div className="form-group">
                    <label className="mb-2">Title</label>
                    <input
                      type="text"
                      className="form-control"
                      name="name"
                      required
                      value={this.state.name}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>
                <div className="col-12">
                  <div className="form-group">
                    <label className="mb-2">Description</label>
                    <textarea
                      type="text"
                      className="form-control"
                      name="description"
                      row="10"
                      value={this.state.description}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>

                <div className="col-4">
                  <div className="form-group">
                    <label className="mb-2">Color</label>
                    <input
                      type="text"
                      className="form-control"
                      name="color"
                      value={this.state.color}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>
                <div className="col-8">
                  <div className="form-group">
                    <label className="mb-2">Seller</label>
                    <input
                      type="text"
                      className="form-control"
                      name="seller"
                      value={this.state.seller}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>
                <div className="col-4">
                  <div className="form-group">
                    <label className="mb-2">Price</label>
                    <input
                      type="number"
                      className="form-control"
                      name="price"
                      value={this.state.price}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>
                <div className="col-4">
                  <div className="form-group">
                    <label className="mb-2">Item Reference Id</label>
                    <input
                      type="text"
                      className="form-control"
                      name="itemRefId"
                      value={this.state.itemRefId}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Assign Item To Employee</label>
                    <select
                      className="form-control"
                      name="employeId"
                      value={this.state.employeId}
                      onChange={this.handleInputChange}
                    >
                      <option value={null}>Select Employee</option>
                      {this.state.employeeOptions.map((option) => (
                        <option key={option.value} value={option.employeId}>
                          {option.employeId}-{option.firstName} {option.lastName}
                        </option>
                      ))}
                    </select>
                  </div>
                </div>
              </div>

              <div className="col-12 mt-3">
                <div className="form-group">
                  <Link to={`/app/office/office/product-category/view/${this.state.officeCategoryId}`} className="btn btn-warning back-link">
                    Back
                  </Link>
                  <button type="submit" className="btn btn-success ml-3" value="Submit">Submit</button>
                </div>
              </div>

            </div>
          </form>
        </div>
      </div>
    );
  }
}

export default ProductForm;



