import React, { Component } from "react";
import axios from "axios";
import { ScaleLoader } from "react-spinners";
import { Link } from "react-router-dom";

class ProductView extends Component {
  constructor(props) {
    super(props);
    this.productDetails = "PERFECT GIFT IDEA: Works on wet, dry, Long, short, thick, curly, and straight hair. Perfect gift for Valentines Day, Mother's Day, Thanksgiving, Christmas, Anniversary and Birthday to your girlfriend, wife, mom, sister and friends. NOTE: Paddle brush is designed to have one missing pin on the bottom of the cushion. This is to help with air circulation and is not a product defect."
    this.handlePrint = () => {
      window.print();

    };
    this.state = {
      title: "",
      unit: "",
      stock: 0,
      description: "",
      categoryId: "",
      delivered: 0,
      lowStockQuantity: "",
      shipments: [],
      deliveries: [],
      loading: false,
    };
  }

  componentDidMount() {
    const id = this.props.match.params.itemId;
    (async () => {
      this.setState({ loading: true })
      try {
        const response = await axios.get(`/item/get/${id}`)
        console.log(response)
        this.setState({
          title: response.data.title,
          unit: response.data.unit,
          stock: response.data.stock,
          delivered: response.data.delivered,
          categoryId: response.data.categoryId,
          description: response.data.description,
          lowStockQuantity: response.data.lowStockQuantity,
          shipments: response.data.shipmentItems,
          deliveries: response.data.deliveryItems,
        });
        this.setState({ loading: false })
      }
      catch (error) {
        console.error("Error fetching product details:");
      };
    })();
  }

  render() {
    const id = this.props.match.params.itemId;

    return (
      <div className="admin-content mx-auto">
        {this.state.loading && (
          <div className="text-center">
            <ScaleLoader size={150} color={"#123abc"} loading={this.state.loading} rounded={true} />
          </div>
        )}
        <div className="w-100 mb-5">
          <h4>{id} Item Details</h4>
          <button className="btn  btn-secondary float-right" style={{ marginBottom: 20 }} onClick={() => { window.print() }}>Print</button>

        </div>

        <div className="w-75">
          <form >
            <div className="container-fluid">
              <div className="row">
                <div className="col-12">
                  <div className="form-group">
                    <label className="mb-2">Title</label>
                    <input type="text" className="form-control" name="title" value={this.state.title} readOnly />
                  </div>
                </div>
                <div className="col-12">
                  <div className="form-group">
                    <label className="mb-2">Desciption</label>
                    <textarea type="text" className="form-control" name="description" row="10" value={this.state.description} readOnly />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Total In Stock</label>
                    <input type="text" className="form-control" name="totalInStock" value={this.state.stock} readOnly />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Delivered</label>
                    <input type="text" className="form-control" name="minQuantity" value={this.state.delivered} readOnly />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Unit</label>
                    <input type="text" className="form-control" name="unit" value={this.state.unit} readOnly />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Low Stock Quantity</label>
                    <input type="text" className="form-control" name="minQuantity" value={this.state.lowStockQuantity} readOnly />
                  </div>
                </div>

                <div className="" style={{ width: "100%", }}>
                  <h4 style={{ textAlign: "center", marginTop: 10 }}>
                    Shipment Of Item {this.state.title}
                  </h4>
                  {this.state.shipments.length > 0 ? (
                    <div>
                      <table
                        style={{
                          width: "100%",
                          borderCollapse: "collapse",
                          border: "1px solid #ddd",
                          backgroundColor: "#f5f5f5",
                        }}
                      >
                        <thead style={{ backgroundColor: "#c2c5c0" }}>
                          <tr>
                            <th style={tableHeaderStyle} >Id</th>
                            <th style={tableHeaderStyle}>Quantity</th>
                            <th style={tableHeaderStyle}>Cost/Unit</th>
                            <th style={tableHeaderStyle}>Delivered</th>
                            <th style={tableHeaderStyle}>In Stock</th>


                          </tr>
                        </thead>
                        <tbody>
                          {this.state.shipments && this.state.shipments.map((shipment) => (
                            <tr
                              key={shipment.id}
                              style={tableRowStyle}
                              onClick={() => {
                                // Navigate to "View Item" page
                                this.props.history.push(`/app/store/store/shipment/view/${shipment.shipmentId}`);
                              }}
                            >
                              <td style={tableCellStyle}>{shipment.shipmentId}</td>
                              <td style={tableCellStyle}>{shipment.quantity}</td>
                              <td style={tableCellStyle}>{shipment.cost}</td>
                              <td style={tableCellStyle}>{shipment.delivered}</td>
                              <td style={tableCellStyle}>{shipment.stock}</td>

                            </tr>
                          ))}
                        </tbody>
                      </table>
                    </div>) : (<div>
                      <table
                        style={{
                          width: "100%",
                          borderCollapse: "collapse",
                          border: "1px solid #ddd",
                          backgroundColor: "#f5f5f5",
                        }}
                      >
                        <thead style={{ backgroundColor: "#c2c5c0" }}>
                          <tr>
                            <th style={tableHeaderStyle} >Id</th>
                            <th style={tableHeaderStyle}>Quantity</th>
                            <th style={tableHeaderStyle}>Cost/Unit</th>
                            <th style={tableHeaderStyle}>Delivered</th>
                            <th style={tableHeaderStyle}>In Stock</th>


                          </tr>
                        </thead>
                        <tbody>
                          {this.state.shipments && this.state.shipments.map((shipment) => (
                            <tr
                              key={shipment.id}
                              style={tableRowStyle}
                              onClick={() => {
                                // Navigate to "View Item" page
                                this.props.history.push(`/app/store/store/shipment/view/${shipment.shipmentId}`);
                              }}
                            >
                              <td style={tableCellStyle}>{shipment.shipmentId}</td>
                              <td style={tableCellStyle}>{shipment.quantity}</td>
                              <td style={tableCellStyle}>{shipment.cost}</td>
                              <td style={tableCellStyle}>{shipment.delivered}</td>
                              <td style={tableCellStyle}>{shipment.stock}</td>

                            </tr>
                          ))}
                        </tbody>
                      </table>
                      <p style={{ textAlign: "center", marginTop: 15 }}><h5>No Shipment Found</h5></p></div>)}

                </div>
                <div className="" style={{ width: "100%", }}>
                  <h4 style={{ textAlign: "center", marginTop: 50 }}>
                    Deliveries Of Item {this.state.title}
                  </h4>
                  {this.state.deliveries.length > 0 ? (
                    <div>
                      <table
                        style={{
                          width: "100%",
                          borderCollapse: "collapse",
                          border: "1px solid #ddd",
                          backgroundColor: "#f5f5f5",
                        }}
                      >
                        <thead style={{ backgroundColor: "#c2c5c0" }}>
                          <tr>
                            <th style={tableHeaderStyle}>Id</th>
                            <th style={tableHeaderStyle}>Quantity</th>
                            <th style={tableHeaderStyle}>Price/Unit</th>
                            <th style={tableHeaderStyle}>Status</th>
                            <th style={tableHeaderStyle}>Amount</th>

                          </tr>
                        </thead>
                        <tbody>
                          {this.state.deliveries && this.state.deliveries.map((delivery) => (
                            <tr
                              key={delivery.deliveryId}
                              style={tableRowStyle}
                              onClick={() => {
                                // Navigate to "View Item" page
                                this.props.history.push(`/app/store/store/delivery/view/${delivery.deliveryId}`);
                              }}
                            >
                              <td style={tableCellStyle}>{delivery.deliveryId}</td>
                              <td style={tableCellStyle}>{delivery.quantity}</td>
                              <td style={tableCellStyle}>{delivery.pricePerUnit}</td>
                              <td style={tableCellStyle}> {delivery.dispatchValue ? "Dispatched" : "Pending"}</td>
                              <td style={tableCellStyle}>{delivery.totalSalePrice}</td>

                            </tr>
                          ))}
                        </tbody>
                      </table>
                    </div>) : (
                    <div>
                      <table
                        style={{
                          width: "100%",
                          borderCollapse: "collapse",
                          border: "1px solid #ddd",
                          backgroundColor: "#f5f5f5",
                        }}
                      >
                        <thead style={{ backgroundColor: "#c2c5c0" }}>
                          <tr>
                            <th style={tableHeaderStyle}>Id</th>
                            <th style={tableHeaderStyle}>Quantity</th>
                            <th style={tableHeaderStyle}>Price/Unit</th>
                            <th style={tableHeaderStyle}>Status</th>
                            <th style={tableHeaderStyle}>Amount</th>

                          </tr>
                        </thead>
                        <tbody>
                          {this.state.deliveries && this.state.deliveries.map((delivery) => (
                            <tr
                              key={delivery.deliveryId}
                              style={tableRowStyle}
                              onClick={() => {
                                // Navigate to "View Item" page
                                this.props.history.push(`/app/store/store/delivery/view/${delivery.deliveryId}`);
                              }}
                            >
                              <td style={tableCellStyle}>{delivery.deliveryId}</td>
                              <td style={tableCellStyle}>{delivery.quantity}</td>
                              <td style={tableCellStyle}>{delivery.pricePerUnit}</td>
                              <td style={tableCellStyle}> {delivery.dispatchValue ? "Dispatched" : "Pending"}</td>
                              <td style={tableCellStyle}>{delivery.totalSalePrice}</td>

                            </tr>
                          ))}
                        </tbody>
                      </table>
                      <p style={{textAlign: "center",  marginTop: 15}}><h5>No Delivery Found</h5></p></div>)}

                </div>
                <div className="col-12 mt-3">
                  <div className="form-group">
                    <Link to={`/app/store/store/product-category/view/${this.state.categoryId}`} className="btn btn-warning ">
                      Back
                    </Link>
                  </div>
                </div>
              </div>
            </div>

          </form>
        </div>

      </div>
    )
  }
}

export default ProductView;

// Inline CSS styles
const tableHeaderStyle = {
  backgroundColor: "#c2c5c0",
  padding: "10px",
  border: "1px solid #ddd",
  fontWeight: "bold",
};

const tableRowStyle = {
  cursor: "pointer",
  transition: "background-color 0.3s",
};

const tableCellStyle = {
  padding: "10px",
  border: "1px solid #ddd",
};


