import React, { Component } from "react";
// import "../../../style/admin.css";
import AnchorTag from "../Anchortag";
import Table from "../table/Table";
import axios from '../axiosConfig';
import { ScaleLoader } from "react-spinners";


class BusinessList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columnList: ["ID", "name", "Action"],
      tableData: [],
      allowAction: false,
      loading: false, // Add loading state
      notification: false,
      notificationText: "",
    };
    this.formattedData = []
  }
  componentDidMount() {
    this.setState({ loading: true }); // Set loading to true when submitting
    this.state.columnList = ["ID", "Bussiness Title", "Action"],

      // Make a GET request to your API endpoint to fetch tableData
      axios.get("/business/get").then((response) => {
        // Update the state with the fetched tableData
        console.log(response.data)
        const formattedData = response.data.map((item) => ({
          "id": item.businessId, // Assuming CategoryId is the ID you want
          "name": item.name,
        }));

        this.setState({ loading: false }); // Set loading to true when submitting

        // Update the state with the formatted data
        if (formattedData !== null && formattedData.length > 0) {
          this.setState({ tableData: formattedData });
        } else {
          this.setState({ tableData: [] });
        }
      }).catch((error) => {
        this.setState({ loading: false }); // Set loading to true when submitting
        // Handle any errors that occur during the request
        console.error("Error fetching tableData:", error);
      });
  }
  handleDelete = async (id) => {
    console.log('delete called', id)
    this.setState({ loading: true })
    const confirmDelete = window.confirm("Are you sure you want to delete?");
    if (confirmDelete) {
      const confirmDelete = window.confirm("This Action Will delete all the Users and Relavent Data Of this Business. Are you sure you want to delete?");
      if (confirmDelete) {
        await axios.delete(`/business/delete/${id}`).then((response) => {
          console.log(`${id}deleted`)
          if (response.status === 200) {
            // Handle a successful response from the server here
            console.log("Form data sent successfully:", response.data);
            this.setState({
              notification: true,
              notificationText: response.data.message

            })
          }
          setTimeout(() => {
            this.setState({ notification: false });
          }, 3000);
        }).catch((error) => {
          // Handle any errors that occur during the request
          console.error("Error fetching tableData:", error);
        })
        window.location.reload();
      }
      this.setState({ loading: false }); // Set loading to true when deleting
    }
    this.setState({ loading: false }); // Set loading to true when deleting
  }
  render() {
    const table = (
      <React.Fragment>
        <Table
          className="table table-striped"
          columnList={this.state.columnList}
          tableData={this.state.tableData}
          actionLinkPrefix="business-list/"
          handledelete={this.handleDelete}
        ></Table>
      </React.Fragment>
    );

    const noDataMessage = (
      <p style={{ textAlign: "center", marginTop: 50 }}>
        <h4>No Business Found </h4>
      </p>
    );
    return (
      <div className="admin-content mx-auto">
        {this.state.loading && (
          <div className="text-center">
            <ScaleLoader
              size={150}
              color={"#123abc"}
              loading={this.state.loading}
              rounded={true} // Set the spinner to be rounded
            />
          </div>
        )}
        {this.state.notification && (
          <div className="alert alert-success" role="alert">
            {this.state.notificationText}
          </div>
        )}
        <div className="w-100 mb-5">
          <AnchorTag link="/app/store/profile/create-business" className="btn btn-warning float-right" itemValue="Add New Business"></AnchorTag>
          <h4>Bussines</h4>
        </div>
        {this.state.tableData.length > 0 ? (
          table
        ) : (
          <React.Fragment>
            <Table
              className="table table-striped"
              columnList={this.state.columnList}
              tableData={[]}
              actionLinkPrefix="business-list/"
              handledelete={this.handleDelete}
            />
            {noDataMessage}
          </React.Fragment>
        )}
      </div>
    )
  }
}

export default BusinessList;


