import React, {Component} from "react";
import InputPasswordGroup from "../../components/input/InputPasswordGroup";
import InputFormGroup from "../../components/input/InputFormGroup";
import SelectFormGroup from "../../components/input/SelectFormGroup";
import axios from "axios";
import { ScaleLoader } from "react-spinners";

class PasswordResetForm extends Component{
  
    constructor(props) {
        super(props);
        this.state = {
            fullName:"",
            email:"", 
            roleId:"",
            password:"",
            status:true,
            rolesOptions:[],
            notification: false,
            notificationText: "",
            danger: false,
            loading: false,
        };
      }
      
      
      handleInputChange = (event) => {
        const { name, value } = event.target;
        this.setState({
          [name]: value,
        });
      };
    
      handleSubmit = (event) => {
        event.preventDefault();
        const {
            fullName,
            email, 
            status,
        } = this.state;
        if (!this.state.fullName || !this.state.email) {
          alert('Please fill in all required fields.');
          return;
        }
        const formData = {
            fullName,
            email, 
            status
        };
        console.log(JSON.stringify(formData));
        this.setState({ loading: true })
        const id = this.props.match.params.userId;

      // Make a POST request to your backend API with Axios
      axios.put(`/user/update/${id}`, formData)
      .then((response) => {
        // Handle a successful response from the server here
        if (response.data.code === 400 || response.data.code === 500) {
          this.setState({
            danger: true,
            notificationText: response.data.message,
          });
          setTimeout(() => {
            this.setState({ notification: false, danger: false });
          }, 5000);
        } else if (response.data.code === 200) {
          console.log("Form data sent successfully:", response.data);
          this.setState({
            notification: true,
            danger: false,
            notificationText: response.data.message,
          });
          setTimeout(() => {
            this.setState({ notification: false, danger: false });
          }, 5000);
        }
        // Handle any other status codes if needed
      })
      .catch((error) => {
        // Handle any errors that occur during the request
        this.setState({
          notificationText: "An error occurred while Updating User",
          danger: true,
        });
        console.error("Error sending form data:", error);
        setTimeout(() => {
          this.setState({ danger: false });
        }, 3000);
      }).finally(() => {
        // Set loading state to false after the request is complete
        this.setState({ loading: false });
      });
  }
    async componentDidMount() {
      // Make an API request to fetch the item options
      const id = this.props.match.params.userId;

      try {
        const response = await axios.get("/role/get");
        console.log(response.data)
        this.setState({ rolesOptions: response.data });
      } catch (error) {
        console.error("Error fetching item options:", error);
      }
      try {
        const response = await axios.get(`/user/get/${id}`);
        console.log(response.data)
        this.setState(
            {
                 email: response.data.email,
                 fullName:response.data.fullName ,
                 roleId:response.data.roleId,
                 status:response.data.status
            });

      } catch (error) {
        console.error("Error fetching item options:", error);
      }
    }
      render() {
        return (
          <div className="admin-content mx-auto">
            {this.state.loading && (
          <div className="text-center">
            <ScaleLoader
              size={150}
              color={"#123abc"}
              loading={this.state.loading}
              rounded={true} // Set the spinner to be rounded
            />
          </div>
        )}
        {this.state.notification && (
          <div className="alert alert-success" role="alert">
            {this.state.notificationText}
          </div>
        )}
        {this.state.danger && (
          <div className="alert alert-danger" role="alert">
            {this.state.notificationText}
          </div>
        )}
            <div className="w-100 mb-5">
              <button
                className="btn btn-primary float-right"
                onClick={() => {
                  window.history.back();
                }}
              >
                Back to User List
              </button>
    
              <h4>Edit User</h4>
            </div>
            <div className="w-75">
              <form onSubmit={this.handleSubmit}>
                <div className="container-fluid">
                  <div className="row">
                    <div className="col-6">
                      <div className="form-group">
                        <label className="mb-2">Full Name<span style={{color:"red"}}>*</span></label>
                        <input
                          type="text"
                          className="form-control"
                          name="fullName"
                          required
                          value={this.state.fullName}
                          onChange={this.handleInputChange}
                        />
                      </div>
                    </div>
                    <div className="col-6">
                      <div className="form-group">
                        <label className="mb-2">Email Address<span style={{color:"red"}}>*</span></label>
                        <input
                          type="email"
                          className="form-control"
                          name="email"
                          required
                          row="10"
                          value={this.state.email}
                          onChange={this.handleInputChange}
                        />
                      </div>
                    </div>
                  
                  <div className="col-6">
                    <div className="form-group">
                      <label className="mb-2">Status</label>
                      <select
                        className="form-control"
                        name="status"
                        value={this.state.status}
                        onChange={this.handleInputChange}
                      >
                        <option  key={true} value={true}>Active</option>
                        <option key={false} value={false}>In Active</option>
                      </select>
                    </div>
                  </div>
                    </div>
    
                    <div className="col-12 mt-3">
                      <div className="form-group">
                        <button
                          className="btn btn-warning back-link"
                          onClick={() => {
                            window.history.back();
                          }}
                        >
                          Back
                        </button>
                        <button type="submit" className="btn btn-success ml-3" value="Submit">Update</button>
                      </div>
                    </div>
                 
                </div>
              </form>
            </div>
          </div>
        );
      }
    }

export default PasswordResetForm;


