import React, { Component } from "react";
import { Link } from "react-router-dom";
import NavLiTag from "../navigation/NavListTag";
import { getUser } from "../Auth";

class HeaderNavBar extends Component {
    constructor(props) {
        super(props);
        this.user = getUser();

        this.state = {
            userRole: this.user.role,
            isManager:this.user.isManager
        };
        console.log(this.state.userRole)
        var navbarText = "";
        if (this.state.userRole === 2) {
            this.navbarText = "Office Inventory Management"
        }
        else if (this.state.userRole === 3) {
            this.navbarText = " Store Inventory Management"
        }
        else {
            this.navbarText = "Inventory Management"

        }
    }


    render() {
        return (
            <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
                <Link className="navbar-brand px-3" to="/">{this.navbarText}</Link>
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>

                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav ml-auto">
                        {(this.state.userRole === 5) && (<NavLiTag link="/app/office/office/dashboard" className="nav-link" itemValue="Office"></NavLiTag>)}
                        {( this.state.userRole === 5) && (<NavLiTag link="/app/store/store/dashboard" className="nav-link" itemValue="Store"></NavLiTag>)}
                        {(this.state.userRole === 3 || this.state.userRole === 5) &&this.state.isManager&& (<NavLiTag link="/app/store/profile/user" className="nav-link" itemValue="Manage User"></NavLiTag>)}
                        {this.state.userRole === 2 &&this.state.isManager&& (<NavLiTag link="/app/office/profile/user" className="nav-link" itemValue="Manage User"></NavLiTag>)}
                        {this.state.userRole === 1 && (<NavLiTag link="/app/store/profile/message" className="nav-link" itemValue="Messages"></NavLiTag>)}

                        {this.state.userRole === 1 && (<NavLiTag link="/app/store/profile/business-list" className="nav-link" itemValue="Manage Accounts"></NavLiTag>)}
                        {this.state.userRole === 2 && (<NavLiTag link="/app/office/profile/password-reset" className="nav-link" itemValue="Change Password"></NavLiTag>)}
                        {this.state.userRole === 3 && (<NavLiTag link="/app/store/profile/password-reset" className="nav-link" itemValue="Change Password"></NavLiTag>)}
                        {this.state.userRole === 2 && (<NavLiTag link="/app/office/profile/me" className="nav-link" itemValue="Profile"></NavLiTag>)}
                        {this.state.userRole === 3 && (<NavLiTag link="/app/store/profile/me" className="nav-link" itemValue="Profile"></NavLiTag>)}
                        {(this.state.userRole === 1 || this.state.userRole === 5) && (<NavLiTag link="/app/store/profile/me" className="nav-link" itemValue="Profile"></NavLiTag>)}
                        {(this.state.userRole === 1 || this.state.userRole === 5) && (<NavLiTag link="/app/store/profile/password-reset" className="nav-link" itemValue="Change Password"></NavLiTag>)}

                        <NavLiTag link="/logout" className="nav-link" itemValue="Logout"></NavLiTag>
                    </ul>
                </div>
            </nav>
        )

    }
}

export default HeaderNavBar;


