import React, {Component} from "react";
import AnchorTag from "../Anchortag";


class ExpenseCategoryEdit extends Component{
    constructor(props){
        super(props);
    }


    render(){
        return (
            <div className="admin-content mx-auto">
                <div className="w-100 mb-5">
                    <AnchorTag link="/app/office/office/expense-category/list" className="btn btn-primary float-right" itemValue="Back to Category List"></AnchorTag>
                    <h4>Edit Expense Category</h4>
                </div>
                <div className="w-75">
                    <form>
                        <div className="container-fluid">
                            <div className="row">
                                <div className="col-6">
                                    <div className="form-group">
                                        <label className="mb-2">Category Name</label>
                                        <input type="text" className="form-control" value="Bills"/>
                                    </div>
                                </div>
                                <div className="col-6">
                                    <div className="form-group">
                                        <label className="mb-2">Date</label>
                                        <input type="date" className="form-control" value="2023-09-23"/>
                                    </div>
                                </div>

                                <div className="col-12 mt-3">
                                    <div className="form-group">
                                        <AnchorTag className="btn btn-warning" itemValue="Back" link="/app/office/expense-category/list"/>
                                        <button type="submit" className="btn btn-success ml-3" value="Submit">Submit</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                        
                    </form>
                </div>
                
            </div>
        ) 
    }
}

export default ExpenseCategoryEdit;


