import React, { Component } from "react";
import axios from "axios";
import { ScaleLoader } from "react-spinners";
import { Link } from "react-router-dom";

class DeliveryForm extends Component {
  constructor(props) {
    super(props);

    this.state = {
      title: "",
      amount: "",
      date: "",
      deliveryId:"",
      cargoCharges:"",
      existingItems: [
        {
          itemId: "",
          customerId: "",
          quantity:"" ,
          customerOptions:[],
        },
      ],
      customerIds:[],
      itemOptions: [], 
      // existingCustomers: [],
      deliveryOptions:[],
      quantity: 600,
      itemPrice: 0,
      notification: false,
      notificationText: "",
      danger: false,
      loading: false,
    };
  }
  resetFormFields = () => {
    this.setState({
      title: "",
      amount: "",
      date: "",
      deliveryId:"",
      cargoCharges:"",
      existingItems: [
        {
          itemId: "",
          customerId: "",
          quantity:"" ,
          customerOptions:[],
        },
      ],
      customerIds:[],
      itemOptions: [], 
      // existingCustomers: [],
      deliveryOptions:[],
    });
  };

  componentDidMount() {
    // Make a GET request to your API endpoint to fetch tableData
    axios.get(` /delivery/get/dispatch`).then((response) => {
      this.setState({ deliveryOptions: response.data });
    }).catch((error) => {
      console.error("Error fetching tableData:", error);
    });
  }
  async fetchItemsByDelivery(deliveryId) {
    try {
      const response = await axios.get(`/deliveryreturn/getItemsbyDelivery/${deliveryId}`);
      this.setState({ itemOptions: response.data });
    } catch (error) {
      console.error("Error fetching item options:", error);
    }
  }
    // Method to fetch customers based on selected item
    async fetchCustomersByItem(itemId,index) {
      try {
        const deliveryId=this.state.deliveryId
        const response = await axios.get(`/deliveryreturn/getCustomerbyDelivery/${deliveryId}/${itemId}`);
        console.log('response',response.data)
       // Get the existing customerIds from existingItems
       const existingCustomerIds = this.state.existingItems
       .filter((item) => item.itemId === itemId)
       .map((item) => parseInt(item.customerId));    console.log('fexistingCustomerIds',existingCustomerIds)

    // Filter response.data to exclude customer options that are already in existingItems
    const filteredCustomerOptions = response.data.filter((customer) => !existingCustomerIds.includes(customer.CustomerId));
console.log('filteredData',filteredCustomerOptions)
    const updatedItems = [...this.state.existingItems];
    updatedItems[index].customerOptions = filteredCustomerOptions;
    this.setState({ existingItems: updatedItems });
      } catch (error) {
        console.error("Error fetching customer options:", error);
      }
    }

  // Handle change of the selected delivery
  handleDeliveryChange = (event) => {
    const deliveryId = event.target.value;
    this.setState({ deliveryId });
    this.fetchItemsByDelivery(deliveryId);
  }

  // Handle change of the selected item
  handleItemChange = (event, index) => {
    const itemId = event.target.value;
    const updatedItems = [...this.state.existingItems];
    updatedItems[index][event.target.name] = itemId;
    this.setState({ existingItems: updatedItems });

    // Fetch customers based on the selected item
    this.fetchCustomersByItem(itemId,index);
  }  
  handleInputChange = (event,index) => {
    const { name, value } = event.target;
    this.setState({
      [name]: value,
    });
  };
  handleItemInputChange = (event, index) => {
    const { name, value } = event.target;
    const updatedItems = [...this.state.existingItems];
    updatedItems[index][name] = value;
    this.setState({ existingItems: updatedItems });
  };
  handleAddExistingItem = () => {
    const newItem = {
      itemId:0 ,
      customerId: 0,
      quantity: 0,
      itemPrice: 0, // Update with the actual item price
    };
    this.setState((prevState) => ({
      existingItems: [...prevState.existingItems, newItem],
    }));
  };

  handleDeleteExistingItem = (index) => {
    const updatedItems = [...this.state.existingItems];
    updatedItems.splice(index, 1);
    this.setState({ existingItems: updatedItems });
  };
  handleSubmit = (event) => {
    event.preventDefault();
    const { title,date, existingItems,deliveryId } = this.state;
    const formData = {
      title,
      date,
      deliveryId,
      deliveryReturnItems:existingItems,
    };
    console.log(formData);
    this.setState({ loading: true });
    // Make a POST request to your backend API with Axios
  axios.post("/deliveryreturn/create", formData)
  .then((response) => {
    if (response.data.code === 400 || response.data.code === 401 || response.data.code === 500) {
      console.log("response")
      this.setState({
        danger: true,
        notificationText: response.data.message,
      });
      setTimeout(() => {
        this.setState({ notification: false, danger: false });
      }, 12000);
    } else if (response.data.code === 200) {
      console.log("Form data sent successfully:", response.data);
      this.setState({
        notification: true,
        danger:false,
        notificationText: response.data.message,
      });
      this.resetFormFields();
      setTimeout(() => {
        this.setState({ notification: false, danger: false });
      }, 5000);
    }
    // Handle any other status codes if needed
  })
  .catch((error) => {
    // Handle any errors that occur during the request
    this.setState({
      notificationText: "An error occurred while creating deliveryReturn",
      danger: true,
    });
    console.error("Error sending form data:", error);
    setTimeout(() => {
      this.setState({ danger: false });
    }, 5000);
  }).finally(() => {
    // Set loading state to false after the request is complete
    this.setState({ loading: false });
  });
}
  async componentDidMount() {
     await axios.get(`/delivery/get/dispatch`).then((response) => {
      this.setState({ deliveryOptions: response.data });
    }).catch((error) => {
      console.error("Error fetching tableData:", error);
    });
  }
  render() {
    const { existingItems, itemOptions,customerOptions } = this.state;

    return (
      <div className="admin-content mx-auto">
        {this.state.loading && (
          <div className="text-center">
            <ScaleLoader
              size={150}
              color={"#123abc"}
              loading={this.state.loading}
              rounded={true} // Set the spinner to be rounded
            />
          </div>
        )}
        {this.state.notification && (
          <div className="alert alert-success" role="alert">
            {this.state.notificationText}
          </div>
        )}
        {this.state.danger && (
          <div className="alert alert-danger" role="alert">
            {this.state.notificationText}
          </div>
        )}
        <div className="w-100 mb-5">
          <button
            className="btn btn-primary float-right"
            onClick={() => {
              window.history.back();
            }}
          >
            Back to Delivery List
          </button>

          <h4>Add Delivery Return</h4>
        </div>
        <div className="w-75">
          <form onSubmit={this.handleSubmit}>
            <div className="container-fluid">
              <div className="row">
                <div className="col-12">
                  <div className="form-group">
                    <label className="mb-2">Title</label>
                    <input
                      type="text"
                      className="form-control"
                      name="title"
                      required
                      value={this.state.title}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>

                <div className="col-4">
                  <div className="form-group">
                    <label className="mb-2">Date</label>
                    <input
                      type="date"
                      className="form-control"
                      name="date"
                      required
                      value={this.state.date}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                  <label className="mb-2">Select Delivery</label>
                  <select
                    className="form-control"
                    name="deliveryId"
                    required
                    value={this.state.deliveryId}
                    onChange={this.handleDeliveryChange}
                  >
                    <option value="">Select Delivery</option>
                    {this.state.deliveryOptions.map((option) => (
                      <option key={option.value} value={option.deliveryId}>
                        {option.deliveryId}-{option.title}
                      </option>
                    ))}
                  </select>
                  </div>
                  </div>
                <div className="form-group">
        {existingItems.map((item, index) => (
          <div key={index} className="existing-item" style={{marginLeft:15, backgroundColor:"#aab4b5",borderRadius:10,marginBottom:20,padding:20}}>
            <div className="row">
              <div className="col-6">
                <div className="form-group">
                  <label className="mb-2">Select Item</label>
                  <select
                    className="form-control"
                    name="itemId"
                    required
                    value={item.itemId}
                    onChange={(event) => this.handleItemChange(event, index)}
                  >
                    <option value="">Select Item</option>
                    {itemOptions.map((option) => (
                      <option key={option.value} value={option.itemId}>
                        {option.itemId}-{option.name}
                      </option>
                    ))}
                  </select>
                </div>
              </div>
              <div className="col-6">
                <div className="form-group">
                  <label className="mb-2">Customer</label>
                  <select
                    className="form-control"
                    name="customerId"
                    required
                    value={item.customerId}
                    onChange={(event) => this.handleItemInputChange(event, index)}
                  >
                    <option value="">Select Customers</option>
                    {item.customerOptions && item.customerOptions.map((option) => (
                <option key={option.CustomerId} value={option.CustomerId}>
                  {option.CustomerId}-{option.name}
                </option>
              ))}
                    {/* Add more options as needed */}
                  </select>
                </div>
              </div>
              <div className="col-4">
                <div className="form-group">
                  <label className="mb-2">Quantity</label>
                  <input
                  required
                    type="number"
                    className="form-control"
                    name="quantity"
                    value={item.quantity}
                    onChange={(event) => this.handleItemInputChange(event, index)}
                  />
                </div>
              </div>
            </div>
            <button
              className="btn btn-danger float-right"
              onClick={() => this.handleDeleteExistingItem(index)}
            >
              Delete
            </button>
          </div>
        ))}
        <button
          className="btn btn-primary"
          type="button"
          onClick={this.handleAddExistingItem}
          style={{marginLeft: 15}}
        >
          + Add Item
        </button>
                </div>

             
              </div>

              <div className="col-12 mt-3">
                <div className="form-group">
                <Link to="/app/store/store/deliveryreturn/list" className="btn btn-warning back-link">
                    Back
                  </Link>
                  <button type="submit" className="btn btn-success ml-3" value="Dispatch">Submit</button>
                </div>
              </div>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

export default DeliveryForm;



