import React, { Component } from "react";
// import "../../../style/admin.css";
import AnchorTag from "../Anchortag";
import Table from "../table/Table";
import axios from "axios";
import { ScaleLoader } from "react-spinners";


class UserList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columnList: ["ID", "Full Name", "Email", "Role", "Status", "Action"],
      tableData: [],
      allowAction: false,
      loading: false, // Add loading state
      notification: false,
      notificationText: "",
    };
  }
  componentDidMount() {
    const id = this.props.match.params.businessId;
    this.setState({ loading: true }); // Set loading to true when submitting
    this.state.columnList = ["ID", "Full Name", "Email", "Role", "Status", "Action"],

      // Make a GET request to your API endpoint to fetch tableData
      axios.get("/user/get").then((response) => {
        // Update the state with the fetched tableData
        console.log(response.data)
        const formattedData = response.data.map((item) => ({
          "id": item.userId, // Assuming CategoryId is the ID you want
          "name": item.fullName,
          "date": item.email, // Format as needed
          "role": item.roleId, // Format as needed
          "status": item.status ? "Active" : "Inactive",

        }));
        this.setState({ loading: false }); // Set loading to true when submitting

        // Update the state with the formatted data
        this.setState({ tableData: formattedData });
        console.log(this.state.tableData)
      }).catch((error) => {
        this.setState({ loading: false }); // Set loading to true when submitting
        // Handle any errors that occur during the request
        console.error("Error fetching tableData:", error);
      });
  }

  render() {
    const id = this.props.match.params.businessId;

    return (
      <div className="admin-content mx-auto">
        {this.state.loading && (
          <div className="text-center">
            <ScaleLoader
              size={150}
              color={"#123abc"}
              loading={this.state.loading}
              rounded={true} // Set the spinner to be rounded
            />
          </div>
        )}
        {this.state.notification && (
          <div className="alert alert-success" role="alert">
            {this.state.notificationText}
          </div>
        )}
        <div className="w-100 mb-5">
          <AnchorTag link={`/app/store/profile/create-user/${id}`} className="btn btn-warning float-right" itemValue="Create User"></AnchorTag>
          <h4>System User</h4>
        </div>
        <Table className="table table-striped" columnList={this.state.columnList} tableData={this.state.tableData} actionLinkPrefix=""></Table>
      </div>
    )
  }
}

export default UserList;


