import React, {Component} from "react";
import { Route, Switch } from "react-router-dom";
import ProductCategoryList from "../../../components/store/Inventory/StoreProductCategoryList";
import ProductCategoryForm from "../../../components/store/Inventory/StoreProductCategoryForm";
import ProductCategoryEdit from "../../../components/store/Inventory/StoreProductCategoryEdit";
import ProductList from "../../../components/store/Inventory/StoreProductList";
import ProductForm from "../../../components/store/Inventory/StoreProductForm";
import ProductView from "../../../components/store/Inventory/StoreProductView";
import ProductEdit from "../../../components/store/Inventory/StoreProductEdit";

import ShipmentList from "../../../components/store/Shipment/ShipmentList";
import ShipmentForm from "../../../components/store/Shipment/ShipmentForm";
import ShipmentView from "../../../components/store/Shipment/ShipmentView";
import ShipmentEdit from "../../../components/store/Shipment/ShipmentEdit";

import DeliveryList from "../../../components/store/Delivery/DeliveryList";
import DeliveryForm from "../../../components/store/Delivery/DeliveryForm";
import DeliveryView from "../../../components/store/Delivery/DeliveryView";
import DeliveryEdit from "../../../components/store/Delivery/DeliveryEdit";

import DeliveryReturnList from "../../../components/store/DeliveryReturn/DeliveryReturnList";
import DeliveryReturnForm from "../../../components/store/DeliveryReturn/DeliveryReturnForm";
import DeliveryReturnView from "../../../components/store/DeliveryReturn/DeliveryReturnView";
import DeliveryReturnEdit from "../../../components/store/DeliveryReturn/DeliveryReturnEdit";

import CustomerList from "../../../components/store/Customer/CustomerList";
import CustomerForm from "../../../components/store/Customer/CustomerForm";
import CustomerView from "../../../components/store/Customer/CustomerView";
import CustomerEdit from "../../../components/store/Customer/CustomerEdit";

import VendorList from "../../../components/store/Vendor/VendorList";
import VendorForm from "../../../components/store/Vendor/VendorForm";
import VendorView from "../../../components/store/Vendor/VendorView";
import VendorEdit from "../../../components/store/Vendor/VendorEdit";

import ReportList from "../../../components/store/Report/ReportList";
import Dashboard from "../../../components/store/Dashboard";
import withAuth from '../../../components/AuthHOC'; // Import the withAuth HOC
const DashboardWithAuth = withAuth(Dashboard);


class CustomerShopPage extends Component{
    constructor(props){
        super(props);
    }


    render(){
        return (
            <div className="w-100">
            <Switch>
                <Route exact path={`${this.props.match.path}/product-category/list`} component={ProductCategoryList}/>
                <Route exact path={`${this.props.match.path}/product-category/create`} component={ProductCategoryForm}/>
                <Route exact path={`${this.props.match.path}/product-category/view/:id`} component={ProductList}/>
                <Route exact path={`${this.props.match.path}/product-category/edit/:categoryId`} component={ProductCategoryEdit}/>
                <Route exact path={`${this.props.match.path}/product/create/:id`} component={ProductForm}/>
                <Route exact path={`${this.props.match.path}/product-category/view/view/:itemId`} component={ProductView}/>
                <Route exact path={`${this.props.match.path}/product-category/view/edit/:itemId`} component={ProductEdit}/>

                <Route exact path={`${this.props.match.path}/shipment/list`} component={ShipmentList}/>
                <Route exact path={`${this.props.match.path}/shipment/create`} component={ShipmentForm}/>
                <Route exact path={`${this.props.match.path}/shipment/view/:shipmentId`} component={ShipmentView}/>
                <Route exact path={`${this.props.match.path}/shipment/edit/:shipmentId`} component={ShipmentEdit}/>

                <Route exact path={`${this.props.match.path}/delivery/list`} component={DeliveryList}/>
                <Route exact path={`${this.props.match.path}/delivery/create`} component={DeliveryForm}/>
                <Route exact path={`${this.props.match.path}/delivery/view/:deliveryId`} component={DeliveryView}/>
                <Route exact path={`${this.props.match.path}/delivery/edit/:deliveryId`} component={DeliveryEdit}/>

                
                <Route exact path={`${this.props.match.path}/deliveryreturn/list`} component={DeliveryReturnList}/>
                <Route exact path={`${this.props.match.path}/deliveryreturn/create`} component={DeliveryReturnForm}/>
                <Route exact path={`${this.props.match.path}/deliveryreturn/view/:deliveryReturnId`} component={DeliveryReturnView}/>
                <Route exact path={`${this.props.match.path}/deliveryreturn/edit/:deliveryReturnId`} component={DeliveryReturnEdit}/>


                <Route exact path={`${this.props.match.path}/customer/list`} component={CustomerList}/>
                <Route exact path={`${this.props.match.path}/customer/create`} component={CustomerForm}/>
                <Route exact path={`${this.props.match.path}/customer/view/:customerId`} component={CustomerView}/>
                <Route exact path={`${this.props.match.path}/customer/edit/:customerId`} component={CustomerEdit}/>

                <Route exact path={`${this.props.match.path}/vendor/create`} component={VendorForm}/>
                <Route exact path={`${this.props.match.path}/vendor/list`} component={VendorList}/>
                <Route exact path={`${this.props.match.path}/vendor/view/:vendorId`} component={VendorView}/>
                <Route exact path={`${this.props.match.path}/vendor/edit/:vendorId`} component={VendorEdit}/>
                <Route exact path={`${this.props.match.path}/report/list`} component={ReportList}/>
                <Route exact path={`${this.props.match.path}/dashboard`} component={DashboardWithAuth}/>



            </Switch>
            
        </div>
        ) 
    }
}

export default CustomerShopPage;


