import React, { Component } from "react";
import InputPasswordGroup from "../../components/input/InputPasswordGroup";
import InputFormGroup from "../../components/input/InputFormGroup";
import SelectFormGroup from "../../components/input/SelectFormGroup";
import axios from '../axiosConfig';
import { ScaleLoader } from "react-spinners";

class EditBusiness extends Component {

  constructor(props) {
    super(props);
    this.state = {
      name: "",
      date: "",
      status: "",
      notification: false,
      danger: false,
      notificationText: "",
      loading: false,
    };
  }

  handleInputChange = (event) => {
    const { name, value } = event.target;
    this.setState({
      [name]: value,
    });
  };

  handleSubmit = (event) => {
    event.preventDefault();
    const {
      name,
      status,

    } = this.state;
    const formData = {
      name,
      status,

    };
    this.setState({ loading: true })
    console.log(JSON.stringify(formData));
    const id = this.props.match.params.businessId;

    // Make a POST request to your backend API with Axios
    axios.put(`/business/update/${id}`, formData)
      .then((response) => {
        // Handle a successful response from the server here
        if (response.status === 200) {
          // Handle a successful response from the server here
          console.log("Form data sent successfully:", response.data);
          this.setState({
            notification: true,
            notificationText: response.data.message

          })
        }
        setTimeout(() => {
          this.setState({ notification: false, danger: false });
        }, 3000);
      })
      .catch((error) => {
        // Handle any errors that occur during the request
        this.setState({ notificationText: "An error occured while creating Category", danger: true, })
        setTimeout(() => {
          this.setState({ danger: false });
        }, 3000);
        console.error("Error sending form data:", error);
      }).finally(() => {
        // Set loading to false after the request is complete
        this.setState({ loading: false });
      });
  };
  async componentDidMount() {
    // Make an API request to fetch the item options
    const id = this.props.match.params.businessId;
    try {
      const response = await axios.get(`/business/get/${id}`);
      console.log(response.data)
      this.setState(
        {
          name: response.data.name,
          status: response.data.status.toString(),

        });

    } catch (error) {
      console.error("Error fetching item options:", error);
    }

  }
  render() {
    return (
      <div className="admin-content mx-auto">
        {this.state.loading && (
          <div className="text-center">
            <ScaleLoader
              size={150}
              color={"#123abc"}
              loading={this.state.loading}
              rounded={true} // Set the spinner to be rounded
            />
          </div>
        )}
        {this.state.notification && (
          <div className="alert alert-success" role="alert">
            {this.state.notificationText}
          </div>
        )}
        {this.state.danger && (
          <div className="alert alert-danger" role="alert">
            {this.state.notificationText}
          </div>
        )}
        <div className="w-100 mb-5">
          <button
            className="btn btn-primary float-right"
            onClick={() => {
              window.history.back();
            }}
          >
            Back to User List
          </button>

          <h4>Edit Business</h4>
        </div>
        <div className="w-75">
          <form onSubmit={this.handleSubmit}>
            <div className="container-fluid">
              <div className="row">
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Name</label>
                    <input
                      type="text"
                      className="form-control"
                      name="name"
                      required
                      value={this.state.name}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Status</label>
                    <select
                      className="form-control"
                      name="status"
                      required
                      value={this.state.status}
                      onChange={this.handleInputChange}
                    >
                      <option value="true">Active</option>
                      <option value="false">Inactive</option>
                    </select>
                  </div>
                </div>
                <div className="col-12 mt-3">
                  <div className="form-group">
                    <button
                      className="btn btn-warning "
                      onClick={() => {
                        window.history.back();
                      }}
                    >
                      Back
                    </button>
                    <button type="submit" className="btn btn-success ml-3" value="Submit">Update</button>
                  </div>
                </div>
              </div>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

export default EditBusiness;


