import React, { Component } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import { ScaleLoader } from "react-spinners";


class VendorView extends Component {
  constructor(props) {
    super(props);
    this.productDetails =
      "PERFECT GIFT IDEA: Works on wet, tion and is not a product defect.";
    this.state = {
      firstName: "",
      lastName: "",
      email: "",
      phoneNumber: "",
      city: "",
      country: "",
      Address: "",
      shipments: [],
      showPaymentForm: false, // Track whether the payment form should be displayed
      paymentAmount: 0,
      paymentDate: "",
      shipmentId: null,
      totalAmount: "",
      totalAmountPaid: "",
      amountPayable: 0,
      loading: false,
      notification: false,
      notificationText: "",
      danger: false,
    };
  }
  resetFormFields = () => {
    this.setState({
      paymentAmount: "",
      paymentDate: "",
      // Add other fields and their initial values if needed
    });
  };

  handlePayNowClick = (rowId) => {
    this.setState({
      showPaymentForm: true,
      shipmentId: rowId,

    });
  };

  handlePaymentInputChange = (event) => {
    const { name, value } = event.target;
    this.setState({
      [name]: value
    });
  };

  handlePaymentSubmit = () => {
    const { shipmentId, paymentAmount, paymentDate } = this.state;
    const vendorId = this.props.match.params.vendorId;
    const formData = {
      shipmentId,
      vendorId,
      paymentAmount,
      paymentDate
    }
    console.log(formData)
    this.setState({ loading: true })
    // Make a POST request to your backend API with Axios
    axios.post("/vendorpayment/create", formData)
      .then((response) => {
        if (response.status === 201) {
          // Handle a successful response from the server here
          console.log("Form data sent successfully:", response.data);
          this.setState({
            notification: true,
            notificationText: response.data.message

          })
          this.resetFormFields()
        }
        setTimeout(() => {
          this.setState({ notification: false, danger: false });
        }, 3000);
        this.componentDidMount()
      })
      .catch((error) => {
        // Handle any errors that occur during the request
        this.setState({ notificationText: "An error occured while creating customerPayment", danger: true, })
        setTimeout(() => {
          this.setState({ danger: false });
        }, 3000);
        console.error("Error sending form data:", error);
      }).finally(() => {
        // Set loading to false after the request is complete
        this.setState({ loading: false });
      });
  };
  componentDidMount() {
    const id = this.props.match.params.vendorId;
    (async () => {
      this.setState({ loading: true })
      try {
        const response = await axios.get(`/vendor/get/${id}`)
        this.setState({
          firstName: response.data.vendor.firstName,
          lastName: response.data.vendor.lastName,
          email: response.data.vendor.email,
          phoneNumber: response.data.vendor.phoneNumber,
          city: response.data.vendor.city,
          country: response.data.vendor.country,
          Address: response.data.vendor.address,
          totalAmount: response.data.vendor.totalAmount,
          totalAmountPaid: response.data.vendor.totalAmountPaid,
          shipments: response.data.ShipmentsDetails,
          amountPayable: response.data.vendor.totalAmount - response.data.vendor.totalAmountPaid
        });
        console.log(this.state.amountPayable)
        this.setState({ loading: false })



      }
      catch (error) {
        console.error("Error fetching product details:");
      };
    })();
  }
  render() {
    return (
      <div className="admin-content mx-auto">
        {this.state.loading && (
          <div className="text-center">
            <ScaleLoader
              size={150}
              color={"#123abc"}
              loading={this.state.loading}
              rounded={true} // Set the spinner to be rounded
            />
          </div>
        )}
        {this.state.notification && (
          <div className="alert alert-success" role="alert">
            {this.state.notificationText}
          </div>
        )}
        {this.state.danger && (
          <div className="alert alert-danger" role="alert">
            {this.state.notificationText}
          </div>
        )}
        <div className="w-100 mb-5">
          <h4>Vendor Details</h4>
          <button
            className="btn  btn-secondary float-right"
            style={{ marginBottom: 20 }}
            onClick={() => {
              window.print();
            }}
          >
            Print
          </button>

        </div>
        <div className="w-75">
          <div >
            <div className="container-fluid">
              <div className="row">
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">First Name</label>
                    <input
                      type="text"
                      className="form-control"
                      name="firstName"
                      value={this.state.firstName}
                      readOnly
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Last Name</label>
                    <input
                      type="text"
                      className="form-control"
                      name="lastName"
                      value={this.state.lastName}
                      readOnly
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Email</label>
                    <input
                      type="text"
                      className="form-control"
                      name="email"
                      row="10"
                      value={this.state.email}
                      readOnly
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Phone Number</label>
                    <input
                      type="text"
                      className="form-control"
                      name="phoneNumber"
                      value={this.state.phoneNumber}
                      readOnly
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">City</label>
                    <input
                      className="form-control"
                      name="city"
                      value={this.state.city}
                      onChange={this.handleInputChange}
                      readOnly
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Country</label>
                    <input
                      className="form-control"
                      name="designation"
                      value={this.state.country}
                      readOnly
                    />
                  </div>
                </div>
                <div className="col-12">
                  <div className="form-group">
                    <label className="mb-2">Address</label>
                    <input
                      className="form-control"
                      name="address"
                      value={this.state.Address}
                      readOnly
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Total Amount</label>
                    <input
                      className="form-control"
                      name="pamountPayable"
                      value={this.state.totalAmount}
                      readOnly
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Amount Paid</label>
                    <input
                      className="form-control"
                      name="designation"
                      value={this.state.totalAmountPaid}
                      readOnly
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Amount Payable{this.state.amountPayable < 0 ? <span style={{ color: "green", fontSize: 14 }}> (Advance)</span> : <span style={{ color: "red", fontSize: 14 }}> (Due)</span>}</label>
                    <input
                      className="form-control"
                      name="pamountPayable"
                      value={this.state.amountPayable}
                      readOnly
                    />
                  </div>
                </div>


                <div className="items-list">
                  <div className="">
                    {this.state.showPaymentForm && (
                      <div style={paymentFormStyle}>
                        <h5 style={formTitleStyle}>Payment Form of {this.state.shipmentId}</h5>
                        <div style={formGroupStyle}>
                          <label style={labelStyle}>Payment Amount</label>
                          <input
                            type="number"
                            name="paymentAmount"
                            required
                            value={this.state.paymentAmount}
                            onChange={this.handlePaymentInputChange}
                            style={inputStyle}
                          />
                        </div>
                        <div style={formGroupStyle}>
                          <label style={labelStyle}>Payment Date</label>
                          <input
                            type="date"
                            name="paymentDate"
                            required
                            value={this.state.paymentDate}
                            onChange={this.handlePaymentInputChange}
                            style={inputStyle}
                          />
                        </div>
                        <button style={submitButtonStyle} onClick={this.handlePaymentSubmit}>
                          Submit
                        </button>
                      </div>
                    )}
                  </div>
                  <h4 style={{textAlign:"center" }}>
                    Shipments of {this.state.firstName} {this.state.lastName}
                  </h4>
                  {this.state.shipments.length > 0 ? (
                    <div>
                      <table
                        style={{
                          width: "140%",
                          borderCollapse: "collapse",
                          border: "1px solid #ddd",
                          marginLeft: 10,
                          backgroundColor: "#f5f5f5",

                        }}
                      >
                        <thead>
                          <tr>
                            <th style={tableHeaderStyle}>Id</th>
                            <th style={tableHeaderStyle}>Total Amount</th>
                            <th style={tableHeaderStyle}>Amount Paid</th>
                            <th style={tableHeaderStyle}>Payment Details</th>
                            <th style={tableHeaderStyle}>Amount Due</th>

                            <th style={tableHeaderStyle}>Action</th>
                          </tr>
                        </thead>
                        <tbody>
                          {this.state.shipments.map((item) => (
                            <tr
                              key={item.shipmentId}
                              style={tableRowStyle}

                            >
                              <td style={tableCellStyle}>{item.shipmentId}</td>
                              <td style={tableCellStyle}>{item.totalAmount}</td>
                              <td style={tableCellStyle}>{item.AmountPaid}</td>

                              <td style={tableCellStylee}>
                                <ul style={paymentListStyle}>
                                  {item.paymentDetails.map((payment, index) => (
                                    <div style={paymentDetails}>
                                      <li key={index} style={paymentItemStyle}>
                                        <div style={paymentAmountStyle}>Amount: {payment.paymentAmount}</div>
                                        <div style={paymentDateStyle}>Date: {payment.paymentDate}</div>
                                      </li>
                                    </div>
                                  ))}
                                </ul>

                              </td>
                              <td style={tableCellStyle}>
                                {item.AmountDues}
                              </td>
                              <td style={tableButtonCellStyle}>
                                <button
                                  className="btn btn-warning"
                                  disabled={item.TotalAmount - item.AmountPaid === 0}
                                  onClick={() => this.handlePayNowClick(item.shipmentId)}
                                >
                                  Pay Now
                                </button>
                              </td>
                            </tr>
                          ))}
                        </tbody>
                      </table>


                    </div>) : (<div>
                      <table
                        style={{
                          width: "140%",
                          borderCollapse: "collapse",
                          border: "1px solid #ddd",
                          marginLeft: 10,
                          backgroundColor: "#f5f5f5",

                        }}
                      >
                        <thead>
                          <tr>
                            <th style={tableHeaderStyle}>Id</th>
                            <th style={tableHeaderStyle}>Total Amount</th>
                            <th style={tableHeaderStyle}>Amount Paid</th>
                            <th style={tableHeaderStyle}>Payment Details</th>
                            <th style={tableHeaderStyle}>Amount Due</th>

                            <th style={tableHeaderStyle}>Action</th>
                          </tr>
                        </thead>
                        <tbody>
                          {this.state.shipments.map((item) => (
                            <tr
                              key={item.shipmentId}
                              style={tableRowStyle}

                            >
                              <td style={tableCellStyle}>{item.shipmentId}</td>
                              <td style={tableCellStyle}>{item.totalAmount}</td>
                              <td style={tableCellStyle}>{item.AmountPaid}</td>

                              <td style={tableCellStylee}>
                                <ul style={paymentListStyle}>
                                  {item.paymentDetails.map((payment, index) => (
                                    <div style={paymentDetails}>
                                      <li key={index} style={paymentItemStyle}>
                                        <div style={paymentAmountStyle}>Amount: {payment.paymentAmount}</div>
                                        <div style={paymentDateStyle}>Date: {payment.paymentDate}</div>
                                      </li>
                                    </div>
                                  ))}
                                </ul>

                              </td>
                              <td style={tableCellStyle}>
                                {item.AmountDues}
                              </td>
                              <td style={tableButtonCellStyle}>
                                <button
                                  className="btn btn-warning"
                                  disabled={item.TotalAmount - item.AmountPaid === 0}
                                  onClick={() => this.handlePayNowClick(item.shipmentId)}
                                >
                                  Pay Now
                                </button>
                              </td>
                            </tr>
                          ))}
                        </tbody>
                      </table>
                      <p style={{ marginLeft: 400, marginTop: 30 }}>
                        <h4>No Shipment Found </h4>
                      </p>
                    </div>
                  )}


                </div>


                <div className="col-12 mt-3">
                  <div className="form-group">
                    <Link to="/app/store/store/vendor/list" className="btn btn-warning back-link">
                      Back
                    </Link>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default VendorView;
// Inline CSS styles
const tableHeaderStyle = {
  backgroundColor: "#c2c5c0",
  padding: "10px",
  border: "1px solid #ddd",
  fontWeight: "bold",
};

const tableRowStyle = {
  cursor: "pointer",
  transition: "background-color 0.3s",
};

const tableCellStyle = {
  padding: "10px",
  border: "1px solid #ddd",
};
const tableButtonCellStyle = {
  padding: "10px",
  width: 130,
  border: "1px solid #ddd",
};
const tableCellStylee = {
  padding: "5px",
  width: 150,
  border: "1px solid #ddd",
};


const paymentListStyle = {
  listStyle: "none",
  padding: 0
};

const paymentItemStyle = {
  marginBottom: "8px"
};

const paymentAmountStyle = {
  fontWeight: "bold"
};

const paymentDateStyle = {
  color: "#888"
};
const paymentFormStyle = {
  backgroundColor: "#f7f7f7",
  padding: "20px",
  border: "1px solid #ddd",
  borderRadius: "5px",
  marginTop: "20px"
};

const formTitleStyle = {
  fontSize: "18px",
  fontWeight: "bold",
  marginBottom: "15px"
};

const formGroupStyle = {
  marginBottom: "15px"
};

const labelStyle = {
  display: "block",
  fontSize: "14px",
  marginBottom: "5px"
};

const inputStyle = {
  width: "100%",
  padding: "8px",
  borderRadius: "3px",
  border: "1px solid #ccc"
};

const submitButtonStyle = {
  backgroundColor: "#ff9800",
  color: "white",
  padding: "10px 20px",
  border: "none",
  borderRadius: "5px",
  cursor: "pointer"
};
const paymentDetails =
{
  backgroundColor: '#d8e3e1',
  marginBottom: 15,
  padding: 5,
  borderRadius: 5
}


