import React, { Component } from "react";
import AnchorTag from "../Anchortag";
import axios from "axios";
import { ScaleLoader } from "react-spinners";


class ExpenseView extends Component {
    constructor(props) {
        super(props);
        this.state = {
            title: "",
            amount: "",
            date: "",
            type: "",
            description: "",
            loading: false,
        };
    }
    async componentDidMount() {
        this.setState({ loading: true });
        const id = this.props.match.params.expenseId;

        try {
            const response = await axios.get(`/expense/get/${id}`)
            console.log(response)
            const date = new Date(response.data.date);
            const options = { year: 'numeric', month: 'short', day: 'numeric' };
            const formattedDate = date.toLocaleDateString('en-US', options);
            this.setState({
                title: response.data.title,
                description: response.data.description,
                date: formattedDate,
                type: response.data.type,
                amount: response.data.amount,
                description: response.data.description,
            });
            this.setState({ loading: false });


        }
        catch (error) {
            console.error("Error fetching product details:");
        };
    }

    render() {
        return (
            <div className="admin-content mx-auto">
                {this.state.loading && (
                    <div className="text-center">
                        <ScaleLoader size={150} color={"#123abc"} loading={this.state.loading} rounded={true} />
                    </div>
                )}
                <div className="w-100 mb-5">
                    <h4>Expense Details</h4>
                    <button className="btn  btn-secondary float-right" style={{ marginBottom: 20 }} onClick={() => { window.print() }}>Print</button>

                </div>
                <div className="w-75">
                    <form>
                        <div className="container-fluid">
                            <div className="row">
                                <div className="col-6">
                                    <div className="form-group">
                                        <label className="mb-2">Title</label>
                                        <input type="text" className="form-control" value={this.state.title} readOnly />
                                    </div>
                                </div>
                                <div className="col-6">
                                    <div className="form-group">
                                        <label className="mb-2">Type</label>
                                        <input type="text" className="form-control" value={this.state.type} readOnly />
                                    </div>
                                </div>
                                <div className="col-12">
                                    <div className="form-group">
                                        <label className="mb-2">Description</label>
                                        <textarea className="form-control" value={this.state.description} rows="10" readOnly />
                                    </div>
                                </div>
                                <div className="col-6">
                                    <div className="form-group">
                                        <label className="mb-2">Amount</label>
                                        <input type="text" className="form-control" value={this.state.amount} readOnly />
                                    </div>
                                </div>
                                <div className="col-6">
                                    <div className="form-group">
                                        <label className="mb-2">Date</label>
                                        <input type="text" className="form-control" value={this.state.date} readOnly />
                                    </div>
                                </div>



                                <div className="col-12 mt-4">
                                    <div className="form-group">
                                        <AnchorTag className="btn btn-warning" itemValue="Back to List" link="/app/office/office/expense/list" />
                                    </div>
                                </div>
                            </div>
                        </div>

                    </form>
                </div>

            </div>
        )
    }
}

export default ExpenseView;


