import React, { Component } from "react";
import axios from "axios";
import { ScaleLoader } from "react-spinners";


class ProductForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      title: "",
      unit: "", 
      lowStockQuantity: "",
      description: "",
      categoryId : "",
      CategoryOptions:[]
    };
  }
  handleInputChange = (event) => {
    const { name, value } = event.target;
    this.setState({
      [name]: value,
    });
  };

  handleItemSubmit = (event) => {
    console.log("dahakdkajsldkjalksdjlakjsdlkajsd")
    event.preventDefault();
    console.log("dahakdkajsldkjalksdjlakjsdlkajsd")

    const {
      title,
      unit,
      lowStockQuantity,
      description,
      categoryId
    } = this.state;
    const formData = {
      title,
      unit,
      lowStockQuantity,
      description,
      categoryId
    };
    console.log(formData);
             
  // Make a POST request to your backend API with Axios
  axios.post("/item/create", formData)
  .then((response) => {
    // Handle a successful response from the server here
    console.log("Form data sent successfully:", response.data);
    this.setState({
       title:"",
      unit:"",
      lowStockQuantity:"",
      description:"", 
      categoryId:""
    });

  })
  .catch((error) => {
    // Handle any errors that occur during the request
    console.error("Error sending form data:", error);
  });
  };
  async componentDidMount() {
    // Make an API request to fetch the item options
    try {
      const response = await axios.get("/itemcategory/get");
      console.log(response.data)
      this.setState({ CategoryOptions: response.data });
    } catch (error) {
      console.error("Error fetching category options:", error);
    }
  }
  render() {
    const unitOptions = ["pcs", "kg", "lb"]; // Array of unit options
    const { CategoryOptions } = this.state;

    return (
      <div className="admin-content mx-auto float-center" style={{backgroundColor:'#abbeb0',borderRadius:10, padding:5,marginBottom:5}}>
 
        <div className="w-75">
          <form >
            <div className="container-fluid">
              <div className="row">
                <div className="col-12">
                  <div className="form-group">
                    <label className="mb-2">Title</label>
                    <input
                      type="text"
                      className="form-control"
                      name="title"
                      value={this.state.title}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>
                <div className="col-12">
                  <div className="form-group">
                    <label className="mb-2">Desciption</label>
                    <textarea
                      type="text"
                      className="form-control"
                      name="description"
                      row="10"
                      value={this.state.description}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>
 
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Unit</label>
                    <select
                      className="form-control"
                      name="unit"
                      value={this.state.unit}
                      onChange={this.handleInputChange}
                    >
                      {unitOptions.map((unit) => (
                        <option key={unit} value={unit}>
                          {unit}
                        </option>
                      ))}
                    </select>
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Catagory</label>
                    <select
                      className="form-control"
                      name="categoryId"
                      required
                      value={this.state.categoryId}
                      onChange={this.handleInputChange}
                    >
                      <option>Select Category</option>
                      {CategoryOptions.map((category) => (
                        <option key={category.CategoryId} value={category.CategoryId}>
                          {category.CategoryId}-{category.name}
                        </option>
                      ))}
                    </select>
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Low Stock Quantity</label>
                    <input
                      type="number"
                      className="form-control"
                      name="lowStockQuantity"
                      value={this.state.lowStockQuantity}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>
                </div>

                <div className="col-12 mt-3">
                  <div className="form-group">

                    <button
                      className="btn btn-success ml-2"
                      onClick={this.handleItemSubmit}
                    >
                      Submit
                      </button>
                  </div>
                </div>
             
            </div>
          </form>
        </div>
      </div>
    );
  }
}

export default ProductForm;



