import React from 'react';
import { useAuth } from './AuthContext'; // Import your authentication context or hook

const withAuth = (WrappedComponent) => {
  return function WithAuth(props) {
    const { login, logout,getUser } = useAuth(); // Get the login and logout functions from the context
console.log("in withAuth",getUser())
const user=getUser()
    // Pass the login and logout functions as props to the wrapped component
    return <WrappedComponent {...props} login={login} logout={logout} user={user} />;
  };
};

export default withAuth;


