import React, { Component } from "react";
import { Route, Switch } from "react-router-dom";
import "../../../style/admin.css";
import HeaderNavBar from "../../../components/navigation/HeaderNavbar";
import AnchorTag from "../../../components/Anchortag";
import OfficePages from "./OfficePages";
import ProfileBasePage from "../profile/ProfileBasePage";
import {getUser} from '../Auth';


class CustomerRootPage extends Component {
    constructor(props) {
        super(props);
        const user = getUser();

        this.userRole = user.role;
    }
    render() {
        return (
            <div>
                <HeaderNavBar></HeaderNavBar>
                <div className="mt-4 ">
                    <div className="">
                        <div className="row">
                            <div className="col-2">
                            {this.userRole!==1 &&<div className="list-group side-navigation">
                                    <AnchorTag link={`${this.props.match.path}/office/dashboard`} liClassName="" className="list-group-item list-group-item-action links" itemValue="Dashboard"></AnchorTag>
                                    <AnchorTag link={`${this.props.match.path}/office/product-category/list`} liClassName="" className="list-group-item list-group-item-action links" itemValue="Inventory"></AnchorTag>
                                    <AnchorTag link={`${this.props.match.path}/office/employee/list`} liClassName="" className="list-group-item list-group-item-action links" itemValue="Employees"></AnchorTag>
                                    <AnchorTag link={`${this.props.match.path}/office/expense/list`} className="list-group-item list-group-item-action links" itemValue="Expense"></AnchorTag>  
                                </div>}
                            </div>
                            <div className="col-9">
                                <Switch>
                                    <Route path={`${this.props.match.path}/office`} component={OfficePages} />
                                    <Route path={`${this.props.match.path}/profile`} component={ProfileBasePage} />
                                </Switch>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default CustomerRootPage;


