import React, { useState } from 'react';
import { ScaleLoader } from "react-spinners";
import axios from 'axios'

const ContactPage = () => {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [phoneNumber, setPhoneNumber] = useState('');
  const [selectedPlan, setSelectedPlan] = useState('');
  const [message, setMessage] = useState('');
  const [purpose, setPurpose] = useState('purchase');
  const [notification, setNotification] = useState(false);
  const [danger, setDanger] = useState(false);
  const [notificationText, setNotificationText] = useState('');
  const [loading, setLoading] = useState(false);

  const handleNameChange = (e) => {
    setName(e.target.value);
  };
  const handleMessageChange = (e) => {
    setMessage(e.target.value);
  };

  const handlePurposeChange = (e) => {
    setPurpose(e.target.value);
  };
  const handleEmailChange = (e) => {
    setEmail(e.target.value);
  };
  const handlePhoneNoChange = (e) => {
    setPhoneNumber(e.target.value);
  };

  const handlePlanChange = (e) => {
    setSelectedPlan(e.target.value);
  };
 

  const handleSubmit = async(e) => {
    e.preventDefault();
    // Send the registration request to the admin or your backend here
    console.log(`Name: ${name}`);
    console.log(`Email: ${email}`);
    console.log(`Phoneno: ${phoneNumber}`);
    console.log(`message: ${message}`);
    console.log(`Selected Plan: ${selectedPlan}`);
    const formData = {
      name:name,
      phoneNo:phoneNumber,
      email:email,
      message:message,
      packageSelected:selectedPlan,
    };
    console.log(formData);
    // Make a POST request to your backend API with Axios
    await axios.post("/messages/create", formData)
      .then((response) => {
        console.log("response", response)
       if (response.status=== 201) {
          console.log("Form data sent successfully:", response.data);
          setEmail('')
          setMessage('')
          setName('')
          setPhoneNumber('')
          setSelectedPlan('')
          setNotification(true)
          setNotificationText(response.data.message)
        }
        setTimeout(() => {
setNotification(false)  
setDanger(false)   
   }, 3000);

        // Handle any other status codes if needed
      })
      .catch((error) => {
        // Handle any errors that occur during the request
        setNotificationText('An error occurred while creating Customer')
        setDanger(true)

        console.error("Error sending form data:", error);
        setTimeout(() => {
setDanger(false)        }, 3000);
      }).finally(() => {
        // Set loading state to false after the request is complete
setLoading(false)      });
  };

  return (
    <div>
         <header >
                <nav className="navbar navbar-expand-lg navbar-dark bg-dark" >
                    <a className="navbar-brand" href="#">Inventory Managment System</a>
                    <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarNav">
                        <ul className="navbar-nav ml-auto">
                            <li className="nav-item ">
                                <a className="nav-link" href="/">Home</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" href="/pricing">Pricing</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" href="/login">Login</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link active" href="/contact">Contact</a>
                            </li>
                        </ul>
                    </div>
                </nav>
            </header>
 
    <div className="container mt-5" >
    {loading && (
          <div className="text-center">
            <ScaleLoader
              size={150}
              color={"#123abc"}
              loading={loading}
              rounded={true} // Set the spinner to be rounded
            />
          </div>
        )}
        {notification && (
          <div className="alert alert-success" role="alert">
            {notificationText}
          </div>
        )}
        {danger && (
          <div className="alert alert-danger" role="alert">
            {notificationText}
          </div>
        )}
      <div className="row">      
        <div className="col-md-6">
          <h2>Contact Us</h2>
          <p>Are you interested in purchasing our product, or do you need more information? Let us know!</p>

          <div className="form-group">
            <label>Purpose</label>
            <select
              className="form-control"
              value={purpose}
              onChange={handlePurposeChange}
            >
              <option value="purchase">I want to make a purchase</option>
              <option value="information">I need more information</option>
            </select>
          </div>

          {purpose === 'purchase' ? (
            <p>Great! Please fill in the form below to make your purchase:</p>
          ) : (
            <p>If you need more information, please contact us using the details provided below:</p>
          )}

          {purpose === 'purchase' && (
            <form onSubmit={handleSubmit}>
              <div className="form-group">
                <label htmlFor="name">Name</label>
                <input
                  type="text"
                  className="form-control"
                  id="name"
                  value={name}
                  onChange={handleNameChange}
                  required
                />
              </div>
              <div className="form-group">
                <label htmlFor="email">Email</label>
                <input
                  type="email"
                  className="form-control"
                  id="email"
                  value={email}
                  onChange={handleEmailChange}
                  required
                />
              </div>
              <div className="form-group">
                <label htmlFor="phone No">Phone No</label>
                <input
                  type="text"
                  className="form-control"
                  id="phoneNumber"
                  value={phoneNumber}
                  onChange={handlePhoneNoChange}
                  required
                />
              </div>
              <div className="form-group">
                <label htmlFor="message">Message</label>
                <textarea
                  className="form-control"
                  id="message"
                  rows="4"
                  value={message}
                  onChange={handleMessageChange}
                  required
                ></textarea>
              </div>
              <div className="form-group">
            <label>Select Plans</label>
            <select
              className="form-control"
              value={selectedPlan}
              onChange={handlePlanChange}
            >
              <option value="Basic office">Basic Office</option>
              <option value="Basic Store">Basic Store</option>
              <option value="Pro">Pro</option>

            </select>
          </div>
              <button type="submit" className="btn btn-primary">
                Submit
              </button>
            </form>
          )}
          {purpose === 'information' && (
        <div className="">
          <h3>Contact Details</h3>
          <p>
            Email: contact@leeddev.io
            <br />
            Phone: +1 905-869-1246
          </p>
        </div>  )}
        </div>
        <div className="col-md-6">
        <img src="/contact.avif" alt="Inventory Image" className="img-fluid" style={{ width: '100%', height: '100%' }}
                        />
            </div>
        </div>

      </div>
    </div>
  );
};

export default ContactPage;



