import React, { Component } from "react";
import AddItemModal from "./AddItemModal"; // Import your modal component
import axios from "axios";
import { ScaleLoader } from "react-spinners";
import { Link } from "react-router-dom";


class ShipmentForm extends Component {
  constructor(props) {
    super(props);

    this.state = {
      title: "",
      date: "",
      vendorOptions: [],
      vendorId: "",
      cargoCharges: 0,
      isModalOpen: false,
      existingItems: [],
      itemOptions: [],
      notification: false,
      notificationText: "",
      danger: false,
      loading: false,
    };
  }
  //  function for reset form.
  resetFormFields = () => {
    this.setState({
      title: "",
      date: "",
      vendorId: "",
      cargoCharges: "",
      existingItems: [],
      // Add other fields and their initial values if needed
    });
  };



  handleInputChange = (event) => {
    const { name, value } = event.target;
    this.setState({
      [name]: value,
    });
  };
  toggleModal = () => {
    this.setState((prevState) => ({
      isModalOpen: !prevState.isModalOpen,
    }));
  };
  handleSubmit = (event) => {
    event.preventDefault();
    const { title, amount, date, vendorId, cargoCharges, existingItems } = this.state;
    const formData = {
      title,
      date,
      vendorId,
      cargoCharges,
      shipmentItems: existingItems
    };
    console.log(formData);
    this.setState({ loading: true });
    // Make a POST request to your backend API with Axios
    axios.post("/shipment/create", formData)
      .then((response) => {
        console.log(response)
        if (response.data.code === 400 || response.data.code === 401 || response.data.code === 500) {
          console.log("response")
          this.setState({
            danger: true,
            notificationText: response.data.message,
          });
          setTimeout(() => {
            this.setState({ notification: false, danger: false });
          }, 12000);
        } else if (response.data.code === 200) {
          console.log("Form data sent successfully:", response.data);
          this.setState({
            notification: true,
            danger: false,
            notificationText: response.data.message,
          });
          this.resetFormFields();
          setTimeout(() => {
            this.setState({ notification: false, danger: false });
          }, 5000);
        }
        // Handle any other status codes if needed
      })
      .catch((error) => {
        // Handle any errors that occur during the request
        this.setState({
          notificationText: "An error occurred while creating Shipment",
          danger: true,
        });
        console.error("Error sending form data:", error);
        setTimeout(() => {
          this.setState({ danger: false });
        }, 3000);
      }).finally(() => {
        // Set loading state to false after the request is complete
        this.setState({ loading: false });
      });
  }
  handleItemInputChange = (event, index) => {
    const { name, value } = event.target;
    const updatedItems = [...this.state.existingItems];
    updatedItems[index][name] = value;
    this.setState({ existingItems: updatedItems });

  };
  handleAddExistingItem = () => {
    const newItem = {
      itemId: 0,
      quantity: 0,
      cost: 0, // Update with the actual item price
      itemOptions: this.state.itemOptions
    };
    console.log(this.state.itemOptions)


    const existingItemIds = this.state.existingItems
      .map((item) => parseInt(item.itemId));
    console.log('fexistingCustomerIds', existingItemIds)

    //    // Filter response.data to exclude customer options that are already in existingItems
    const filteredItemOptions = this.state.itemOptions.filter((item) => !existingItemIds.includes(item.itemId));
    console.log('filteredData', filteredItemOptions)
    newItem.itemOptions = filteredItemOptions;
    this.setState((prevState) => ({
      existingItems: [...prevState.existingItems, newItem],
    }));
  };

  handleDeleteExistingItem = (index) => {
    const updatedItems = [...this.state.existingItems];
    updatedItems.splice(index, 1);
    this.setState({ existingItems: updatedItems });
  };
  async componentDidMount() {
    // Make an API request to fetch the item options
    try {
      const response = await axios.get("/item/get");
      this.setState({ itemOptions: response.data });
    } catch (error) {
      console.error("Error fetching item options:", error);
    }
    try {
      const response = await axios.get("/vendor/get");
      this.setState({ vendorOptions: response.data });
    } catch (error) {
      console.error("Error fetching item options:", error);
    }
  }
  render() {

    const { existingItems, itemOptions, vendorOptions } = this.state;

    return (
      <div className="admin-content mx-auto">
        {this.state.loading && (
          <div className="text-center">
            <ScaleLoader
              size={150}
              color={"#123abc"}
              loading={this.state.loading}
              rounded={true} // Set the spinner to be rounded
            />
          </div>
        )}
        {this.state.notification && (
          <div className="alert alert-success" role="alert">
            {this.state.notificationText}
          </div>
        )}
        {this.state.danger && (
          <div className="alert alert-danger" role="alert">
            {this.state.notificationText}
          </div>
        )}
        <div className="w-100 mb-5">
          <button
            className="btn btn-primary float-right"
            onClick={() => {
              window.history.back();
            }}
          >
            Back to Shipment List
          </button>
          <button
            className="btn btn-info float-right"
            style={{ marginRight: 10 }}
            onClick={this.toggleModal}
          >
            Add New Item
          </button>
          <h4>Add Shipment</h4>
        </div>
        <div className="w-75">
          <form onSubmit={this.handleSubmit}>
            <div className="container-fluid">
              <div className="row">
                <div className="col-12">
                  <div className="form-group">
                    <label className="mb-2">Title</label>
                    <input
                      type="text"
                      className="form-control"
                      name="title"
                      required
                      value={this.state.title}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>

                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Vendor</label>
                    <select
                      className="form-control"
                      name="vendorId"
                      required
                      value={this.state.vendorId}
                      onChange={this.handleInputChange}
                    >
                      <option value="">Select Vendor</option>

                      {vendorOptions.map((option) => (
                        <option key={option.value} value={option.vendorId}>
                          {option.vendorId}-{option.firstName} {option.lastName}
                        </option>
                      ))}
                      {/* Add more options as needed */}
                    </select>
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Date</label>
                    <input
                      type="date"
                      className="form-control"
                      name="date"
                      required
                      value={this.state.date}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Cargo Charges</label>
                    <input
                      type="number"
                      className="form-control"
                      name="cargoCharges"
                      required
                      value={this.state.cargoCharges}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>
                <div>

                </div>


                {/* Render your modal component */}
                {this.state.isModalOpen && <AddItemModal />}


                <div className="col-12">
                  <div className="form-group">
                    {existingItems.map((item, index) => (
                      <div key={index} className="existing-item" style={{ backgroundColor: "#aab4b5", borderRadius: 10, marginBottom: 20, padding: 20 }}>
                        <div className="row">
                          <div className="col-6">
                            <div className="form-group">
                              <label className="mb-2">Select Item</label>
                              <select
                                className="form-control"
                                name="itemId"
                                required
                                value={item.itemId}
                                onChange={(event) => this.handleItemInputChange(event, index)}
                              >
                                <option value="">Select Item</option>
                                {item.itemOptions.map((option) => (
                                  <option key={option.value} value={option.itemId}>
                                    {option.itemId}-{option.title}
                                  </option>
                                ))}
                              </select>
                            </div>
                          </div>

                          <div className="col-4">
                            <div className="form-group">
                              <label className="mb-2">Quantity</label>
                              <input
                                required
                                type="number"
                                className="form-control"
                                name="quantity"
                                value={item.quantity}
                                onChange={(event) => this.handleItemInputChange(event, index)}
                              />
                            </div>
                          </div>
                          <div className="col-4">
                            <div className="form-group">
                              <label className="mb-2">Cost Per Unit</label>
                              <input
                                type="number"
                                className="form-control"
                                name="cost"
                                required
                                value={item.cost}
                                onChange={(event) => this.handleItemInputChange(event, index)}
                              />

                            </div>
                          </div>
                        </div>
                        <button
                          className="btn btn-danger float-right"
                          onClick={() => this.handleDeleteExistingItem(index)}
                        >
                          Delete
                        </button>
                      </div>
                    ))}


                  </div>
                </div>

                <button
                  className="btn btn-info"
                  type="button"
                  onClick={this.handleAddExistingItem} 
                  style={{marginLeft: 15}}
                >
                  + Add Existing Item
                </button>
              </div>

              <div className="col-12 mt-3">
                <div className="form-group">
                  <Link to="/app/store/store/shipment/list" className="btn btn-warning back-link" >
                    Back
                  </Link>
                  <button type="submit" className="btn btn-success ml-3" value="Submit">Submit</button>
                </div>
              </div>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

export default ShipmentForm;



