import React, { Component } from "react";
import axios from "axios";
import { ScaleLoader } from "react-spinners";
import { Link } from "react-router-dom";


class VendorForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      firstName: "",
      lastName: "",
      email: "",
      phoneNumber: "",
      address: "",
      city: "",
      country: "",
      countriesOptions: [],
      citiesOptions: [],
      notification: false,
      danger: false,
      notificationText: "",
      loading: false,

    };
  }
  handleInputChange = (event) => {
    const { name, value } = event.target;
    this.setState({
      [name]: value,
    });
  };

  handleSubmit = (event) => {
    event.preventDefault();
    const { firstName, lastName, email, phoneNumber, address, city, country } =
      this.state;
    const formData = {
      firstName,
      lastName,
      email,
      phoneNumber,
      address,
      country,
      city
    };
    console.log(formData);
    this.setState({ loading: true })
    const id = this.props.match.params.vendorId;

    // Make a POST request to your backend API with Axios
    axios.put(`/vendor/update/${id}`, formData)
      .then((response) => {
        if (response.data.code === 400 || response.data.code === 500) {
          console.log("response")
          this.setState({
            danger: true,
            notificationText: response.data.message,
          });
        } else if (response.data.code === 200) {
          console.log("Form data sent successfully:", response.data);
          this.setState({
            notification: true,
            notificationText: response.data.message,
          });
        }
        setTimeout(() => {
          this.setState({ notification: false, danger: false });
        }, 3000);

        // Handle any other status codes if needed
      })
      .catch((error) => {
        // Handle any errors that occur during the request
        this.setState({
          notificationText: "An error occurred while updating Customer",
          danger: true,
        });
        console.error("Error sending form data:", error);
        setTimeout(() => {
          this.setState({ danger: false });
        }, 3000);
      }).finally(() => {
        // Set loading state to false after the request is complete
        this.setState({ loading: false });
      });
  }
  componentDidMount() {
    // Fetch the list of countries from a public API
    axios.get('https://restcountries.com/v2/all')
      .then((response) => {
        // Extract names from the response data
        const countryNames = response.data.map(country => country.name);
        this.setState({ countriesOptions: countryNames });

        console.log(countryNames);
      })
    const id = this.props.match.params.vendorId;
    (async () => {
      try {
        const response = await axios.get(`/vendor/get/${id}`)
        console.log(response)
        this.setState({
          firstName: response.data.vendor.firstName,
          lastName: response.data.vendor.lastName,
          email: response.data.vendor.email,
          phoneNumber: response.data.vendor.phoneNumber,
          address: response.data.vendor.address,
          country: response.data.vendor.country,
          city: response.data.vendor.city,
        });
      }
      catch (error) {
        console.error("Error fetching product details:");
      };
    })();
  }

  handleCountryChange = (event) => {
    const { name, value } = event.target;
    this.setState({
      [name]: value,
    });
    const selectedCountry = event.target.value;

    // Fetch cities based on the selected country
    axios.get('https://parseapi.back4app.com/classes/Pakistancities_City?limit=500&keys=name,country', {
      headers: {
        'X-Parse-Application-Id': 'kGRqhHPGneDbiyyFBT2tV5JM7YXLocukhBuX43Ac', // This is your app's application id
        'X-Parse-REST-API-Key': 'qBjPLtOfxNzVYZ0fauBUKHEyp1A1coVoZ2Kw6kGh', // This is your app's REST API key
      }
    })
      .then((response) => {
        console.log(response.data.results)
        this.setState({ citiesOptions: response.data.results });
      })
      .catch((error) => {
        console.error("Error fetching cities:", error);
      });
  };
  render() {
    const { countriesOptions, citiesOptions } = this.state;
    return (
      <div className="admin-content mx-auto">
        {this.state.loading && (
          <div className="text-center">
            <ScaleLoader
              size={150}
              color={"#123abc"}
              loading={this.state.loading}
              rounded={true} // Set the spinner to be rounded
            />
          </div>
        )}
        {this.state.notification && (
          <div className="alert alert-success" role="alert">
            {this.state.notificationText}
          </div>
        )}
        {this.state.danger && (
          <div className="alert alert-danger" role="alert">
            {this.state.notificationText}
          </div>
        )}
        <div className="w-100 mb-5">
          <button
            className="btn btn-primary float-right"
            onClick={() => {
              window.history.back();
            }}
          >
            Back to Vendor List
          </button>
          <h4>Update Vendor</h4>
        </div>
        <div className="w-75">
          <form onSubmit={this.handleSubmit}>
            <div className="container-fluid">
              <div className="row">
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">First Name</label>
                    <input
                      type="text"
                      className="form-control"
                      name="firstName"
                      required
                      value={this.state.firstName}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Last Name</label>
                    <input
                      type="text"
                      className="form-control"
                      name="lastName"
                      value={this.state.lastName}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Email</label>
                    <input
                      type="email"
                      className="form-control"
                      name="email"
                      row="10"
                      value={this.state.email}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Phone Number</label>
                    <input
                      type="text"
                      className="form-control"
                      name="phoneNumber"
                      required
                      value={this.state.phoneNumber}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>
                <div className="col-12">
                  <div className="form-group">
                    <label className="mb-2">Address</label>
                    <input
                      type="text"
                      className="form-control"
                      name="address"
                      value={this.state.address}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Country</label>
                    <select
                      className="form-control"
                      name="country"
                      value={this.state.country}
                      onChange={this.handleCountryChange}
                    >
                      <option value="">Select Country</option>
                      {countriesOptions.map((option) => (
                        <option key={option} value={option}>
                          {option}
                        </option>
                      ))}
                    </select>
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">City</label>
                    <input
                      type="text"
                      className="form-control"
                      name="city"
                      value={this.state.city}
                      onChange={this.handleInputChange}
                    />
                    {/* <select
                      className="form-control"
                      name="city"
                      value={this.state.city}
                      onChange={this.handleInputChange}
                    >
                      <option value="">Select City</option>
                      {citiesOptions.map((option) => (
                        <option key={option.objectId} value={option.name}>
                          {option.name}
                        </option>
                      ))}
                    </select> */}
                  </div>
                </div>

                <div className="col-12 mt-3">
                  <div className="form-group">
                  <Link to="/app/store/store/vendor/list" className="btn btn-warning">
                    Back
                  </Link>
                  <button type="submit" className="btn btn-success ml-3" value="Submit">Update</button>
                  </div>
                </div>
              </div>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

export default VendorForm;



