import React, { Component } from "react";
// import "../../../style/admin.css";
import AnchorTag from "../Anchortag";
import axios from "axios";
import { ScaleLoader } from "react-spinners";
import { Link } from "react-router-dom";

class UserView extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name: "",
            email: "",
            phoneNo: "",
            packageSelected: "",
            message: "",
            loading: false, // Add loading state
            notification: false,
            notificationText: "",
        }
    }
    async componentDidMount() {
        // Make an API request to fetch the item options
        const id = this.props.match.params.messageId;
        this.setState({ loading: true })
        try {
            const response = await axios.get(`/messages/get/${id}`);
            console.log(response.data)
           
            this.setState(
                {
                    email: response.data.email,
                    name: response.data.name,
                    phoneNo: response.data.phoneNo,
                    packageSelected: response.data.packageSelected,
                    message: response.data.message
                });
            this.setState({ loading: false })

        } catch (error) {
            console.error("Error fetching item options:", error);
        }
    }

    render() {
        return (
            <div className="admin-content mx-auto">
                {this.state.loading && (
                    <div className="text-center">
                        <ScaleLoader
                            size={150}
                            color={"#123abc"}
                            loading={this.state.loading}
                            rounded={true} // Set the spinner to be rounded
                        />
                    </div>
                )}
                <div className="w-100 mb-5">
                    <AnchorTag link="/app/store/profile/message" className="btn btn-primary float-right" itemValue="Back to Request List"></AnchorTag>
                    <h4>Message Details</h4>
                </div>
                <div className="w-75">
                    <form>
                        <div className="container-fluid">
                            <div className="row">
                                <div className="col-6">
                                    <div className="form-group">
                                        <label className="mb-2">Name</label>
                                        <input type="text" className="form-control" value={this.state.name} readOnly />
                                    </div>
                                </div>
                                <div className="col-6">
                                    <div className="form-group">
                                        <label className="mb-2">Email</label>
                                        <input type="text" className="form-control" value={this.state.email} readOnly />
                                    </div>
                                </div>
                                <div className="col-6">
                                    <div className="form-group">
                                        <label className="mb-2">Phone Number</label>
                                        <input type="text" className="form-control" value={this.state.phoneNo} readOnly />
                                    </div>
                                </div>
                                <div className="col-6">
                                    <div className="form-group">
                                        <label className="mb-2">Package Selected</label>
                                        <input type="text" className="form-control" value={this.state.packageSelected} readOnly />
                                    </div>
                                </div>
                                <div className="col-12">
                                    <div className="form-group">
                                        <label className="mb-2">Message</label>
                                        <textarea type="text" className="form-control" value={this.state.message} readOnly />
                                    </div>
                                </div>
                                <div className="col-12 mt-3">
                                    <div className="form-group">
                                        <AnchorTag className="btn btn-warning" itemValue="Back" link="/app/store/profile/message" />
                                    </div>
                                </div>
                            </div>
                        </div>

                    </form>
                </div>

            </div>
        )
    }
}

export default UserView;


