import './App.css';
import React, {useEffect} from "react";
import { BrowserRouter as Router, Route, Switch, Redirect } from "react-router-dom"
import HomePage from "./pages/HomePage";
import NotFoundPage from "./pages/NotFound";
import AuthPage from "./pages/AuthPage";
import StoreRootPage from "./pages/app/store/RootPage";
import OfficeRootPage from "./pages/app/office/RootPage";
import { AuthProvider } from '../src/components/AuthContext';
import PasswordResetForm from "../src/components/auth/passwordchange";
import LandingPage from './pages/LandingPage';
import PricingPage from './pages/pricing';
import ContactPage from './pages/contact';

import { getUser } from './components/Auth';
import PrivateRoute from './components/PriveateRoutes'
function App() {
  useEffect(() => {
    const user = getUser();
    if (user) {
      // You have access to user data, such as user.email, user.role, etc.
      console.log('Decoded user data:', user);
    } else {
      console.log('No user data found.');
    }
  }, []);

  return (
    <AuthProvider>

      <div className="App">
        <Router>
          <Switch>
            <PrivateRoute path="/app/store" component={StoreRootPage} requiredRole={3}/>
            <PrivateRoute path="/app/office" component={OfficeRootPage} requiredRole={2}/>
            <Route exact path="/" component={LandingPage}></Route>
            <Route exact path="/login" component={AuthPage}></Route>
            <Route exact path="/logout" component={AuthPage}></Route>
            <Route exact path="/pricing" component={PricingPage}></Route>
            <Route exact path="/contact" component={ContactPage}></Route>

            {/* <Route exact path="/registration" component={AuthPage}></Route> */}
            <Route exact path="/password-reset-account-verify" component={AuthPage}></Route>
            <Route exact path="/resetpassword" component={AuthPage}></Route>
            <Route exact path="/passwordupdate" component={PasswordResetForm}></Route>
            <Route component={NotFoundPage} />

          </Switch>
        </Router>
      </div>
      </AuthProvider>

    );
}

export default App;



