import React, { Component } from "react";
import axios from "axios";
import { ScaleLoader } from "react-spinners";
import { Link } from "react-router-dom";

class ProductCategoryForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name: "",
            date: "",
            notification: false,
            danger: false,
            notificationText: "",
            loading: false,
        };
    }
    resetFormFields = () => {
        this.setState({
            name: "",
            date: "",
            // Add other fields and their initial values if needed
        });
    };
    handleInputChange = (event) => {
        const { name, value } = event.target;
        this.setState({
            [name]: value,
        });
    };

    handleSubmit = (event) => {
        event.preventDefault();
        const { name, date } = this.state;
        const formData = {
            name: name,
            date: date,
        };
        this.setState({ loading: true });
        console.log(JSON.stringify(formData));

        // Make a POST request to your backend API with Axios
        axios.post("/officecategory/create", formData)
            .then((response) => {
                if (response.status === 201) {
                    // Handle a successful response from the server here
                    console.log("Form data sent successfully:", response.data);
                    this.setState({
                        notification: true,
                        notificationText: response.data.message

                    })
                    this.resetFormFields()
                }
                setTimeout(() => {
                    this.setState({ notification: false, danger: false });
                }, 3000);
            })
            .catch((error) => {
                // Handle any errors that occur during the request
                this.setState({ notificationText: "An error occured while creating Category", danger: true, })
                setTimeout(() => {
                    this.setState({ danger: false });
                }, 3000);
                console.error("Error sending form data:", error);
            }).finally(() => {
                // Set loading to false after the request is complete
                this.setState({ loading: false });
            });
    };
    render() {
        return (
            <div className="admin-content mx-auto">
                {this.state.loading && (
                    <div className="text-center">
                        <ScaleLoader
                            size={150}
                            color={"#123abc"}
                            loading={this.state.loading}
                        // rounded={true} // Set the spinner to be rounded
                        />
                    </div>
                )}
                {this.state.notification && (
                    <div className="alert alert-success" role="alert">
                        {this.state.notificationText}
                    </div>
                )}
                {this.state.danger && (
                    <div className="alert alert-danger" role="alert">
                        {this.state.notificationText}
                    </div>
                )}
                <div className="w-100 mb-5">
                    <button className="btn btn-primary float-right" onClick={() => { window.history.back(); }}>Back to Items List</button>
                    <h4>Create Item Category</h4>
                </div>
                <div className="w-75">
                    <form onSubmit={this.handleSubmit}>
                        <div className="container-fluid">
                            <div className="row">
                                <div className="col-6">
                                    <div className="form-group">
                                        <label className="mb-2">Category Name</label>
                                        <input type="text" className="form-control" name="name" required value={this.state.name} onChange={this.handleInputChange} />
                                    </div>
                                </div>
                                <div className="col-6">
                                    <div className="form-group">
                                        <label className="mb-2">Date</label>
                                        <input type="date" className="form-control" name="date" required value={this.state.date} onChange={this.handleInputChange} />
                                    </div>
                                </div>

                                <div className="col-12 mt-3">
                                    <div className="form-group">
                                        <Link to="/app/office/office/product-category/list" className="btn btn-warning">
                                            Back
                                        </Link>
                                        <button type="submit" className="btn btn-success ml-3" value="Submit">Submit</button>
                                    </div>
                                </div>
                            </div>
                        </div>

                    </form>
                </div>

            </div>
        )
    }
}

export default ProductCategoryForm;


