import React, {Component} from "react";
import AnchorTag from "../Anchortag";
import Table from "../table/Table";
import InputFormGroup from "../input/InputFormGroup";


class ExpenseCatagoryList extends Component{
    constructor(props){
        super(props);
        this.columnList = ["ID", "Catagory", "Total Entries", "Amount", "Action"];
        this.tableData = [
            {"id": 1, "Catagory": "Bills", "Total Entries": "201256", "Amount": "238.00"},
            {"id": 2, "Catagory": "Salary", "Total Entries": "201256", "Amount": "980.00"},
            {"id": 3, "Catagory": "Bills", "Total Entries": "201256", "Amount": "305.00"},
            {"id": 4, "Catagory": "Bills", "Total Entries": "201256", "Amount": "139.00"},
        ]
    }


    render(){
        return (
            <div className="admin-content mx-auto">
                <div className="w-100 mb-5">
                    <AnchorTag link="/app/office/office/expense-category/create" className="btn btn-sm btn-warning float-right" itemValue="Add Expense Catagory"></AnchorTag>
                    <h4>Expense Category List</h4>
                </div>
                <Table className="table table-striped" columnList={this.columnList} tableData={this.tableData} actionLinkPrefix=""></Table>
            </div>
        ) 
    }
}

export default ExpenseCatagoryList;


