import React, {useEffect} from "react";

import { Route, Redirect } from 'react-router-dom';
import { getUser } from './Auth';
// Define a private route that requires authentication
const PrivateRoute = ({ component: Component, requiredRole,more, ...rest }) => (
  <Route
    {...rest}
    render={(props) => {
      const user = getUser();

      if (user && user.role === requiredRole || user && user.role===1 || user && user.role===5 || user && user.role===more) {
        return <Component {...props} />;
      } else {
        // You can redirect to a different route or display an unauthorized message
        return <Redirect to="/login" />;
      }
    }}
  />
);

export default PrivateRoute;



