import React, { Component } from "react";
import InputPasswordGroup from "../../components/input/InputPasswordGroup";
import InputFormGroup from "../../components/input/InputFormGroup";
import SelectFormGroup from "../../components/input/SelectFormGroup";
import axios from "axios";
import { ScaleLoader } from "react-spinners";
import { getUser } from '../Auth'

class PasswordResetForm extends Component {

  constructor(props) {
    super(props);
    this.state = {
      fullName: "",
      email: "",
      roleId: "",
      password: "",
      rolesOptions: [],
      notification: false,
      notificationText: "",
      danger: false,
      loading: false,

    };
  }
  resetFormFields = () => {
    this.setState({
      fullName: "",
      email: "",
      roleId: "",
      password: "",
      rolesOptions: [],
      // Add other fields and their initial values if needed
    });
    axios.get('/role/get')
      .then((response) => {
        const filteredData = response.data.filter((item) => item.roleId != 1);
        this.setState({ rolesOptions: filteredData });
      })
  };

  handleInputChange = (event) => {
    const { name, value } = event.target;
    this.setState({
      [name]: value,
    });
  };

  handleSubmit = (event) => {
    event.preventDefault();
    let id;
    const user = getUser()
    // this.setState{roleId:user.role}
    if (this.props.match.params.businessId) {
      id = this.props.match.params.businessId;
    } else {
      id = user.businessId
    }
    const {
      fullName,
      email,
      roleId
    } = this.state;
    if (!this.state.fullName || !this.state.email) {
      alert('Please fill in all required fields.');
      return;
    }
    // Function to generate a random password
    function generateRandomPassword(length) {
      const charset = "abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789";
      let password = "";
      for (let i = 0; i < length; i++) {
        const randomIndex = Math.floor(Math.random() * charset.length);
        password += charset.charAt(randomIndex);
      }
      return password;
    }

    const password = generateRandomPassword(8);
    const businessId = id
    console.log(password)
    let formData = {}
    if (user.role == 1) {
      formData = {
        fullName,
        email,
        roleId,
        password,
        businessId,
        isManager: true
      };
    }
    else if (user.role == 2 || user.role == 3) {
      formData = {
        fullName,
        email,
        roleId: user.role,
        password,
        businessId: user.businessId,
        isManager: false
      };
    }
    else {
      formData = {
        fullName,
        email,
        roleId,
        password,
        businessId: user.businessId,
        isManager: false
      };
    }
    console.log(formData);
    this.setState({ loading: true })
    // Make a POST request to your backend API with Axios
    axios.post("/user/create", formData)
      .then((response) => {
        // Handle a successful response from the server here
        console.log("Form data sent successfully:", response.data);
        console.log(response)
        if (response.data.code === 400 || response.data.code === 500) {
          this.setState({
            danger: true,
            notificationText: response.data.message,
          });
          setTimeout(() => {
            this.setState({ notification: false, danger: false });
          }, 5000);
        } else if (response.data.code === 200) {
          console.log("Form data sent successfully:", response.data);
          this.setState({
            notification: true,
            danger: false,
            notificationText: response.data.message,
          });
          this.resetFormFields();
          setTimeout(() => {
            this.setState({ notification: false, danger: false });
          }, 5000);
        }
        // Handle any other status codes if needed
      })
      .catch((error) => {
        // Handle any errors that occur during the request
        this.setState({
          notificationText: "An error occurred while creating User",
          danger: true,
        });
        console.error("Error sending form data:", error);
        setTimeout(() => {
          this.setState({ danger: false });
        }, 3000);
      }).finally(() => {
        // Set loading state to false after the request is complete
        this.setState({ loading: false });
      });
  }
  async componentDidMount() {
    // Make an API request to fetch the item options
    try {
      const response = await axios.get("/role/get");
      console.log(response.data)
      const filteredData = response.data.filter((item) => item.roleId != 1);
      this.setState({ rolesOptions: filteredData });
    } catch (error) {
      console.error("Error fetching item options:", error);
    }
  }
  render() {
    const user = getUser()

    return (
      <div className="admin-content mx-auto">
        {this.state.loading && (
          <div className="text-center">
            <ScaleLoader
              size={150}
              color={"#123abc"}
              loading={this.state.loading}
              rounded={true} // Set the spinner to be rounded
            />
          </div>
        )}
        {this.state.notification && (
          <div className="alert alert-success" role="alert">
            {this.state.notificationText}
          </div>
        )}
        {this.state.danger && (
          <div className="alert alert-danger" role="alert">
            {this.state.notificationText}
          </div>
        )}
        <div className="w-100 mb-5">
          <button
            className="btn btn-primary float-right"
            onClick={() => {
              window.history.back();
            }}
          >
            Back to User List
          </button>

          <h4>Create User</h4>
        </div>
        <div className="w-75">
          <form onSubmit={this.handleSubmit}>
            <div className="container-fluid">
              <div className="row">
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Full Name<span style={{ color: "red" }}>*</span></label>
                    <input
                      type="text"
                      className="form-control"
                      name="fullName"
                      required
                      value={this.state.fullName}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Email Address<span style={{ color: "red" }}>*</span></label>
                    <input
                      type="email"
                      className="form-control"
                      name="email"
                      required
                      row="10"
                      value={this.state.email}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>
                {user.role == 1 &&
                  <div className="col-6">
                    <div className="form-group">
                      <label className="mb-2">Plan Purchased<span style={{ color: "red" }}>*</span></label>
                      <select
                        className="form-control"
                        name="roleId"
                        required
                        value={this.state.roleId}
                        onChange={this.handleInputChange}
                      >
                        <option>Select Plan</option>
                        {this.state.rolesOptions.map((option) => (
                          <option key={option.roleId} value={option.roleId}>
                            {option.name}
                          </option>
                        ))}
                      </select>
                    </div>
                  </div>
                }
                {user.role == 5 && user.isManager &&
                  <div className="col-6">
                    <div className="form-group">
                      <label className="mb-2">Permissions<span style={{ color: "red" }}>*</span></label>
                      <select
                        className="form-control"
                        name="roleId"
                        required
                        value={this.state.roleId}
                        onChange={this.handleInputChange}
                      >
                        <option>Select Plan</option>
                        {this.state.rolesOptions.map((option) => (
                          <option key={option.roleId} value={option.roleId}>
                            {option.name}
                          </option>
                        ))}
                      </select>
                    </div>
                  </div>
                }
              </div>


              <div className="col-12 mt-3">
                <div className="form-group">
                  <button
                    className="btn btn-warning back-link"
                    onClick={() => {
                      window.history.back();
                    }}
                  >
                    Back
                  </button>
                  <button type="submit" className="btn btn-success ml-3" value="Submit">Submit</button>
                </div>
              </div>

            </div>
          </form>
        </div>
      </div>
    );
  }
}

export default PasswordResetForm;


