import React, {Component} from "react";
import { Route, Switch } from "react-router-dom";
import Profile from "../../../components/profile/Profile";
import PasswordResetForm from "../../../components/profile/PasswordReset";
import createUser from "../../../components/profile/createUser";
import UserList from "../../../components/profile/UserList";
import UserView from "../../../components/profile/UserView";
import EditUser from "../../../components/profile/EditUser";
import BusinessList from "../../../components/business/BussinessList";
import BusinessView from "../../../components/business/BusinessView"
import BusinessUserList from "../../../components/business/bussinesUserList"
import CreateBusiness from "../../../components/business/createBusiness"
import EditBusiness from "../../../components/business/EditBusiness"
import MessagesPage from "../../../components/profile/messagePage";
import MessageView from "../../../components/profile/messageView";
import PrivateRoute from "../../../components/PriveateRoutes";
class ProfileBasePage extends Component{
    constructor(props){
        super(props);
    }


    render(){
        return (
            <div className="w-100">
            <Switch>
                <Route exact path={`${this.props.match.path}/me`} component={Profile}/>
                
                <Route exact path={`${this.props.match.path}/password-reset`} component={PasswordResetForm}/>
                
                <PrivateRoute exact path={`${this.props.match.path}/create-user/:businessId`} component={createUser} requiredRole={2} more={3}/>
                <PrivateRoute exact path={`${this.props.match.path}/business-list/view/:businessId`} component={BusinessUserList} requiredRole={1}/>
                <PrivateRoute exact path={`${this.props.match.path}/business-list/view/user/view/:userId`} component={UserView} requiredRole={1}/>
                <PrivateRoute exact path={`${this.props.match.path}/business-list/view/user/edit/:userId`} component={EditUser} requiredRole={1}/>
                <PrivateRoute exact path={`${this.props.match.path}/user`} component={BusinessUserList} requiredRole={2} more={3}/>
                <PrivateRoute exact path={`${this.props.match.path}/user/view/:userId`} component={UserView} requiredRole={2} more={3}/>
                <PrivateRoute exact path={`${this.props.match.path}/user/edit/:userId`} component={EditUser} requiredRole={2} more={3}/>
                <PrivateRoute exact path={`${this.props.match.path}/message`} component={MessagesPage} requiredRole={1}/>
                <PrivateRoute exact path={`${this.props.match.path}/view/:messageId`} component={MessageView} requiredRole={1}/>

                <PrivateRoute exact path={`${this.props.match.path}/create-business`} component={CreateBusiness} requiredRole={1}/>
                <PrivateRoute exact path={`${this.props.match.path}/business-list`} component={BusinessList} requiredRole={1}/>
                {/* <PrivateRoute exact path={`${this.props.match.path}/business/view/:businessId`} component={BusinessView} requiredRole={1}/> */}
                <PrivateRoute exact path={`${this.props.match.path}/business-list/edit/:businessId`} component={EditBusiness} requiredRole={2}/>
            </Switch>
            
        </div>
        ) 
    }
}

export default ProfileBasePage;


