import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';

const LandingPage = () => {
    return (
        <div>
            <header >
                <nav className="navbar navbar-expand-lg navbar-dark bg-dark" >
                    <a className="navbar-brand" href="#">Inventory Managment System</a>
                    <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarNav">
                        <ul className="navbar-nav ml-auto">
                            <li className="nav-item active">
                                <a className="nav-link" href="/">Home <span className="sr-only">(current)</span></a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" href="/pricing">Pricing</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" href="/login">Login</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" href="/contact">Contact</a>
                            </li>
                        </ul>
                    </div>
                </nav>
            </header>

            <section className="" style={{ marginTop: 40, marginLeft: 50, marginRight: 50 }}>
                <div className="row">
                    <div className="col-md-6">
                        <div className="row">
                            <div className="col-md-12">

                                <h2>Unlock the Power of Efficient Inventory Management</h2>
                                <p style={{ fontFamily: "sans-serif", fontSize: 17 }}>Take Control of Your Inventory with Our Comprehensive Inventory System</p>
                                <p>We provide a complete solution for managing your inventory efficiently.</p>

                                <a href="/login" className="btn btn-secondary">Get Started</a>
                            </div>
                        </div>
                        <div className="row">

                            <div className="col-md-12">
                                <section className="mt-3">
                                    <div className="row">
                                        <div className="col-md-6">
                                            <h3>Benefits of Our Inventory System</h3>
                                            <ul>
                                                <li>Easily track stock levels, orders, and sales.</li>
                                                <li>Reduce manual data entry and errors.</li>
                                                <li>Optimize stock levels for maximum profitability.</li>
                                            </ul>
                                        </div>
                                        <div className="col-md-6">
                                            <h3>Real-time Data Access</h3>
                                            <ul>
                                                <li>Access your inventory data from anywhere, anytime.</li>
                                                <li>Monitor stock levels and sales on the go.</li>
                                                <li>Collaborate with your team in real-time.</li>
                                            </ul>
                                        </div>
                                    </div>
                                    <div className="row mt-4">
                                        <div className="col-md-6">
                                            <h3>Advanced Reporting</h3>
                                            <ul>
                                                <li>Generate detailed reports on inventory performance.</li>
                                                <li>Make data-driven decisions for your business.</li>
                                            </ul>
                                        </div>
                                        <div className="col-md-6">
                                            <h3>Inventory Alerts</h3>
                                            <ul>
                                                <li>Avoid costly stockouts and excess inventory.</li>
                                                <li>Stay in control with automated alerts.</li>
                                            </ul>
                                        </div>
                                    </div>
                                </section>
                            </div>
                        </div>
                    </div>

                    <div className="col-md-6">
                        <img src="/inventory-image.png" alt="Inventory Image" className="img-fluid" style={{ width: '100%', height: '100%' }}
                        />
                    </div>
                </div>
            </section>
        </div>
    );
};

export default LandingPage;



