import React, { Component } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import { ScaleLoader } from "react-spinners";

class ShipmentView extends Component {
  constructor(props) {
    super(props);
    this.productDetails =
      "PERFECT GIFT IDEA: Works on wet, tion and is not a product defect.";
    this.state = {
      title: "",
      amount: 0,
      cargoCharges: 0,
      date: "",
      status: "",
      deliveryId: null,
      items: [],
      loading: false,
      notification: false,
      notificationText: "",
      danger: false,
    };

  }
  componentDidMount() {
    const id = this.props.match.params.deliveryReturnId;
    (async () => {
      this.setState({ loading: true })
      try {
        const response = await axios.get(`/deliveryreturn/get/${id}`)
        console.log(response.data)
        this.setState({
          title: response.data.deliveryReturns.title,
          amount: response.data.deliveryReturns.totalItemsAmount,
          date: response.data.deliveryReturns.date,
          deliveryId: response.data.deliveryReturns.deliveryId,
          items: response.data.deliveryReturnitemss
        });
        this.setState({ loading: false })


      }
      catch (error) {
        console.error("Error fetching product details:");
      };
    })();
  }

  render() {
    return (
      <div className="admin-content mx-auto">
        {this.state.loading && (
          <div className="text-center">
            <ScaleLoader
              size={150}
              color={"#123abc"}
              loading={this.state.loading}
            // rounded={true} // Set the spinner to be rounded
            />
          </div>
        )}
        <div className="w-100 mb-5">
          <h4>Delivery Return Details</h4>
          <button
            className="btn  btn-secondary float-right"
            style={{ marginBottom: 20 }}
            onClick={() => {
              window.print();
            }}
          >
            Print
          </button>
        </div>
        <div className="w-75">
          <form onSubmit={this.handleSubmit}>
            <div className="container-fluid">
              <div className="row">
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Title</label>
                    <input
                      type="text"
                      className="form-control"
                      name="title"
                      value={this.state.title}
                      readOnly
                    />
                  </div>
                </div>

                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Amount</label>
                    <input
                      type="text"
                      className="form-control"
                      name="amount"
                      row="10"
                      value={this.state.amount}
                      readOnly
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">Date</label>
                    <input
                      type="text"
                      className="form-control"
                      name="date"
                      value={this.state.date}
                      readOnly
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="form-group">
                    <label className="mb-2">DeliveryId</label>
                    <input
                      type="text"
                      className="form-control"
                      name="deliveryId"
                      value={this.state.deliveryId}
                      readOnly
                    />
                  </div>
                </div>
              </div>


              <div className="items-list">
                {Array.from(new Set(this.state.items && this.state.items.map(item => item.customerId))).map(customerId => (

                  <div key={customerId} style={{ marginBottom: 20 }}>
                    <h5 style={{ marginLeft: 80 }}>
                      Items of DeliveryReturn for {this.state.items.find(item => item.customerId === customerId).customerName}
                    </h5>
                    <table
                      style={{
                        width: "100%",
                        borderCollapse: "collapse",
                        border: "1px solid #ddd",
                        marginLeft: 0,
                        backgroundColor: "#f5f5f5",
                      }}
                    >
                      <thead>
                        <tr>
                          <th style={tableHeaderStyle}>ItemId</th>
                          <th style={tableHeaderStyle}>Quantity</th>

                        </tr>
                      </thead>
                      <tbody>
                        {this.state.items
                          .filter(item => item.customerId === customerId)
                          .map(item => (
                            <tr
                              key={item.id}
                              style={tableRowStyle}
                              onClick={() => {
                                // Navigate to "View Item" page
                                this.props.history.push(
                                  `/app/store/store/product-category/view/view/${item.itemId}`
                                );
                              }}
                            >
                              <td style={tableCellStyle}>{item.itemId}</td>
                              <td style={tableCellStyle}>{item.quantity}</td>
                              {/* <td style={tableCellStyle}>{item.pricePerUnit}</td>
                <td style={tableCellStyle}>{item.pricePerUnit*item.quantity}</td> */}

                            </tr>
                          ))}
                        <tr>

                          {/* <td style={{ fontWeight: "bold" ,}}>
              {this.state.items
                .filter(item => item.customerId === customerId)
                .reduce((total, item) => parseInt(total) + parseInt(item.pricePerUnit*item.quantity), 0)}
            </td> */}
                        </tr>
                      </tbody>
                    </table>
                  </div>
                ))}
              </div>


              <div className="col-12 mt-3">
                <div className="form-group">
                  <Link to="/app/store/store/deliveryreturn/list" className="btn btn-warning back-link">
                    Back
                  </Link>
                </div>
              </div>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

export default ShipmentView;
// Inline CSS styles
const tableHeaderStyle = {
  backgroundColor: "#c2c5c0",
  padding: "10px",
  border: "1px solid #ddd",
  fontWeight: "bold",
};

const tableRowStyle = {
  cursor: "pointer",
  transition: "background-color 0.3s",
};

const tableCellStyle = {
  padding: "10px",
  border: "1px solid #ddd",
};



