import React, { Component } from "react";
import AnchorTag from "../Anchortag";

class TdTag extends Component {
    constructor(props) {
        super(props);
        // console.log(this.props.allowEdit)
        // console.log(this.props.handledelete)
        // this.props.delete()
    }


    render() {
        if (this.props.isLinked == "false") {
            return <td scope="col">{this.props.value}</td>
        }
        else {
            return (
                <td scope="col">

                    <AnchorTag link={`${this.props.linkPrefix}view/${this.props.value}`} className="ml-2 btn btn-sm btn-info" itemValue="View" />
                                     {
                        this.props.allowEdit && (
                            <a
                            href={`${this.props.linkPrefix}edit/${this.props.value}`}
                            className="ml-2 btn btn-sm btn-secondary"
                            itemValue="Edit"
                        >
                            Edit
                        </a>
                        )
                    }
                    {this.props.allowEdit && (

                          <button
                          onClick={() => this.props.handledelete(this.props.value)}
                          className="ml-2 btn btn-sm btn-danger"
                          itemValue="Delete"
                      >
                          Delete
                      </button>

                    )}

                </td>
            )
        }
    }
}

export default TdTag;


